<script>
import { emitter, popup } from '@hui/utils'
import Utils from './aria-utils'

export default {
  name: 'ElDrawer',
  mixins: [popup, emitter],
  props: {
    appendToBody: {
      type: Boolean,
      default: false
    },
    beforeClose: {
      type: Function,
      default: null
    },
    customClass: {
      type: String,
      default: ''
    },
    closeOnPressEscape: {
      type: Boolean,
      default: true
    },
    destroyOnClose: {
      type: Boolean,
      default: false
    },
    modal: {
      type: Boolean,
      default: true
    },
    direction: {
      type: String,
      default: 'rtl',
      validator(val) {
        return ['ltr', 'rtl', 'ttb', 'btt'].includes(val)
      }
    },
    modalAppendToBody: {
      type: Boolean,
      default: true
    },
    showClose: {
      type: Boolean,
      default: true
    },
    size: {
      type: String,
      default: '30%'
    },
    title: {
      type: String,
      default: ''
    },
    visible: {
      type: Boolean
    },
    wrapperClosable: {
      type: Boolean,
      default: true
    },
    withHeader: {
      type: Boolean,
      default: true
    },
    isDrag: {
      type: Boolean,
      default: false,
    },
    dragSize: {
      type: Array,
      default: () => [],
      validator(val) {
        return (!val.length || (
          val.length === 2
          && typeof val[0] === 'number'
          && typeof val[1] === 'number'
          && val[0] < val[1]
        )
        )
      },
    },
    isHandle: {
      type: Boolean,
      default: () => false
    },
    headerStyle: {
      type: Object,
      default: () => {}
    },
    bodyStyle: {
      type: Object,
      default: () => {}
    },
    footerStyle: {
      type: Object,
      default: () => {}
    },
  },
  data() {
    return {
      closed: false,
      prevActiveElement: null,
      rendered: false,
      emitParams: {}, // drag
    }
  },
  computed: {
    isHorizontal() {
      return this.direction === 'rtl' || this.direction === 'ltr'
    },
    dragDirection() {
      return this.direction[this.direction.length - 1]
    },
  },
  watch: {
    appendToBody: {
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            document.body.appendChild(this.$el)
          })
        }
      },
      immediate: true
    },
    visible(val) {
      if (val) {
        this.closed = false
        this.$emit('open')
        this.rendered = true

        this.prevActiveElement = document.activeElement
        this.$nextTick(() => {
          Utils.focusFirstDescendant(this.$refs.drawer)
          if (this.isDrag)
            this.drag()
        })
      }
      else {
        if (!this.closed)
          this.$emit('close')

        // popuppositionabsolutepositionstatic
        if (this.isHandle) {
          if (this.$refs.drawerContainer.style.position === 'absolute')
            this.$refs.drawerContainer.style.position = 'static'
        }
        this.$nextTick(() => {
          if (this.prevActiveElement)
            this.prevActiveElement.focus()
        })
      }
    },
  },
  mounted() {
    if (this.visible) {
      this.rendered = true
      this.open()
      if (this.isDrag) {
        this.$nextTick(() => {
          this.drag()
        })
      }
    }
  },
  destroyed() {
    // if appendToBody is true, remove DOM node after destroy
    if (this.appendToBody && this.$el && this.$el.parentNode)
      this.$el.parentNode.removeChild(this.$el)
  },
  methods: {
    afterEnter() {
      if (this.isHorizontal)
        this.emitParams.width = this.$refs.drawer.clientWidth
      else
        this.emitParams.height = this.$refs.drawer.clientHeight

      this.$emit('opened', this.emitParams)
    },
    afterLeave() {
      this.$emit('closed')
    },
    hide(cancel) {
      if (cancel !== false) {
        this.$emit('update:visible', false)
        this.$emit('close')
        if (this.destroyOnClose)
          this.rendered = false

        this.closed = true
      }
    },
    handleWrapperClick() {
      if (this.wrapperClosable)
        this.closeDrawer()
    },
    closeDrawer() {
      if (typeof this.beforeClose === 'function')
        this.beforeClose(this.hide)
      else this.hide()
    },
    handleClose() {
      // This method here will be called by PopupManger, when the `closeOnPressEscape` was set to true
      // pressing `ESC` will call this method, and also close the drawer.
      // This method also calls `beforeClose` if there was one.
      this.closeDrawer()
    },
    drag() {
      this.$refs.drag.onmousedown = () => {
        const wrapHeight = this.$refs.drawerWrap.clientHeight
        const wrapWidth = this.$refs.drawerWrap.clientWidth
        document.onmousemove = (e) => {
          this.$refs.drawerWrap.style.pointerEvents = 'none'
          // emitParams
          this.emitParams = {}

          switch (this.dragDirection) {
            case 'r': {
              const width = e.clientX
              this.calculateSize('width', width)
              break
            }
            case 'l': {
              const width = wrapWidth - e.clientX
              this.calculateSize('width', width)
              break
            }
            case 't': {
              const height = wrapHeight - e.clientY
              this.calculateSize('height', height)
              break
            }
            case 'b': {
              const height = e.clientY
              this.calculateSize('height', height)
              break
            }
            default: {
              const width = e.clientX
              this.calculateSize('width', width)
            }
          }

          this.$emit('drag', this.emitParams)

          document.onmouseup = (e) => {
            this.$refs.drawerWrap.style.pointerEvents = null
            document.onmousemove = null
            document.onmouseup = null
          }
        }
      }
    },
    calculateSize(name, size) {
      let tempSize = size
      if (this.dragSize.length === 2) {
        if (tempSize > this.dragSize[1])
          tempSize = this.dragSize[1]

        if (tempSize < this.dragSize[0])
          tempSize = this.dragSize[0]
      }
      // drag
      this.emitParams[name] = tempSize
      this.$refs.drawer.style[name] = `${tempSize}px`
    },
  },
}
</script>

<template>
  <div ref="drawerContainer">
    <template v-if="isHandle && !visible">
      <slot name="handle" />
    </template>
    <transition
      name="el-drawer-fade"
      @after-enter="afterEnter"
      @after-leave="afterLeave"
    >
      <div v-show="visible" class="el-drawer__wrapper" tabindex="-1">
        <template v-if="isHandle && visible">
          <slot name="handle" />
        </template>
        <div
          ref="drawerWrap"
          class="el-drawer__container"
          :class="visible && 'el-drawer__open'"
          role="document"
          tabindex="-1"
          @click.self="handleWrapperClick"
        >
          <div
            ref="drawer"
            aria-modal="true"
            aria-labelledby="el-drawer__title"
            :aria-label="title"
            class="el-drawer"
            :class="[direction, customClass]"
            :style="isHorizontal ? `width: ${size}` : `height: ${size}`"
            role="dialog"
            tabindex="-1"
          >
            <header
              v-if="withHeader"
              id="el-drawer__title"
              class="el-drawer__header"
              :style="headerStyle"
            >
              <slot v-if="!$slots.title" name="header">
                <span
                  v-if="!$slots.title"
                  role="heading"
                  tabindex="0"
                  :title="title"
                >
                  {{ title }}
                </span>
              </slot>
              <slot v-else name="title">
              <!-- DEPRECATED SLOT -->
              <!-- <span role="heading" tabindex="0" :title="title">
                {{ title }}
              </span> -->
              </slot>
              <button
                v-if="showClose"
                :aria-label="`close ${title || 'drawer'}`"
                class="el-drawer__close-btn"
                type="button"
                @click="closeDrawer"
              >
                <i class="el-dialog__close el-icon h-icon-close" />
              </button>
            </header>
            <section v-if="rendered" class="el-drawer__body" :style="bodyStyle">
              <slot />
            </section>
            <div v-if="$slots.footer" class="el-drawer__footer" :style="footerStyle">
              <slot name="footer" />
            </div>
            <div
              v-if="isDrag"
              ref="drag"
              class="el-drawer__drag"
              :class="[`el-drawer__drag--${dragDirection}`]"
            />
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
