const getPropertyFromData = function(node, prop) {
  const props = node.store.props;
  const data = node.data || {};
  const config = props[prop];

  if (typeof config === 'function') {
    return config(data, node);
  } else if (typeof config === 'string') {
    return data[config];
  } else if (typeof config === 'undefined') {
    return '';
  }
};

let nodeIdSeed = 0;

export default class Node {
  constructor(options) {
    this.id = nodeIdSeed++;
    this.text = null;
    this.checked = false;
    this.indeterminate = false;
    this.data = null;
    this.expanded = false;
    this.parent = null;
    this.visible = true;
    this.cacheExpanded = null;
    this.filtering = false;

    for (const name in options) {
      if (hasOwn(options, name)) {
        this[name] = options[name];
      }
    }

    // internal
    this.level = 0;
    this.loaded = false;
    this.childNodes = [];
    this.loading = false;

    if (this.parent) {
      this.level = this.parent.level + 1;
    }

    const store = this.store;
    if (!store) {
      throw new Error('[Node]store is required!');
    }
    store.registerNode(this);

    const props = store.props;
    if (props && typeof props.isLeaf !== 'undefined') {
      const isLeaf = getPropertyFromData(this, 'isLeaf');
      if (typeof isLeaf === 'boolean') {
        this.isLeafByUser = isLeaf;
      }
    }

    if (
      (store.lazy !== true && this.data) ||
      (this.data && options.loadedBeforeMove && options.loadedChildren)
    ) {
      this.setData(this.data, options.loadedChildren);

      if (store.defaultExpandAll) {
        this.expanded = true;
      }
    } else if (this.level > 0 && store.lazy && store.defaultExpandAll) {
      this.expand();
    }

    if (!this.data) return;
    const defaultExpandedKeys = store.defaultExpandedKeys;
    const key = store.key;
    if (
      key &&
      defaultExpandedKeys &&
      defaultExpandedKeys.indexOf(this.key) !== -1
    ) {
      this.expand(null, store.autoExpandParent);
    }

    if (
      key &&
      (store.currentNodeKey || store.currentNodeKey === 0) &&
      this.key === store.currentNodeKey
    ) {
      store.currentNode = this;
    }

    if (store.lazy) {
      store._initDefaultCheckedNode(this);
    }

    this.updateLeafState();
  }

  setData(data, loadedChildren) {
    if (!Array.isArray(data)) {
      markNodeData(this, data);
    }

    this.data = data;
    this.childNodes = [];

    let children;
    if (loadedChildren) {
      children = loadedChildren;
    } else if (this.level === 0 && this.data instanceof Array) {
      children = this.data;
    } else {
      children = getPropertyFromData(this, 'children') || [];
    }

    for (let i = 0, j = children.length; i < j; i++) {
      this.insertChild({ data: children[i] });
    }
  }

  get svgIcon() {
    return getPropertyFromData(this, 'svgIcon');
  }

  get svgIconOffline() {
    return getPropertyFromData(this, 'svgIconOffline');
  }

  get svgIconColor() {
    return getPropertyFromData(this, 'svgIconColor');
  }

  get svgIconColorActive() {
    return getPropertyFromData(this, 'svgIconColorActive');
  }

  get svgIconRequire() {
    return getPropertyFromData(this, 'svgIconRequire');
  }

  get label() {
    return getPropertyFromData(this, 'label');
  }

  get key() {
    const nodeKey = this.store.key;
    if (this.data) return this.data[nodeKey];
    return null;
  }

  get disabled() {
    return getPropertyFromData(this, 'disabled');
  }

  get icon() {
    return getPropertyFromData(this, 'icon');
  }

  get selectable() {
    const selectable = getPropertyFromData(this, 'selectable');
    return typeof selectable === 'boolean' ? selectable : true;
  }

  insertChild(child, index, isSetLeafFalse, loadedBeforeMove, loadedChildren) {
    if (!child) throw new Error('insertChild error: child is required.');

    if (!(child instanceof Node)) {
      merge(child, {
        parent: this,
        store: this.store,
        loadedBeforeMove,
        loadedChildren
      });
      child = new Node(child);
    }

    child.level = this.level + 1;

    if (typeof index === 'undefined' || index < 0) {
      this.childNodes.push(child);
    } else {
      this.childNodes.splice(index, 0, child);
    }

    this.updateLeafState(isSetLeafFalse);
    // isSetLeafFalse && (this.loaded = true);
  }

  insertBefore(child, ref, loadedBeforeMove, loadedChildren) {
    let index;
    if (ref) {
      index = this.childNodes.indexOf(ref);
    }
    this.insertChild(child, index, null, loadedBeforeMove, loadedChildren);
  }

  insertAfter(child, ref, loadedBeforeMove, loadedChildren) {
    let index;
    if (ref) {
      index = this.childNodes.indexOf(ref);
      if (index !== -1) index += 1;
    }
    this.insertChild(child, index, null, loadedBeforeMove, loadedChildren);
  }

  removeChild(child) {
    const index = this.childNodes.indexOf(child);

    if (index > -1) {
      this.store && this.store.deregisterNode(child);
      child.parent = null;
      this.childNodes.splice(index, 1);
    }

    this.updateLeafState();
  }

  removeChildByData(data) {
    let targetNode = null;
    this.childNodes.forEach(node => {
      if (node.data === data) {
        targetNode = node;
      }
    });

    if (targetNode) {
      this.removeChild(targetNode);
    }
  }

  expand(callback, expandParent) {
    const done = () => {
      if (expandParent) {
        let parent = this.parent;
        while (parent.level > 0) {
          parent.expanded = true;
          parent = parent.parent;
        }
      }
      this.expanded = true;
      if (callback) callback();
    };

    if (this.shouldLoadData()) {
      this.loadNode(done);
    } else {
      if (
        this.store.lazy === true &&
        this.store.load &&
        this.loadedBeforeMove
      ) {
        this.isLeaf = true;
      }
      done();
    }
  }

  loadNode(done) {
    this.loadData(data => {
      if (data instanceof Array) {
        if (this.checked) {
          this.setChecked(true, true);
        } else if (!this.store.checkStrictly) {
          reInitChecked(this);
        }
        done && done();
      }
    });
  }

  doCreateChildren(array, defaultProps = {}) {
    array.forEach(item => {
      this.insertChild(merge({ data: item }, defaultProps));
    });
  }

  collapse() {
    this.expanded = false;
  }

  shouldLoadData() {
    return (
      this.store.lazy === true &&
      this.store.load &&
      !this.loaded &&
      !this.isLeaf &&
      !this.loadedBeforeMove
    );
  }

  updateLeafState(isSetLeafFalse) {
    if (isSetLeafFalse) {
      this.isLeaf = false;
      return;
    }

    if (
      this.store.lazy === true &&
      this.loaded !== true &&
      typeof this.isLeafByUser !== 'undefined'
    ) {
      this.isLeaf = this.isLeafByUser;
      return;
    }
    const childNodes = this.childNodes;
    if (
      !this.store.lazy ||
      (this.store.lazy === true && this.loaded === true)
    ) {
      this.isLeaf = !childNodes || childNodes.length === 0;
      return;
    }
    this.isLeaf = false;
  }

  setChecked(value, deep, recursion, passValue, rootOnly = false) {
    const {
      all,
      allWithoutDisable,
      allDisabled,
      noneWithoutDisable,
      half
    } = getChildState(this.childNodes);

    // checkboxchecked
    // a, a-1a-2a-3
    // a-1a-2aa-3
    // a-1a-2acheckbox

    // 
    // 1.
    // 2.
    // 3.
    if (
      !this.isLeaf &&
      !recursion &&
      !allDisabled &&
      !this.store.checkStrictly
    ) {
      if (
        noneWithoutDisable &&
        this.checked === false &&
        value === false &&
        half
      ) {
        // 1.
        // 2.checkboxfalse
        // 3.
        // 4.
        value = true;
      } else if (!all && allWithoutDisable) {
        // 
        value = false;
      }
    }
    this.indeterminate = value === 'half';
    this.checked = value === true;

    if (this.store.checkStrictly) return;

    if (!(this.shouldLoadData() && !this.store.checkDescendants)) {
      // if (!this.isLeaf && !all && allWithoutDisable) {
      //   this.checked = false;
      //   value = false;
      // }

      const handleDescendants = () => {
        if (deep) {
          const childNodes = this.childNodes;
          for (let i = 0, j = childNodes.length; i < j; i++) {
            const child = childNodes[i];
            passValue = passValue || value !== false;
            if (!child.disabled) {
              child.setChecked(passValue, deep, true, passValue);
            }
          }
          const { half, all, allDisabled } = getChildState(childNodes);
          if (!all && !allDisabled) {
            this.checked = all;
            this.indeterminate = half;
          }
        }
      };

      if (this.shouldLoadData()) {
        // Only work on lazy load data.
        this.loadData(
          () => {
            this.store.checkParentToChild && handleDescendants();
            reInitChecked(this);
          },
          {
            checked: value !== false
          }
        );
        return;
      } else {
        if (!this.disabled) {
          this.store.checkParentToChild && handleDescendants();
        }
      }
    }

    const parent = this.parent;
    if (!parent || parent.level === 0) return;

    if (!recursion && !rootOnly && this.store.checkChildToParent) {
      reInitChecked(parent);
    }
  }

  getChildren() {
    // this is data
    const data = this.data;
    if (!data) return null;

    const props = this.store.props;
    let children = 'children';
    if (props) {
      children = props.children || 'children';
    }

    if (data[children] === undefined) {
      data[children] = null;
    }

    return data[children];
  }

  updateChildren() {
    const newData = this.getChildren() || [];
    const oldData = this.childNodes.map(node => node.data);

    const newDataMap = {};
    const newNodes = [];

    newData.forEach((item, index) => {
      if (item[NODE_KEY]) {
        newDataMap[item[NODE_KEY]] = { index, data: item };
      } else {
        newNodes.push({ index, data: item });
      }
    });

    oldData.forEach(item => {
      if (!newDataMap[item[NODE_KEY]]) this.removeChildByData(item);
    });

    newNodes.forEach(({ index, data }) => {
      this.insertChild({ data }, index);
    });

    this.updateLeafState();
  }

  loadData(callback, defaultProps = {}) {
    if (
      this.store.lazy === true &&
      this.store.load &&
      !this.loaded &&
      (!this.loading || Object.keys(defaultProps).length)
    ) {
      this.loading = true;

      const resolve = children => {
        this.loaded = true;
        this.loading = false;
        this.childNodes = [];
        this.doCreateChildren(children, defaultProps);

        this.updateLeafState();
        if (callback) {
          callback.call(this, children);
        }
      };

      this.store.load(this, resolve);
    } else {
      if (callback) {
        callback.call(this);
      }
    }
  }
}
