<script>
import {Clickoutside, emitter, migrating} from '@hui/utils';
import {Button as ElButton, ButtonGroup as ElButtonGroup} from '@hui/components/button';
import { generateId } from '@hui/utils';

export default {
  name: 'ElDropdown',

  componentName: 'ElDropdown',

  directives: { Clickoutside },

  components: {
    ElButton,
    ElButtonGroup
  },

  mixins: [emitter, migrating],

  provide() {
    return {
      dropdown: this
    };
  },

  props: {
    trigger: {
      type: String,
      default: 'hover'
    },
    type: {
      type: String,
      default: ''
    },
    size: {
      type: String,
      default: ''
    },
    splitButton: {
      type: Boolean,
      default: null
    },
    hideOnClick: {
      type: Boolean,
      default: true
    },
    placement: {
      type: String,
      default: 'bottom-end'
    },
    visibleArrow: {
      type: Boolean,
      default: true
    },
    showTimeout: {
      type: Number,
      default: 250
    },
    hideTimeout: {
      type: Number,
      default: 150
    },
    hideOnClickOutside: {
      type: Boolean,
      default: true
    },
    isItemKeyEnterShow: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      timeout: null,
      visible: false,
      triggerElm: null,
      menuItems: null,
      menuItemsArray: null,
      dropdownElm: null,
      focusing: false,
      listId: `dropdown-menu-${generateId()}`
    };
  },

  computed: {
    dropdownSize() {
      return this.size || (this.$ELEMENT || {}).size;
    }
  },

  watch: {
    visible(val) {
      this.broadcast('ElDropdownMenu', 'visible', val);
      this.$emit('visible-change', val);
    },
    focusing(val) {
      const selfDefine = this.$el.querySelector('.el-dropdown-selfdefine');
      if (selfDefine) {
        // 
        if (val) {
          selfDefine.className += ' focusing';
        } else {
          selfDefine.className = selfDefine.className.replace('focusing', '');
        }
      }
    }
  },

  mounted() {
    this.$on('menu-item-click', this.handleMenuItemClick);
    this.initEvent();
    this.initAria();
  },

  methods: {
    getMigratingConfig() {
      return {
        props: {
          'menu-align': 'menu-align is renamed to placement.'
        }
      };
    },
    show() {
      if (this.triggerElm.disabled) return;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(
        () => {
          this.visible = true;
        },
        this.trigger === 'click' ? 0 : this.showTimeout
      );
    },
    hide() {
      if (this.triggerElm.disabled) return;
      this.removeTabindex();
      this.resetTabindex(this.triggerElm);
      clearTimeout(this.timeout);
      this.timeout = setTimeout(
        () => {
          this.visible = false;
        },
        this.trigger === 'click' ? 0 : this.hideTimeout
      );
    },
    handleClick() {
      if (this.triggerElm.disabled) return;
      if (this.visible) {
        this.hide();
      } else {
        this.show();
      }
    },
    clickOutside() {
      if (this.hideOnClickOutside) {
        this.hide();
      }
    },
    handleTriggerKeyDown(ev) {
      const keyCode = ev.keyCode;
      if ([38, 40].indexOf(keyCode) > -1) {
        // up/down
        this.removeTabindex();
        this.resetTabindex(this.menuItems[0]);
        this.menuItems[0].focus();
        ev.preventDefault();
        ev.stopPropagation();
      } else if (keyCode === 13) {
        // space enter
        this.handleClick();
      } else if ([9, 27].indexOf(keyCode) > -1) {
        // tab || esc
        this.hide();
      }
    },
    handleItemKeyDown(ev) {
      const keyCode = ev.keyCode;
      const target = ev.target;
      const currentIndex = this.menuItemsArray.indexOf(target);
      const max = this.menuItemsArray.length - 1;
      let nextIndex;
      if ([38, 40].indexOf(keyCode) > -1) {
        // up/down
        if (keyCode === 38) {
          // up
          nextIndex = currentIndex !== 0 ? currentIndex - 1 : 0;
        } else {
          // down
          nextIndex = currentIndex < max ? currentIndex + 1 : max;
        }
        this.removeTabindex();
        this.resetTabindex(this.menuItems[nextIndex]);
        this.menuItems[nextIndex].focus();
        ev.preventDefault();
        ev.stopPropagation();
      } else if (keyCode === 13) {
        // enter
        this.triggerElm.focus();
        target.click();
        // itemfalse
        if (this.hideOnClick && !this.isItemKeyEnterShow) {
          // click
          this.visible = false;
        }
      } else if ([9, 27].indexOf(keyCode) > -1) {
        // tab // esc
        this.hide();
        this.triggerElm.focus();
      }
    },
    resetTabindex(ele) {
      // tab
      this.removeTabindex();
      ele.setAttribute('tabindex', '0'); // 
    },
    removeTabindex() {
      this.triggerElm.setAttribute('tabindex', '-1');
      this.menuItemsArray.forEach(item => {
        item.setAttribute('tabindex', '-1');
      });
    },
    initAria() {
      this.dropdownElm.setAttribute('id', this.listId);
      this.triggerElm.setAttribute('aria-haspopup', 'list');
      this.triggerElm.setAttribute('aria-controls', this.listId);
      this.menuItems = this.dropdownElm.querySelectorAll("[tabindex='-1']");
      this.menuItemsArray = Array.prototype.slice.call(this.menuItems);

      if (!this.splitButton) {
        // 
        this.triggerElm.setAttribute('role', 'button');
        this.triggerElm.setAttribute('tabindex', '0');
        this.triggerElm.setAttribute(
          'class',
          (this.triggerElm.getAttribute('class') || '') +
            ' el-dropdown-selfdefine'
        ); // 
      }
    },
    initEvent() {
      const {
        trigger,
        show,
        hide,
        handleClick,
        splitButton,
        handleTriggerKeyDown,
        handleItemKeyDown
      } = this;
      this.triggerElm = splitButton
        ? this.$refs.trigger.$el
        : this.$slots.default[0].elm;

      const dropdownElm = (this.dropdownElm = this.$slots.dropdown[0].elm);

      this.triggerElm.addEventListener('keydown', handleTriggerKeyDown); // triggerElm keydown
      dropdownElm.addEventListener('keydown', handleItemKeyDown, true); // item keydown
      // 
      if (!splitButton) {
        this.triggerElm.addEventListener('focus', () => {
          this.focusing = true;
        });
        this.triggerElm.addEventListener('blur', () => {
          this.focusing = false;
        });
        this.triggerElm.addEventListener('click', () => {
          this.focusing = false;
        });
      }
      if (trigger === 'hover') {
        this.triggerElm.addEventListener('mouseenter', show);
        this.triggerElm.addEventListener('mouseleave', hide);
        dropdownElm.addEventListener('mouseenter', show);
        dropdownElm.addEventListener('mouseleave', hide);
      } else if (trigger === 'click') {
        this.triggerElm.addEventListener('click', handleClick);
      }
    },
    handleMenuItemClick(command, instance) {
      if (this.hideOnClick) {
        // 
        clearTimeout(this.timeout);
        this.visible = false;
      }
      this.$emit('command', command, instance);
    },
    focus() {
      this.triggerElm.focus && this.triggerElm.focus();
    }
  },

  render() {
    const { hide, clickOutside, splitButton, type, dropdownSize } = this;

    const handleMainButtonClick = event => {
      this.$emit('click', event);
      hide();
    };

    const triggerElm = !splitButton ? (
      this.$slots.default
    ) : (
      <el-button-group>
        <el-button
          type={type}
          size={dropdownSize}
          nativeOn-click={handleMainButtonClick}
        >
          {this.$slots.default}
        </el-button>
        <el-button
          ref='trigger'
          type={type}
          size={dropdownSize}
          class='el-dropdown__caret-button'
        >
          <i class='el-dropdown__icon h-icon-angle_down_sm' />
        </el-button>
      </el-button-group>
    );

    return (
      <div class='el-dropdown' v-clickoutside={clickOutside}>
        {triggerElm}
        {this.$slots.dropdown}
      </div>
    );
  }
};
</script>
