<script>
export default {
  name: 'ElNavItemGroup',

  componentName: 'ElNavItemGroup',

  inject: ['rootNav'],
  props: {
    title: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      paddingLeft: 10
    }
  },
  computed: {
    levelPadding() {
      let padding = 10
      let subPadding = 26 // 
      let parent = this.$parent

      // 
      if (!this.icon) {
        padding = 22
        subPadding = 0
      }

      if (this.rootNav.collapse)
        return 16
      while (parent && parent.$options.componentName !== 'ElNav') {
        if (parent.$options.componentName === 'ElSubnav')
          padding += 10

        parent = parent.$parent
        // 
        if (parent && parent.$options.componentName === 'ElNav')
          padding += subPadding
      }
      padding === 10 && (padding = 10)
      return padding
    }
  }
}
</script>
<template>
  <li class="el-nav-item-group">
    <div
      :style="{ paddingLeft: levelPadding + 'px' }"
      class="el-nav-item-group__title"
    >
      <template v-if="!$slots.title">
        {{ title }}
      </template>
      <slot v-else name="title" />
    </div>
    <ul>
      <slot />
    </ul>
  </li>
</template>
