<script>
export default {
  name: 'ElCard',

  props: {
    header: {
      type: null,
      default: null,
    },
    headerStyle: {
      type: null,
      default: null,
    },
    bodyStyle: {
      type: null,
      default: null,
    },
    shadow: {
      type: String,
      default: 'hover',
    }
  },
}
</script>

<template>
  <div :class="['el-card',`is-${shadow}-shadow`]">
    <div v-if="$slots.header || header" class="el-card__header" :style="headerStyle">
      <slot name="header">
        <span class="title">{{ header }}</span>
      </slot>
    </div>
    <div :style="bodyStyle" class="el-card__body">
      <slot />
    </div>
  </div>
</template>
