<script>
import { Tooltip } from '@hui/components/tooltip'

export default {
  name: 'ElSliderButton',

  components: {
    ElTooltip: Tooltip,
  },

  props: {
    value: {
      type: Number,
      default: 0,
    },
    vertical: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      hovering: false,
      dragging: false,
      startX: 0,
      currentX: 0,
      startY: 0,
      currentY: 0,
      startPosition: 0,
      newPosition: null,
      oldValue: this.value,
    }
  },

  computed: {
    disabled() {
      return this.$parent.disabled
    },

    max() {
      return this.$parent.max
    },

    min() {
      return this.$parent.min
    },

    step() {
      return this.$parent.step
    },

    showTooltip() {
      return (
        this.$parent.showTooltip
        && !(this.$parent.showInput && this.$parent.range)
      )
    },

    precision() {
      return this.$parent.precision
    },

    currentPosition() {
      return `${((this.value - this.min) / (this.max - this.min)) * 100}%`
    },

    enableFormat() {
      return this.$parent.formatTooltip instanceof Function
    },

    formatValue() {
      return (
        (this.enableFormat && this.$parent.formatTooltip(this.value))
        || this.value
      )
    },

    wrapperStyle() {
      return this.vertical
        ? { bottom: this.currentPosition }
        : { left: this.currentPosition }
    },
  },

  watch: {
    dragging(val) {
      this.$parent.dragging = val
    },
  },

  methods: {
    displayTooltip() {
      this.$refs.tooltip && (this.$refs.tooltip.showPopper = true)
    },

    hideTooltip() {
      this.$refs.tooltip && (this.$refs.tooltip.showPopper = false)
    },

    handleMouseEnter() {
      this.hovering = true
      this.displayTooltip()
    },

    handleMouseLeave() {
      this.hovering = false
      this.hideTooltip()
    },

    onButtonDown(event) {
      if (this.disabled)
        return
      event.preventDefault()
      this.$emit('button-down')
      this.onDragStart(event)
      window.addEventListener('mousemove', this.onDragging)
      window.addEventListener('mouseup', this.onDragEnd)
      window.addEventListener('contextmenu', this.onDragEnd)
    },

    onDragStart(event) {
      this.dragging = true
      if (this.vertical)
        this.startY = event.clientY
      else
        this.startX = event.clientX

      this.startPosition = parseFloat(this.currentPosition)
      // add by zhangxiaogang
      this.$parent.$emit('drag-start', event, this.oldValue)
    },

    onDragging(event) {
      if (this.dragging) {
        this.displayTooltip()
        this.$parent.resetSize()
        let diff = 0
        if (this.vertical) {
          this.currentY = event.clientY
          diff
            = ((this.startY - this.currentY) / this.$parent.sliderSize) * 100
        }
        else {
          this.currentX = event.clientX
          diff
            = ((this.currentX - this.startX) / this.$parent.sliderSize) * 100
        }
        this.newPosition = this.startPosition + diff
        this.setPosition(this.newPosition)
      }
    },

    onDragEnd() {
      if (this.dragging) {
        /*
         *  mouseup  click
         *  preventDefault  mouseup  click  DOM 
         */
        setTimeout(() => {
          this.dragging = false
          this.hideTooltip()
          this.setPosition(this.newPosition)
          // add by zhangxiaogang
          this.$emit('drag-end')
        }, 0)
        window.removeEventListener('mousemove', this.onDragging)
        window.removeEventListener('mouseup', this.onDragEnd)
        window.removeEventListener('contextmenu', this.onDragEnd)
      }
    },

    setPosition(newPosition, type) {
      if (newPosition === null)
        return
      if (newPosition < 0)
        newPosition = 0
      else if (newPosition > 100)
        newPosition = 100

      const lengthPerStep = 100 / ((this.max - this.min) / this.step)
      const steps = Math.round(newPosition / lengthPerStep)
      let value
        = steps * lengthPerStep * (this.max - this.min) * 0.01 + this.min
      value = parseFloat(value.toFixed(this.precision))
      this.newPosition = newPosition
      this.$emit('input', value)
      type && this.$emit(type, value)
      this.$refs.tooltip && this.$refs.tooltip.updatePopper()
      if (!this.dragging && this.value !== this.oldValue)
        this.oldValue = this.value
    },
  },
}
</script>

<template>
  <div
    ref="button"
    :class="{ hover: hovering, dragging: dragging }"
    :style="wrapperStyle"
    class="el-slider__button-wrapper"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave"
    @mousedown="onButtonDown"
  >
    <el-tooltip
      ref="tooltip"
      :disabled="!showTooltip"
      effect="light"
      :placement="vertical?'right':'top'"
    >
      <span slot="content">
        {{ formatValue }}
      </span>
      <div
        :class="{ hover: hovering, dragging: dragging }"
        class="el-slider__button"
      />
    </el-tooltip>
  </div>
</template>
