<script>
import Vue from 'vue'
import { Clickoutside, VuePopper, emitter, hasOwn, merge } from '@hui/utils'
import { Input } from '@hui/components/input'
import {
  DEFAULT_FORMATS,
  TYPE_VALUE_RESOLVER_MAP,
  dateValidator,
  formatAsFormatAndType,
  formatDate,
  formatSpecial,
  getTimestamp,
  isDateObject,
  isEmpty,
  isString,
  modifyDate,
  parseAsFormatAndType,
  parseDate,
  toDate
} from './util'

const NewPopper = {
  props: {
    appendToBody: VuePopper.props.appendToBody,
    offset: VuePopper.props.offset,
    boundariesPadding: VuePopper.props.boundariesPadding,
    arrowOffset: VuePopper.props.arrowOffset,
  },
  methods: VuePopper.methods,

  data() {
    return merge({ visibleArrow: false }, VuePopper.data)
  },
  beforeDestroy: VuePopper.beforeDestroy,
}

const PLACEMENT_MAP = {
  left: 'bottom-start',
  center: 'bottom',
  right: 'bottom-end',
}

const HAVE_TRIGGER_TYPES = [
  'date',
  'datetime',
  'time',
  'time-select',
  'week',
  'month',
  'year',
  'daterange',
  'monthrange',
  'timerange',
  'datetimerange',
  'dates',
  'yearrange',
]

/*
 * Considers:
 *   1. Date object
 *   2. date string
 *   3. array of 1 or 2
 */
const valueEquals = function(a, b) {
  // considers Date object and string
  const dateEquals = function(a, b) {
    const aIsDate = a instanceof Date
    const bIsDate = b instanceof Date
    if (aIsDate && bIsDate)
      return a.getTime() === b.getTime()

    if (!aIsDate && !bIsDate)
      return a === b

    return false
  }

  const aIsArray = a instanceof Array
  const bIsArray = b instanceof Array
  if (aIsArray && bIsArray) {
    if (a.length !== b.length)
      return false

    return a.every((item, index) => dateEquals(item, b[index]))
  }
  if (!aIsArray && !bIsArray)
    return dateEquals(a, b)

  return false
}

const stringValidator = function(val) {
  // either: String, Array of String, null / undefined
  return (
    isEmpty(val)
    || isString(val)
    || (Array.isArray(val) && val.length === 2 && val.every(isString))
  )
}

export default {
  components: { ElInput: Input },

  directives: { Clickoutside },
  mixins: [emitter, NewPopper],

  inject: {
    elForm: {
      default: '',
    },
    elFormItem: {
      default: '',
    },
  },

  props: {
    size: {
      type: String,
      default: null,
    },
    format: {
      type: String,
      default: null,
    },
    valueFormat: {
      type: String,
      default: null,
    },
    readonly: {
      type: Boolean,
      default: null,
    },
    placeholder: {
      type: String,
      default: null,
    },
    startPlaceholder: {
      type: String,
      default: null,
    },
    endPlaceholder: {
      type: String,
      default: null,
    },
    prefixIcon: {
      type: String,
      default: null,
    },
    clearIcon: {
      type: String,
      default: 'h-icon-close_f',
    },
    id: {
      default: '',
      validator: stringValidator,
    },
    name: {
      default: '',
      validator: stringValidator,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    popperClass: {
      type: String,
      default: null,
    },
    editable: {
      type: Boolean,
      default: true,
    },
    align: {
      type: String,
      default: 'left',
    },
    value: {
      default: null,
      validator: dateValidator,
    },
    defaultValue: {
      default: null,
      validator: dateValidator,
    },
    defaultTime: {
      default: null,
      validator: dateValidator,
    },
    rangeSeparator: { type: String, default: '-' },
    pickerOptions: {
      type: Object,
      default: () => { },
    },
    unlinkPanels: {
      type: Boolean,
      default: true,
    },
    unlinkRange: {
      type: Boolean,
      default: true,
    },
    validateEvent: {
      type: Boolean,
      default: true,
    },
    unlink: {
      type: Boolean,
      default: null,
    },
    calendarType: {
      type: String,
      default: '',
    },
    linkedRangeChosen: {
      type: Boolean,
      default: () => false
    },
    formatRangeSelection: {
      type: Boolean,
      default: () => true
    }
  },

  data() {
    return {
      pickerVisible: false,
      showClose: false,
      userInput: null,
      valueOnOpen: null, // value when picker opens, used to determine whether to emit change
      unwatchPickerOptions: null,
      changeInput: true,
      preConfirmValue: null,
      calcValue: null,
      tempTableType: null,
    }
  },

  computed: {
    ranged() {
      return this.type.includes('range')
    },

    refInput() {
      if (this.reference)
        return [].slice.call(this.reference.querySelectorAll('input'))

      return []
    },

    valueIsEmpty() {
      const val = this.value
      if (Array.isArray(val)) {
        for (let i = 0, len = val.length; i < len; i++) {
          if (val[i])
            return false
        }
      }
      else {
        if (val)
          return false
      }
      return true
    },

    triggerClass() {
      return (
        this.prefixIcon
        || (this.type === 'time'
          || this.type === 'time-select'
          || this.type === 'timerange'
          ? 'h-icon-clock'
          : 'h-icon-calendar')
      )
    },

    selectionMode() {
      if (this.type === 'week')
        return 'week'
      else if (this.type === 'month')
        return 'month'
      else if (this.type === 'year')
        return 'year'
      else if (this.type === 'dates')
        return 'dates'

      return 'day'
    },

    haveTrigger() {
      if (typeof this.showTrigger !== 'undefined')
        return this.showTrigger

      return HAVE_TRIGGER_TYPES.includes(this.type)
    },

    displayValue() {
      let formattedValue = formatAsFormatAndType(
        this.preConfirmValue,
        this.format,
        this.type,
      )
      // special date(thniboer)
      if (this.calendarType === 'th' || this.calendarType === 'np') {
        formattedValue = formatSpecial(
          formattedValue,
          this.format,
          this.type,
          this.calendarType,
        )
      }
      const userInput = this.userInput
      const isTrueValue = () => {
        if (
          Array.isArray(formattedValue)
          && !isEmpty(formattedValue[0])
          && !isEmpty(formattedValue[1])
        )
          return true

        return !isEmpty(formattedValue)
      }
      if (Array.isArray(userInput) && isTrueValue()) {
        return [
          userInput[0] || (formattedValue && formattedValue[0]) || '',
          userInput[1] || (formattedValue && formattedValue[1]) || '',
        ]
      }
      else if (userInput) {
        return userInput
      }
      else if (isTrueValue()) {
        return this.type === 'dates'
          ? formattedValue.join(', ')
          : formattedValue
      }
      else {
        return ''
      }
    },

    parsedValue() {
      if (!this.calcValue)
        return this.calcValue // component value is not set
      if (this.type === 'time-select') {
        // time-select does not require parsing, this might change in next major version
        return this.value.length !== 5 ? this.calcValue : this.value
      }
      const valueIsDateObject
        = isDateObject(this.calcValue)
        || (Array.isArray(this.calcValue) && this.calcValue.every(isDateObject))
      if (valueIsDateObject)
        return this.calcValue

      // modify by taooujiang 2024-08-13 valueFormat;formatToValue
      // if (this.valueFormat) {
      //   const formatValue = parseAsFormatAndType(
      //     this.calcValue,
      //     this.valueFormat,
      //     this.type,
      //     this.rangeSeparator,
      //   )
      //   if (!isEmpty(formatValue))
      //     return formatValue
      //   else
      //     return this.calcValue
      // }

      // NOTE: deal with common but incorrect usage, should remove in next major version
      // user might provide string / timestamp without value-format, coerce them into date (or array of date)
      return Array.isArray(this.calcValue)
        ? this.calcValue.map((val) => { return this.linkedRangeChosen ? (val ? toDate(val) : null) : toDate(val) })
        : toDate(this.calcValue)
    },

    _elFormItemSize() {
      return (this.elFormItem || {}).elFormItemSize
    },

    pickerSize() {
      return this.size || this._elFormItemSize || (this.$ELEMENT || {}).size
    },

    pickerDisabled() {
      return this.disabled || (this.elForm || {}).disabled
    },

    firstInputId() {
      const obj = {}
      let id
      if (this.ranged)
        id = this.id && this.id[0]
      else
        id = this.id

      if (id)
        obj.id = id
      return obj
    },

    secondInputId() {
      const obj = {}
      let id
      if (this.ranged)
        id = this.id && this.id[1]

      if (id)
        obj.id = id
      return obj
    },

    activeBarStyle() {
      const activeBarStyle = {}
      if (this.pickerVisible && this.picker.tableType) {
        const currentInput = this.$refs.reference.querySelector(`.el-range-input[data-range-input-${this.picker.tableType}]`)
        if (currentInput) {
          activeBarStyle.left = `${currentInput.offsetLeft}px`
          activeBarStyle.width = `${currentInput.offsetWidth}px`
        }
      }
      return activeBarStyle
    },
  },

  watch: {
    pickerVisible(val) {
      if (this.readonly || this.pickerDisabled)
        return
      if (val) {
        this.showPicker()
        this.valueOnOpen = Array.isArray(this.value)
          ? [...this.value]
          : this.value
      }
      else {
        this.hidePicker()
        this.userInput = null
        if (this.validateEvent)
          this.dispatch('ElFormItem', 'el.form.blur')

        this.$emit(
          'blur',
          this.formatToValue(this.calcValue),
          this.displayValue,
        )
        this.blur()
      }
    },
    parsedValue: {
      immediate: true,
      handler(val) {
        if (this.picker && val)
          this.picker.value = val
      },
    },
    defaultValue(val) {
      // NOTE: should eventually move to jsx style picker + panel ?
      if (this.picker)
        this.picker.defaultValue = val
    },
    value(val, oldVal) {
      if (
        !valueEquals(val, oldVal)
        && !this.pickerVisible
        && this.validateEvent
      ) {
        this.dispatch('ElFormItem', 'el.form.change', val)
        if (!valueEquals(this.formatToValue(this.preConfirmValue), val))
          this.preConfirmValue = val

        if (!valueEquals(this.formatToValue(this.calcValue), val))
          this.calcValue = val
      }
    },
    preConfirmValue(val, oldVal) {
      if (!valueEquals(val, this.value))
        this.$emit('input', this.formatToValue(val), this.displayValue)
    },
    calcValue(val, oldVal) {
      if (
        !valueEquals(val, this.preConfirmValue)
        && this.isValidValue(val)
        && this.pickerVisible
      ) {
        // calcValueel.form.change(@param doNotValidtrue)pick
        this.emitChange(val, false, true)
      }
    },
    displayValue(val) {
      if (this.picker)
        this.picker.parentDisplayValue = val
    }
  },
  created() {
    // init
    const { value: val, type } = this
    if (typeof val === 'number')
      this.calcValue = new Date(val)
    else if (
      typeof val === 'string'
      && (type.includes('date')
        || type.includes('month')
        || type.includes('year')
        || type.includes('week'))
    )
      this.calcValue = val ? (!isNaN(new Date(val).getTime()) ? new Date(val) : new Date()) : ''

    else
      this.calcValue = val

    this.preConfirmValue = val
    // vue-popper
    this.popperOptions = {
      boundariesPadding: 0,
      gpuAcceleration: false,
    }
    this.placement = PLACEMENT_MAP[this.align] || PLACEMENT_MAP.left
    this.$on('fieldReset', this.handleFieldReset)
  },
  mounted() {
    this.setReference()
  },
  methods: {
    setReference() {
      const reference = this.$refs.reference
      this.reference = reference.$el || reference
    },

    replaceDate(value) {
      return value.replace(/-/g, '/')
    },

    focus() {
      if (window)
        window.focus()

      this.handleFocus()
      this.refInput[0].focus()
    },

    blur() {
      this.refInput.forEach(input => input.blur())
    },

    formatToValue(value) {
      if (typeof value === 'number')
        value = new Date(value)

      if (isEmpty(value))
        return ''

      const isParsed
        = isDateObject(value)
        || (Array.isArray(value) && value.every(isDateObject))
      if (this.valueFormat && isParsed) {
        const formatValue = formatAsFormatAndType(
          value,
          this.valueFormat,
          this.type,
        )
        return isEmpty(formatValue) ? value : formatValue
      }
      else {
        return value
      }
    },

    // {parse, formatTo} String deals with user input
    parseString(value) {
      const type = Array.isArray(value)
        ? this.type
        : this.type.replace('range', '')
      return parseAsFormatAndType(value, this.format, type)
    },

    formatToString(value) {
      const type = Array.isArray(value)
        ? this.type
        : this.type.replace('range', '')
      return formatAsFormatAndType(value, this.format, type)
    },

    handleMouseEnter() {
      if (this.readonly || this.pickerDisabled)
        return
      if (!this.valueIsEmpty && this.clearable)
        this.showClose = true
    },
    // 0
    fixNumberString(num, length) {
      return (`${num}`).length < length ? ((new Array(length + 1)).join('0') + num).slice(-length) : `${num}`
    },
    // ///
    setCurrentSelection(selectionInfo, event, direction = 'right') {
      if (!selectionInfo)
        return

      const { target } = event
      const { startIndex, endIndex } = selectionInfo
      const { selectionStart, selectionEnd } = target
      const formatSelections = target._formatSelections ? JSON.parse(target._formatSelections) : null
      if (direction === 'right') {
        if (selectionEnd >= endIndex && formatSelections) {
          const tempIndex = formatSelections.findIndex(item => item.endIndex === endIndex)
          if (tempIndex === formatSelections.length - 1) { target.setSelectionRange(startIndex, endIndex) }
          else {
            target.setSelectionRange(formatSelections[tempIndex + 1].startIndex, formatSelections[tempIndex + 1].endIndex)
            this.setInputSelection(formatSelections, target)
          }
        }
      }
      else if (direction === 'left') {
        const tempIndex = formatSelections.findIndex(item => item.startIndex === startIndex && item.endIndex === endIndex)
        if (tempIndex === 0) {
          target.setSelectionRange(startIndex, endIndex)
        }
        else {
          target.setSelectionRange(formatSelections[tempIndex - 1].startIndex, formatSelections[tempIndex - 1].endIndex)
          this.setInputSelection(formatSelections, target)
        }
      }
      else if (direction === 'up' || direction === 'down') {
        const tempSubstringText = target.value.substring(startIndex, endIndex)
        const targetValue = direction === 'up' ? (parseInt(tempSubstringText) + 1) : (parseInt(tempSubstringText) - 1)
        let targetValueString = this.fixNumberString(targetValue, selectionInfo.formatString.length)
        if (selectionInfo.formatString === 'HH' || selectionInfo.formatString === 'mm' || selectionInfo.formatString === 'ss' || selectionInfo.formatString === 'yyyy') {
          if (!(new RegExp(selectionInfo.regx).test(targetValueString)))
            targetValueString = tempSubstringText
        }
        target.value = target.value.substring(0, startIndex) + targetValueString + target.value.substring(endIndex)
        this.handleSetInput(event, true)
      }
      else if (direction === 'none') {
        target.setSelectionRange(startIndex, endIndex)
      }
    },
    // yyyy00
    formatDateSelection(splitSelections, currentSplitIndex, selectionInfo, event) {
      const { selectionStart, selectionEnd, value } = event.target

      const currentSplitSelection = splitSelections[currentSplitIndex]
      const tempIndex = value.indexOf(currentSplitSelection.content, currentSplitSelection.content ? selectionInfo.startIndex : selectionInfo.endIndex)
      const tempNowTime = new Date()
      const nowDateString = this.formatToString(new Date(tempNowTime.getFullYear(), tempNowTime.getMonth(), 1))
      // selectionStart
      let tempFormatString = selectionInfo.formatString
      let tempFormatSelection = null

      const formatSelections = event.target._formatSelections ? JSON.parse(event.target._formatSelections) : null

      if (formatSelections)
        tempFormatSelection = formatSelections.find(it => selectionStart <= it.endIndex)

      if (tempFormatSelection) {
        tempFormatString = tempFormatSelection.formatString
        currentSplitIndex = formatSelections.findIndex(it => selectionStart <= it.endIndex)
      }

      const formatStringIsHMS = (tempFormatString === 'HH' || tempFormatString === 'mm' || tempFormatString === 'ss')

      if (tempIndex < currentSplitSelection.startIndex) {
        let tempSubstringText = value.substring(currentSplitIndex > 0 ? splitSelections[currentSplitIndex - 1].endIndex : 0, tempIndex < 0 ? selectionStart : tempIndex)
        const defaultText = tempSubstringText === '' ? (!formatStringIsHMS ? '1' : '0') : tempSubstringText
        if (tempSubstringText.length < tempFormatString.length) {
          if (tempFormatString === 'yyyy')
            tempSubstringText = defaultText + this.fixNumberString(0, tempFormatString.length - defaultText.length)
          else
            tempSubstringText = this.fixNumberString(0, tempFormatString.length - defaultText.length) + defaultText
        }
        const tempValue = `${currentSplitIndex ? value.substring(0, splitSelections[currentSplitIndex - 1].endIndex) : ''}${tempSubstringText}${value.substring((tempIndex < 0 ? selectionStart : tempIndex) + (currentSplitSelection.content ? (currentSplitSelection.content.length - 1) : 0))}`

        event.target.value = tempValue + nowDateString.substring(tempValue.length)
        this.handleSetInput(event, true)
      }
      // HH mm ss 
      else if (tempIndex === currentSplitSelection.startIndex && formatStringIsHMS) {
        let tempSubstringText = value.substring(currentSplitIndex > 0 ? splitSelections[currentSplitIndex - 1].endIndex : 0, tempIndex)
        let isNotNext = true
        if (tempSubstringText.length === tempFormatString.length) {
          if (tempFormatString === 'HH' && parseInt(tempSubstringText) > 23) {
            tempSubstringText = '23'
            isNotNext = false
          }
          else if ((tempFormatString === 'mm' || tempFormatString === 'ss') && parseInt(tempSubstringText) > 59) {
            tempSubstringText = '59'
            isNotNext = false
          }
        }
        const tempValue = `${currentSplitIndex ? value.substring(0, splitSelections[currentSplitIndex - 1].endIndex) : ''}${tempSubstringText}${value.substring(tempIndex + (currentSplitSelection.content ? (currentSplitSelection.content.length - 1) : 0))}`

        event.target.value = tempValue
        this.handleSetInput(event, isNotNext)
      }
      else {
        event.preventDefault()
      }
    },
    // 
    formatCurrentSelectionString(event) {
      const selectionInfo = event.target._selectionInfo ? JSON.parse(event.target._selectionInfo) : null
      if (selectionInfo && event.target.value) {
        const splitSelections = event.target._splitSelections ? JSON.parse(event.target._splitSelections) : null
        if (splitSelections) {
          const currentSplitIndex = splitSelections.findIndex(item => item.startIndex >= selectionInfo.endIndex)
          this.$nextTick(() => {
            this.formatDateSelection(splitSelections, currentSplitIndex, selectionInfo, event)
          })
        }
      }
    },
    // compositionstart  preventDefault
    //  beforeinput  input
    handleBeforeInput(event) {
      if (!this.formatRangeSelection)
        return null

      const { selectionStart, selectionEnd, value } = event.target

      if (
        ((event.inputType === 'insertCompositionText' || event.inputType === 'insertText') && /^\d$/.test(event.data))
        || (event.inputType === 'deleteContentBackward')
        || (event.data && parseDate(event.data, (this.format || DEFAULT_FORMATS[this.type])))
      ) {
        event.target._cacheText = ''
      }
      else if (event.data && !/^\d$/.test(event.data)) {
        const selectionInfo = event.target._selectionInfo ? JSON.parse(event.target._selectionInfo) : null
        const cacheText = selectionInfo ? selectionInfo.cacheValue : value.substring(selectionStart, selectionEnd)
        event.target._cacheText = cacheText
      }
    },

    handleSetInput(event, notSelectioChange) {
      if (this.formatRangeSelection) {
        const cacheText = event.target._cacheText
        const selectionInfo = event.target._selectionInfo ? JSON.parse(event.target._selectionInfo) : null
        if (!cacheText) {
          this.handleInputMethodByType(event)

          if (!notSelectioChange) {
            // 
            this.$nextTick(() => {
              this.$nextTick(() => {
              // 
                if (this.parseString(event.target.value)) {
                  this.setCurrentSelection(selectionInfo, event)
                }
                else {
                // 
                  const tempFormat = this.format ? this.format : DEFAULT_FORMATS[this.type]
                  if (event.target.value && event.target.value.length === tempFormat.length)
                    this.formatCurrentSelectionString(event)
                }
              })
            })
          }
          else {
            this.$nextTick(() => {
              this.$nextTick(() => {
                this.setCurrentSelection(selectionInfo, event, 'none')
              })
            })
          }
        }
        else {
          this.$nextTick(() => {
            event.target.value = cacheText
            event.target.setSelectionRange(selectionInfo.startIndex, selectionInfo.endIndex)
          })
        }
      }
      else {
        this.handleInputMethodByType(event)
      }
    },

    handleInputMethodByType(event) {
      if (this.type.includes('range')) {
        if (this.tempTableType === 'min')
          this.handleStartInput(event)
        else if (this.tempTableType === 'max')
          this.handleEndInput(event)
      }
      else {
        this.handleInput(event)
      }
    },

    handleInput(event) {
      const val = event.target.value
      this.$nextTick(() => {
        this.userInput = val
        const parseDate = this.parseString(val)
        if (val && this.picker) {
          if (parseDate) {
            this.picker.value = parseDate
            if (this.isValidValue(parseDate)) {
              this.emitInput(parseDate)
              // timedate
              setTimeout(() => {
                this.picker.adjustSpinners && this.picker.adjustSpinners()
              }, 0)
            }
          }
        }
        else if (this.picker) {
          this.emitInput(parseDate)
        }
      })
    },
    // event.target.dataset
    handleInputSelection(event) {
      if (!event.target.value || !this.parseString(event.target.value))
        return null

      if (!this.formatRangeSelection)
        return null

      if (event.target.nodeName !== 'INPUT' || this.type === 'week') {
        return null
      }
      else {
        if (event.target._formatSelections) {
          const tempSelections = JSON.parse(event.target._formatSelections)
          this.setInputSelection(tempSelections, event.target)
        }
        else {
          const tempFormat = this.format ? this.format : DEFAULT_FORMATS[this.type]
          const formatRange = ['yyyy', 'MM', 'dd', 'HH', 'mm', 'ss']
          const formatRangeRegx = {
            yyyy: '^[1-9]\\d{3}$',
            yy: '\\d{2}$',
            MM: '^(0?[1-9]|1[0-2])$',
            dd: '^((0?[1-9])|((1|2)[0-9])|30|31)$',
            HH: '([01][0-9]|2[0-3])',
            mm: '[0-5][0-9]',
            ss: '[0-5][0-9]',
          }
          let tempSelections = []
          const splitSelections = []
          formatRange.forEach((item) => {
            let tempFormatString = item
            let tempIndex = tempFormat?.indexOf(item)
            if (item === 'yyyy' && tempIndex < 0) {
              tempFormatString = 'yy'
              tempIndex = tempFormat.indexOf(tempFormatString)
            }
            if (tempIndex > -1) {
              tempSelections.push({
                formatString: tempFormatString,
                startIndex: tempIndex,
                endIndex: tempIndex + tempFormatString.length,
                regx: formatRangeRegx[tempFormatString]
              })
            }
          })
          tempSelections = tempSelections.sort((a, b) => { return a.startIndex - b.startIndex })
          tempSelections.length && tempSelections.forEach((item, index) => {
            if (index === tempSelections.length - 1) {
              if (tempFormat.length >= item.endIndex) {
                splitSelections.push({
                  startIndex: item.endIndex,
                  endIndex: tempFormat.length,
                  content: tempFormat.substring(item.endIndex, tempFormat.length)
                })
              }
            }
            else {
              const tempItem = tempSelections[index + 1]
              if (tempItem.startIndex > item.endIndex) {
                splitSelections.push({
                  startIndex: item.endIndex,
                  endIndex: tempItem.startIndex,
                  content: tempFormat.substring(item.endIndex, tempItem.startIndex)
                })
              }
            }
          })
          event.target._formatSelections = JSON.stringify(tempSelections)
          event.target._splitSelections = JSON.stringify(splitSelections)
          this.setInputSelection(tempSelections, event.target)
        }
        this.formatCurrentSelectionString(event)
      }
    },
    // MM
    setInputSelection(selections, target) {
      if (selections) {
        for (let i = 0; i < selections.length; i++) {
          if (target.selectionStart <= selections[i].endIndex) {
            target.setSelectionRange(selections[i].startIndex, selections[i].endIndex)
            if (target.value)
              selections[i].cacheValue = target.value

            target._selectionInfo = JSON.stringify(selections[i])

            break
          }
        }
      }
    },

    handleChange(event) {
      const val = event.target.value
      this.userInput = val
    },

    handleStartInput(event) {
      const val = event.target.value
      const preConfirmValue = this.preConfirmValue && this.preConfirmValue[0]
      const parseValue = this.parseString(val)
      const isTimerange = this.type === 'timerange'
      if (parseValue && this.picker) {
        const value = isTimerange
          ? modifyDate(
            parseValue,
            preConfirmValue.getFullYear(),
            preConfirmValue.getMonth(),
            preConfirmValue.getDate(),
          )
          : parseValue
        // TODO: 
        const tempFlag = getTimestamp(value, isTimerange ? 'normal' : null) > getTimestamp(this.picker.value && this.picker.value[1], isTimerange ? 'normal' : null)
        const newValue = [!tempFlag ? value : preConfirmValue, this.picker.value && this.picker.value[1]]
        this.picker.value = newValue

        // picker
        // if (this.linkedRangeChosen)
        event.target.value = formatDate(newValue[0], this.format || DEFAULT_FORMATS[this.type])

        if (this.isValidValue(newValue))
          this.emitInput(newValue)
      }
    },

    handleEndInput(event) {
      const val = event.target.value
      const preConfirmValue = this.preConfirmValue && this.preConfirmValue[1]
      const parseValue = this.parseString(val)
      const isTimerange = this.type === 'timerange'
      if (parseValue && this.picker) {
        const value = isTimerange
          ? modifyDate(
            parseValue,
            preConfirmValue.getFullYear(),
            preConfirmValue.getMonth(),
            preConfirmValue.getDate(),
          )
          : parseValue
        // TODO: 
        const tempFlag = getTimestamp(value, value, isTimerange ? 'normal' : null) < getTimestamp(this.picker.value && this.picker.value[0], value, isTimerange ? 'normal' : null)
        const newValue = [this.picker.value && this.picker.value[0], !tempFlag ? value : preConfirmValue]
        this.picker.value = newValue

        // picker
        // if (this.linkedRangeChosen)
        event.target.value = formatDate(newValue[1], this.format || DEFAULT_FORMATS[this.type])

        if (this.isValidValue(newValue))
          this.emitInput(newValue)
      }
    },

    handleStartChange(event) {
      const val = event.target.value
      if (this.userInput)
        this.userInput = [val, this.userInput[1]]
      else
        this.userInput = [val, null]
    },

    handleEndChange(event) {
      const val = event.target.value
      if (this.userInput)
        this.userInput = [this.userInput[0], val]
      else
        this.userInput = [null, val]
    },

    handleClickSuffixIcon(event) {
      if (this.showClose)
        this.handleClickIcon(event)
      else
        this.handleFocus()
    },

    handleClickIcon(event) {
      if (this.readonly || this.pickerDisabled)
        return
      this.valueOnOpen = this.value
      event.stopPropagation()
      this.emitInput(null)
      this.showClose = false
      if (this.picker && typeof this.picker.handleClear === 'function')
        this.picker.handleClear()
    },

    handleClose() {
      if (!this.pickerVisible)
        return
      this.pickerVisible = false
    },

    handleFieldReset(initialValue) {
      this.userInput = initialValue === '' ? null : initialValue
    },

    handleFocus(event, tableType) {
      if (!this.editable)
        this.blur()

      if (this.ranged) {
        this.tempTableType = tableType || 'min'
        if (this.picker)
          this.picker.tableType = tableType || 'min'
      }

      const type = this.type

      if (HAVE_TRIGGER_TYPES.includes(type) && !this.pickerVisible)
        this.pickerVisible = true

      this.$emit(
        'focus',
        this.formatToValue(this.calcValue),
        this.displayValue,
      )
    },

    handleKeydown(event) {
      let pickerKeydown = true
      const keyCode = event.keyCode
      // ESC
      if (keyCode === 27) {
        this.pickerVisible = false
        event.stopPropagation()
        return
      }

      // Tab
      if (keyCode === 9) {
        if (!this.ranged) {
          this.pickerVisible = this.picker.visible = false
          this.blur()
          event.stopPropagation()
        }
        else {
          // user may change focus between two input
          setTimeout(() => {
            if (!this.refInput.includes(document.activeElement)) {
              this.pickerVisible = false
              this.blur()
              event.stopPropagation()
            }
          }, 0)
        }
        return
      }

      // Enter
      if (keyCode === 13) {
        if (
          this.userInput === ''
          || this.isValidValue(this.parseString(this.displayValue))
        ) {
          this.pickerVisible = this.picker.visible = false
          this.blur()
        }
        event.stopPropagation()
        return
      }

      if (this.formatRangeSelection) {
        if (event.key === 'ArrowRight' || event.key === 'ArrowLeft' || event.key === 'ArrowUp' || event.key === 'ArrowDown') {
          const selectionInfo = event.target._selectionInfo ? JSON.parse(event.target._selectionInfo) : null
          const tempKey = event.key.toLowerCase().replace('arrow', '')
          const { selectionStart, selectionEnd } = event.target
          pickerKeydown = false
          if (selectionStart === selectionEnd)
            this.formatCurrentSelectionString(event)

          setTimeout(() => {
            this.setCurrentSelection(selectionInfo, event, tempKey)
          }, 0)
        }

        if ((keyCode < 48 || keyCode > 57) && keyCode !== 8 && !(event.ctrlKey && keyCode === 86) && !(event.ctrlKey && keyCode === 67)) {
          //    ctrl+v ctrl+c 
          event.preventDefault()
        }

        if (keyCode >= 48 && keyCode <= 57) {
          // 
          const splitSelections = event.target._splitSelections ? JSON.parse(event.target._splitSelections) : null
          if (splitSelections && splitSelections.length && event.target.value && (event.target.selectionStart === event.target.selectionEnd)) {
            const currentSplitIndex = splitSelections.findIndex(item => item.startIndex >= event.target.selectionEnd)
            const tempIndex = splitSelections[currentSplitIndex].content
              ? event.target.value.indexOf(splitSelections[currentSplitIndex].content, event.target.selectionStart)
              : event.target.value.lastIndexOf(splitSelections[currentSplitIndex].content, splitSelections[currentSplitIndex].startIndex)
            if (tempIndex === splitSelections[currentSplitIndex].startIndex)
              event.preventDefault()
          }
        }
      }

      if (this.refInput[0] === document.activeElement) {
        event.stopPropagation()
        return false
      }

      // delegate other keys to panel
      if (this.picker && this.picker.handleKeydown && pickerKeydown)
        this.picker.handleKeydown(event)
    },

    hidePicker() {
      if (this.picker) {
        let emitValue
        if (this.userInput === '') {
          emitValue = null
        }
        else if (!this.userInput && this.picker.unlink) {
          this.calcValue = this.preConfirmValue
          emitValue = this.calcValue
        }
        else {
          this.preConfirmValue = this.calcValue
          emitValue = this.preConfirmValue
        }
        this.emitInput(emitValue)
        this.pickerVisible = false
        this.picker.visible = false
        // tableType
        this.picker.tableType = null
        this.destroyPopper()
      }
    },

    showPicker() {
      if (this.$isServer)
        return
      this.setReference()
      if (!this.picker)
        this.mountPicker()

      this.$nextTick(() => {
        this.picker.value = this.parsedValue
        this.resetView()
        this.pickerVisible = true
        this.picker.visible = true
        this.updatePopper()
        this.picker.adjustSpinners && this.picker.adjustSpinners()
        this.picker.tableType = this.tempTableType
      })
    },

    mountPicker() {
      const specialUnlinkTypes = ['year', 'month', 'time']
      this.picker = new Vue(this.panel).$mount()
      if (Object.keys(this.$slots).length)
        this.picker.$slots = this.$slots

      if (Object.keys(this.$scopedSlots).length)
        this.picker.$scopedSlots = this.$scopedSlots

      this.picker.defaultValue = this.defaultValue
      this.picker.defaultTime = this.defaultTime
      this.picker.popperClass = this.popperClass
      this.popperElm = this.picker.$el
      this.picker.width = this.reference.getBoundingClientRect().width
      this.picker.showTime
        = this.type === 'datetime' || this.type === 'datetimerange'
      this.picker.type = this.type
      this.picker.selectionMode = this.selectionMode
      this.picker.unlinkPanels = this.unlinkPanels
      this.picker.arrowControl
        = this.arrowControl || this.timeArrowControl || false
      if (this.compareTimes && this.isRange)
        this.picker.compareTimesFunc = this.compareTimes

      // HUI custom
      this.picker.unlink
        = this.unlink === null
          ? !specialUnlinkTypes.includes(this.type)
          : this.unlink
      this.picker.unlinkRange = this.unlinkRange
      this.picker.linkedRangeChosen = this.linkedRangeChosen
      this.picker.isRange = this.isRange
      this.picker.startPlaceholder = this.startPlaceholder
      this.picker.endPlaceholder = this.endPlaceholder
      this.picker.displayValue = this.displayValue
      this.picker.calendarType = this.calendarType
      this.$watch('format', (format) => {
        this.picker.format = format
      })

      const updateOptions = () => {
        const options = this.pickerOptions
        if (options && options.selectableRange) {
          let ranges = options.selectableRange
          const parser = TYPE_VALUE_RESOLVER_MAP.datetimerange.parser
          const format = DEFAULT_FORMATS.timerange

          ranges = Array.isArray(ranges) ? ranges : [ranges]
          this.picker.selectableRange = ranges.map(range =>
            parser(range, format, this.rangeSeparator),
          )
        }

        for (const option in options) {
          if (
            hasOwn(options, option)
            //  time-picker 
            && option !== 'selectableRange'
          )
            this.picker[option] = options[option]
        }

        // main format must prevail over undocumented pickerOptions.format
        if (this.format)
          this.picker.format = this.format
      }
      updateOptions()
      this.unwatchPickerOptions = this.$watch(
        'pickerOptions',
        () => updateOptions(),
        {
          deep: true,
        },
      )
      this.$el.appendChild(this.picker.$el)
      this.picker.$on('dodestroy', this.doDestroy)
      this.picker.$on('pick', (date = '', visible = false, changeInput) => {
        this.calcValue = date
        if (changeInput !== undefined)
          this.changeInput = changeInput
        else
          this.changeInput = !visible

        if (this.changeInput)
          this.emitChange(date)

        if (!visible || this.changeInput)
          this.emitInput(date)

        this.picker.visible = visible
        this.pickerVisible = visible
      })
      this.picker.$on('select-range', (start, end, pos) => {
        if (this.refInput.length === 0 || !this.pickerVisible)
          return
        if (!pos || pos === 'min') {
          this.refInput[0].setSelectionRange(start, end)
          this.refInput[0].focus()
        }
        else if (pos === 'max') {
          this.refInput[1].setSelectionRange(start, end)
          this.refInput[1].focus()
        }
      })
    },

    unmountPicker() {
      if (this.picker) {
        this.picker.$destroy()
        this.picker.$off()
        this.doDestroy()
        if (typeof this.unwatchPickerOptions === 'function')
          this.unwatchPickerOptions()

        this.picker.$el.parentNode.removeChild(this.picker.$el)
      }
    },

    emitChange(val, forceEmit, doNotValid) {
      // determine user real change only
      if (
        (!valueEquals(val, this.preConfirmValue)
          && !valueEquals(val, this.valueOnOpen))
        || forceEmit
      ) {
        this.$emit('change', this.formatToValue(val), this.displayValue)
        this.valueOnOpen = val
        if (this.validateEvent && !doNotValid)
          this.dispatch('ElFormItem', 'el.form.change', val)
      }
    },

    emitInput(val) {
      this.userInput = null
      if (!valueEquals(this.preConfirmValue, val)) {
        this.calcValue = val
        this.preConfirmValue = val
      }
    },

    isValidValue(value) {
      if (!this.picker)
        this.mountPicker()

      if (this.picker.isValidValue)
        return value && this.picker.isValidValue(value)
      else
        return true
    },

    resetView() {
      this.picker.resetView && this.picker.resetView()
    },
  },
}
</script>

<template>
  <el-input
    v-if="!ranged" ref="reference" v-clickoutside="handleClose" :class="'el-date-editor--' + type"
    :readonly="!editable || readonly || type === 'dates' || type === 'week'" :disabled="pickerDisabled" :size="pickerSize" :name="name"
    :placeholder="placeholder" :value="displayValue" :title="displayValue" :validate-event="false" v-bind="firstInputId"
    class="el-date-editor" maxlength="20" @mouseenter.native="handleMouseEnter" @mouseleave.native="showClose = false"
    @focus="handleFocus" @keydown.native="handleKeydown" @change.native="handleChange" @input.native="handleSetInput"
    @click.native="handleInputSelection"
    @beforeinput.native="handleBeforeInput"
  >
    <i
      slot="suffix" :class="[showClose ? clearIcon : triggerClass]" class="el-input__icon el-date-editor__icon"
      @click="handleClickSuffixIcon"
    />
  </el-input>
  <div
    v-else ref="reference" v-clickoutside="handleClose" :class="[
      'el-date-editor--' + type,
      pickerSize ? `el-date-editor--${pickerSize}` : '',
      pickerDisabled ? 'is-disabled' : '',
      pickerVisible ? 'is-active' : ''
    ]" :title="displayValue === '' ? '' : `${displayValue && displayValue[0]} ${rangeSeparator} ${displayValue && displayValue[1]}`" class="el-date-editor el-range-editor el-input__inner" @click="handleFocus" @keydown="handleKeydown"
    @mouseenter="handleMouseEnter" @mouseleave="showClose = false"
  >
    <input
      :placeholder="startPlaceholder" :value="displayValue && displayValue[0]" :disabled="pickerDisabled"
      v-bind="firstInputId" :readonly="!editable || readonly" :name="name && name[0]" autocomplete="off"
      class="el-input__inner el-range-input" data-range-input-min @input="handleSetInput" @change="handleStartChange" @focus.stop="handleFocus($event,'min')"
      @click.stop="handleInputSelection"
      @keydown.stop="handleKeydown"
      @beforeinput.stop="handleBeforeInput"
    >
    <slot name="range-separator">
      <span class="el-range-separator">{{ rangeSeparator }}</span>
    </slot>
    <input
      :placeholder="endPlaceholder" :value="displayValue && displayValue[1]" :disabled="pickerDisabled"
      v-bind="secondInputId" :readonly="!editable || readonly" :name="name && name[1]" autocomplete="off"
      class="el-input__inner el-range-input" data-range-input-max @input="handleSetInput" @change="handleEndChange" @focus.stop="handleFocus($event,'max')"
      @click.stop="handleInputSelection"
      @keydown.stop="handleKeydown"
      @beforeinput.stop="handleBeforeInput"
    >
    <span class="el-input__suffix">
      <span class="el-input__suffix-inner">
        <i
          :class="[showClose ? clearIcon : triggerClass]" class="el-input__icon el-range__icon el-date-editor__icon"
          @click="handleClickSuffixIcon"
        />
      </span>
    </span>
    <div class="el-date-picker-active-bar" :style="activeBarStyle" />
  </div>
</template>
