<script>
import { Tooltip as ElTooltip } from '@hui/components/tooltip'
import {
  addResizeListener,
  emitter,
  removeResizeListener
} from '@hui/utils'
import Menu from './menu-mixin'

export default {
  name: 'ElMenuItem',

  componentName: 'ElMenuItem',

  components: {
    ElTooltip
  },

  mixins: [Menu, emitter],
  props: {
    index: {
      type: String,
      required: true
    },
    route: {
      type: Object,
      required: false,
      default: null
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    },
    icon: {
      type: String,
      default: null
    },
    suffix: {
      type: String,
      default: null
    },
    description: {
      type: String,
      default: null
    },
    divided: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      isIe: false,
      textEllipsis: false
    }
  },
  computed: {
    active() {
      return this.index === this.rootMenu.activeIndex
    }
  },
  created() {
    this.parentMenu.addItem(this)
    this.rootMenu.addItem(this)
  },
  mounted() {
    addResizeListener(this.$el, this.setTextEllipsis)
    if (!!window.ActiveXObject || 'ActiveXObject' in window)
      this.isIe = true
  },
  beforeDestroy() {
    this.parentMenu.removeItem(this)
    this.rootMenu.removeItem(this)
    removeResizeListener(this.$el, this.setTextEllipsis)
  },
  methods: {
    handleClick() {
      if (!this.disabled) {
        this.dispatch('ElMenu', 'item-click', this)
        this.$emit('click', this)
      }
    },
    setTextEllipsis() {
      const $text = this.$el.querySelector('.el-menu-item--text')
      if (!$text || !$text.parentNode)
        return
      const gutter = 60 // 
      this.textEllipsis
        = $text.offsetWidth > $text.parentNode.offsetWidth - gutter
    }
  }
}
</script>
<template>
  <div>
    <li
      :class="{
        'is-active': active,
        'is-disabled': disabled,
      }"
      :title="
        rootMenu.mode === 'vertical' &&
          !rootMenu.collapse &&
          !disabled &&
          (isIe || textEllipsis) &&
          $slots.default &&
          $slots.default[0].text
      "
      class="el-menu-item"
      @click="handleClick"
    >
      <el-tooltip
        v-if="parentMenu === rootMenu && rootMenu.collapse"
        effect="dark"
        placement="right"
        popper-class="tooltip-for-menu-item"
      >
        <div slot="content">
          {{ $slots.default[0].text }}
        </div>
        <div class="el-menu-item__collapse">
          <i v-if="$slots.icon" class="el-menu-icon is-slot">
            <slot name="icon" />
          </i>
          <i v-else-if="icon" :class="icon" class="el-menu-icon" />
          <span class="el-menu-item--text">
            <slot />
          </span>
        </div>
      </el-tooltip>
      <template v-else>
        <div class="el-menu-item--content" :style="{paddingLeft:paddingStyle}">
          <i v-if="$slots.icon" class="el-menu-icon is-slot">
            <slot name="icon" />
          </i>
          <i v-else-if="icon" :class="icon" class="el-menu-icon" />
          <i v-else class="el-menu-no-icon" />
          <span class="el-menu-item--text">
            <slot />
          </span>
          <span class="el-menu-item--suffix">
            <slot v-if="$slots.suffix" name="suffix" />
            <span v-else-if="suffix">{{ suffix }}</span>
          </span>
        </div>
        <div v-if="$slots.description || description" class="el-menu-item--description" :style="{paddingLeft:paddingStyle}">
          <slot v-if="$slots.description" name="description" />
          <div v-else class="el-menu-item--description-content">
            {{ description }}
          </div>
        </div>
      </template>
    </li>
    <div v-if="divided" class="el-menu--divided" />
  </div>
</template>
