<script>
import { Button } from '@hui/components/button'
import { emitter } from '@hui/utils'
import { t } from '@hui/locale'
import TransferPanel from './transfer-panel.vue'

export default {
  name: 'ElTransfer',

  components: {
    TransferPanel,
    ElButton: Button
  },

  mixins: [emitter],

  props: {
    data: {
      type: Array,
      default() {
        return []
      }
    },
    titles: {
      type: Array,
      default() {
        return []
      }
    },
    buttonTexts: {
      type: Array,
      default() {
        return []
      }
    },
    filterPlaceholder: {
      type: String,
      default: ''
    },
    filterMethod: {
      type: Function,
      default: null
    },
    leftDefaultChecked: {
      type: Array,
      default() {
        return []
      }
    },
    rightDefaultChecked: {
      type: Array,
      default() {
        return []
      }
    },
    leftPanelWidth: {
      type: Number,
      default: undefined,
    },
    rightPanelWidth: {
      type: Number,
      default: undefined,
    },
    panelContentHeight: {
      type: Number,
      default: undefined,
    },
    leftShowAllCheckedBox: {
      type: Boolean,
      default: true
    },
    rightShowAllCheckedBox: {
      type: Boolean,
      default: true
    },
    renderContent: {
      type: Function,
      default: null
    },
    value: {
      type: Array,
      default() {
        return []
      }
    },
    footerFormat: {
      type: [Object, Boolean],
      default() {
        return {}
      }
    },
    filterable: {
      type: Boolean,
      default: null
    },
    props: {
      type: Object,
      default() {
        return {
          label: 'label',
          key: 'key',
          disabled: 'disabled'
        }
      }
    },
    searchPlaceholder: {
      type: String,
      default: ''
    },
    searchMethod: {
      type: Function,
      default: null
    },
    beforeAddToRight: {
      type: Function,
      default: () => new Promise(resolve => resolve())
    },
    beforeAddToLeft: {
      type: Function,
      default: () => new Promise(resolve => resolve())
    },
    simple: {
      type: Boolean,
      default: false
    },
    retainSelectedItems: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      leftChecked: [],
      rightChecked: [],
      t,
    }
  },

  computed: {
    sourceData() {
      const cloneData = JSON.parse(JSON.stringify(this.data))
      return this.retainSelectedItems
        ? cloneData.map((item) => {
          item.__checked = this.value.includes(item[this.props.key])
          return item
        })
        : this.data.filter(
          item => !this.value.includes(item[this.props.key])
        )
    },

    targetData() {
      return this.data.filter(
        item => this.value.includes(item[this.props.key])
      )
    },

  },

  watch: {
    value(val) {
      this.dispatch('ElFormItem', 'el.form.change', val)
    }
  },

  methods: {
    onSourceCheckedChange(val) {
      this.leftChecked = val
    },

    onTargetCheckedChange(val) {
      this.rightChecked = val
    },

    addToLeft() {
      this.beforeAddToLeft()
        .then(() => {
          const currentValue = this.value.slice()
          this.rightChecked.forEach((item) => {
            const index = currentValue.indexOf(item)
            if (index > -1)
              currentValue.splice(index, 1)
          })
          this.$emit('input', currentValue)
          this.$emit('change', currentValue, 'left', this.rightChecked)
          this.$nextTick(() => {
            if (this.retainSelectedItems) {
              this.handleCheckedKeysChange([], 'right')
              this.handleCheckedKeysChange(currentValue, 'left')
            }
          })
        })
        .catch(() => {})
    },

    addToRight() {
      this.beforeAddToRight()
        .then(() => {
          let currentValue = this.value.slice()
          this.leftChecked.forEach((item) => {
            if (!this.value.includes(item))
              currentValue = currentValue.concat(item)
          })
          this.$emit('input', currentValue)
          this.$emit('change', currentValue, 'right', this.leftChecked)
          this.$nextTick(() => {
            if (this.retainSelectedItems)
              this.handleCheckedKeysChange(currentValue, 'left')
          })
        })
        .catch(() => {})
    },

    clearQuery(which) {
      if (which === 'left') {
        this.$refs.leftPanel.query = ''
      }
      else if (which === 'right') {
        this.$refs.rightPanel.query = ''
      }
      else if (which === undefined) {
        this.$refs.leftPanel.query = ''
        this.$refs.rightPanel.query = ''
      }
    },

    handleCheckedKeysChange(checkedKeys, panelName) {
      if (this.retainSelectedItems && panelName === 'left')
        this[`${panelName}Checked`] = checkedKeys.filter(it => !this.value.includes(it))
      else
        this[`${panelName}Checked`] = checkedKeys

      this.$refs[`${panelName}Panel`].cloneDefaultChecked = checkedKeys
    },

    handleCheckEvent(checkedKeys, panelName) {
      this.handleCheckedKeysChange(checkedKeys, panelName)

      if (this.simple)
        this.handleSimplePanelItemClick(checkedKeys, panelName)
    },

    handleSimplePanelItemClick(checkedKeys, panelAt) {
      if (this.simple) {
        if (panelAt === 'left') {
          this.leftChecked = checkedKeys
          this.addToRight()
        }
        else if (panelAt === 'right') {
          this.rightChecked = checkedKeys
          this.addToLeft()
        }
      }
    },
  }
}
</script>

<template>
  <div class="el-transfer" :class="[simple?'is-simple':'']">
    <transfer-panel
      ref="leftPanel"
      v-bind="$props"
      :data="sourceData"
      :title="titles[0] || t('el.transfer.titles.0')"
      :default-checked="leftDefaultChecked"
      :show-all-checked-box="leftShowAllCheckedBox"
      panel-at="left"
      :retain-selected-items="retainSelectedItems"
      :panel-content-height="panelContentHeight"
      :style="[leftPanelWidth || rightPanelWidth ? {width: (leftPanelWidth || rightPanelWidth) + 'px'}: {}]"
      @checked-change="onSourceCheckedChange"
      @simple-item-click="handleSimplePanelItemClick"
    >
      <template slot="left-panel-content">
        <slot name="left-panel" :data="sourceData" :check-event="(params)=>handleCheckEvent(params,'left')" :checked-keys="[...new Set(retainSelectedItems?[...value,...leftChecked]:leftChecked)]" />
      </template>
      <slot name="left-footer" />
    </transfer-panel>
    <div v-if="!simple" class="el-transfer__buttons">
      <el-button
        :disabled="leftChecked.length === 0"
        class="el-transfer__button"
        :class="[buttonTexts[1] !== undefined?'':'is-icon']"
        @click.native="addToRight"
      >
        <span v-if="buttonTexts[1] !== undefined">
          {{ buttonTexts[1] }}
        </span>
        <i class="h-icon-angle_right" />
      </el-button>
      <el-button
        :disabled="rightChecked.length === 0"
        class="el-transfer__button"
        :class="[buttonTexts[0] !== undefined?'':'is-icon']"
        @click.native="addToLeft"
      >
        <i class="h-icon-angle_left" />
        <span v-if="buttonTexts[0] !== undefined">
          {{ buttonTexts[0] }}
        </span>
      </el-button>
    </div>
    <transfer-panel
      ref="rightPanel"
      v-bind="$props"
      :data="targetData"
      :title="titles[1] || t('el.transfer.titles.1')"
      :default-checked="rightDefaultChecked"
      :show-all-checked-box="rightShowAllCheckedBox"
      panel-at="right"
      :panel-content-height="panelContentHeight"
      :style="[rightPanelWidth || leftPanelWidth ? {width: (rightPanelWidth || leftPanelWidth) + 'px'}: {}]"
      @checked-change="onTargetCheckedChange"
      @simple-item-click="handleSimplePanelItemClick"
    >
      <template slot="right-panel-content">
        <slot name="right-panel" :data="targetData" :check-event="(params)=>handleCheckEvent(params,'right')" :checked-keys="rightChecked" />
      </template>
      <slot name="right-footer" />
    </transfer-panel>
  </div>
</template>
