<script>
import { emitter } from '@hui/utils'

export default {
  name: 'ElOptionGroup',
  mixins: [emitter],

  componentName: 'ElOptionGroup',

  props: {
    label: {
      type: String,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      visible: true,
    }
  },

  watch: {
    disabled(val) {
      this.broadcast('ElOption', 'handleGroupDisabled', val)
    },
  },

  created() {
    this.$on('queryChange', this.queryChange)
  },

  mounted() {
    if (this.disabled)
      this.broadcast('ElOption', 'handleGroupDisabled', this.disabled)
  },

  methods: {
    queryChange() {
      this.visible
        = this.$children
        && Array.isArray(this.$children)
        && this.$children.some(option => option.visible === true)
    },
  },
}
</script>

<template>
  <div class="el-select-group">
    <ul class="el-select-group__wrap">
      <li v-show="visible" class="el-select-group__title">
        {{ label }}
      </li>
      <li>
        <ul class="el-select-group">
          <slot />
        </ul>
      </li>
    </ul>
  </div>
</template>
