<script>
const STROKE_WIDTH_SMALL = 8

export default {
  name: 'ElProgress',
  props: {
    type: {
      type: String,
      default: 'line',
      validator: val => ['line', 'circle'].includes(val)
    },
    percentage: {
      type: Number,
      default: 0,
      validator: val => val >= 0 && val <= 100
    },
    status: {
      type: String,
      default: ''
    },
    strokeWidth: {
      type: Number,
      default() {
        return this.type === 'line' ? 12 : 8
      }
    },
    textInside: {
      type: Boolean,
      default: false
    },
    width: {
      type: Number,
      default: 160
    },
    showText: {
      type: Boolean,
      default: true
    },
    verticalAlign: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    color: {
      type: String,
      default: ''
    },
    background: {
      type: String,
      default: ''
    },
    statusText: {
      type: String,
      default: ''
    },
    strokeLinecap: {
      type: String,
      default: 'square'
    },
    dynamic: {
      type: Boolean,
      default: false
    },
    // , modify by yangzhini 2019-04-15
    duration: {
      type: Number,
      default: 1.5,
      validator: val => val >= 0
    }
  },
  computed: {
    barStyle() {
      const style = {}
      style.backgroundColor = this.color
      if (this.background)
        style.background = this.background
      // , , percentage100%
      style.width = this.internalDynamic ? '100%' : `${this.percentage}%`
      style.borderRadius = this.strokeLinecap === 'square' ? '0px' : '100px'
      return style
    },
    relativeStrokeWidth() {
      // , strokeWidth
      return ((this.strokeWidth / this.width) * 100).toFixed(1)
    },
    trackPath() {
      const radius = parseInt(50 - parseFloat(this.relativeStrokeWidth) / 2, 10)

      return `M 50 50 m 0 -${radius} a ${radius} ${radius} 0 1 1 0 ${radius
        * 2} a ${radius} ${radius} 0 1 1 0 -${radius * 2}`
    },
    perimeter() {
      const radius = 50 - parseFloat(this.relativeStrokeWidth) / 2
      return 2 * Math.PI * radius
    },
    circlePathStyle() {
      const perimeter = this.perimeter
      return {
        strokeDasharray: `${perimeter}px,${perimeter}px`,
        strokeDashoffset: `${(1 - this.percentage / 100) * perimeter}px`,
        transition: 'stroke-dashoffset 0.6s ease 0s, stroke 0.6s ease',
        strokeLinecap:
          this.percentage === 0
            ? ''
            : this.strokeLinecap === 'square'
              ? 'butt'
              : 'round'
      }
    },
    stroke() {
      let ret
      switch (this.status) {
        case 'success':
          ret = this.color || '#1DB84C' // $--color-success
          break
        case 'exception':
          ret = this.color || '#E6202A' // $--color-danger
          break
        default:
          ret = this.color || '#176CFF' // $--color-info
      }
      return ret
    },
    iconClass() {
      if (this.type === 'line') {
        return this.status === 'success'
          ? 'h-icon-tip_right'
          : 'h-icon-tip_error'
      }
      else {
        return this.status === 'success' ? 'h-icon-done' : 'h-icon-close'
      }
    },
    progressTextSize() {
      return this.type === 'line'
        ? 8 + this.internalStrokeWidth * 0.5
        : this.width * 0.111111
    },
    iconSize() {
      return this.type === 'line'
        ? 12 + this.internalStrokeWidth * 0.5
        : this.width * 0.2 + 16
    },
    // ''
    internalSmall() {
      return this.small && this.type === 'line' && !this.textInside
    },
    // , 8px, strokeWidth
    internalStrokeWidth() {
      return this.internalSmall ? STROKE_WIDTH_SMALL : this.strokeWidth
    },
    // text
    internalShowText() {
      return this.internalDynamic ? false : this.showText
    },
    // 
    internalDynamic() {
      return this.dynamic && this.type === 'line'
    }
  }
}
</script>
<template>
  <div
    :class="[
      'el-progress--' + type,
      status ? 'is-' + status : '',
      verticalAlign ? 'is-text-vertical' : '',
      {
        'el-progress--without-text': !internalShowText,
        'el-progress--text-inside': textInside,
        'el-progress--small': internalSmall
      }
    ]"
    class="el-progress"
  >
    <div v-if="type === 'line'" class="el-progress-bar">
      <div
        :style="{
          height: internalStrokeWidth + 'px',
          'border-radius': strokeLinecap === 'square' ? '0px' : '100px'
        }"
        class="el-progress-bar__outer"
      >
        <div :style="barStyle" class="el-progress-bar__inner">
          <div
            v-if="internalShowText && textInside"
            class="el-progress-bar__innerText"
          >
            {{ percentage }}%
          </div>
          <div
            v-if="internalDynamic"
            :style="`animation-duration:${duration}s;`"
            class="el-progress-bar__dynamic-block"
          />
        </div>
      </div>
    </div>
    <div
      v-else
      :style="{ height: width + 'px', width: width + 'px' }"
      class="el-progress-circle"
    >
      <svg viewBox="0 0 100 100">
        <path
          :d="trackPath"
          :stroke-width="relativeStrokeWidth"
          class="el-progress-circle__track"
          stroke="#e0e0e0"
          fill="none"
        />
        <path
          :d="trackPath"
          :stroke="stroke"
          :stroke-width="relativeStrokeWidth"
          :style="circlePathStyle"
          class="el-progress-circle__path"
          fill="none"
        />
      </svg>
    </div>
    <div
      v-if="internalShowText && !textInside"
      :title="!status ? `${percentage}%` : statusText"
      :style="{ fontSize: progressTextSize + 'px' }"
      class="el-progress__text"
    >
      <slot name="text">
        <template v-if="!status">
          {{ percentage }}%
        </template>
        <template v-else>
          <i :style="{ fontSize: iconSize + 'px' }" :class="iconClass" />
          <span v-if="type === 'line'" class="el-progress__status-text">
            {{ statusText }}
          </span>
        </template>
      </slot>
    </div>
  </div>
</template>
