/* istanbul ignore next */

import Vue from 'vue'

import { hasOwn } from './util'
const isServer = Vue.prototype.$isServer
const SPECIAL_CHARS_REGEXP = /([:\-_]+(.))/g
const MOZ_HACK_REGEXP = /^moz([A-Z])/
const ieVersion = isServer ? 0 : Number(document.documentMode)

/* istanbul ignore next */
const trim = function(string) {
  return (string || '').replace(/^[\s\uFEFF]+|[\s\uFEFF]+$/g, '')
}
/* istanbul ignore next */
const camelCase = function(name) {
  return name
    .replace(SPECIAL_CHARS_REGEXP, (_, separator, letter, offset) => {
      return offset ? letter.toUpperCase() : letter
    })
    .replace(MOZ_HACK_REGEXP, 'Moz$1')
}

/* istanbul ignore next */
export const on = (function() {
  if (!isServer && document.addEventListener) {
    return function(element, event, handler) {
      if (element && event && handler)
        element.addEventListener(event, handler, false)
    }
  }
  else {
    return function(element, event, handler) {
      if (element && event && handler)
        element.attachEvent(`on${event}`, handler)
    }
  }
})()

/* istanbul ignore next */
export const off = (function() {
  if (!isServer && document.removeEventListener) {
    return function(element, event, handler) {
      if (element && event)
        element.removeEventListener(event, handler, false)
    }
  }
  else {
    return function(element, event, handler) {
      if (element && event)
        element.detachEvent(`on${event}`, handler)
    }
  }
})()

/* istanbul ignore next */
export const once = function(el, event, fn) {
  var listener = function() {
    if (fn)
      fn.apply(this, arguments)

    off(el, event, listener)
  }
  on(el, event, listener)
}

/* istanbul ignore next */
export function hasClass(el, cls) {
  if (!el || !cls)
    return false
  if (cls.includes(' '))
    throw new Error('className should not contain space.')

  if (el.classList)
    return el.classList.contains(cls)
  else
    return (` ${el.className} `).includes(` ${cls} `)
}

/* istanbul ignore next */
export function addClass(el, cls) {
  if (!el)
    return
  let curClass = el.className
  const classes = (cls || '').split(' ')

  for (let i = 0, j = classes.length; i < j; i++) {
    const clsName = classes[i]
    if (!clsName)
      continue

    if (el.classList) {
      el.classList.add(clsName)
    }
    else {
      if (!hasClass(el, clsName))
        curClass += ` ${clsName}`
    }
  }
  if (!el.classList)
    el.className = curClass
}

/* istanbul ignore next */
export function removeClass(el, cls) {
  if (!el || !cls)
    return
  const classes = cls.split(' ')
  let curClass = ` ${el.className} `

  for (let i = 0, j = classes.length; i < j; i++) {
    const clsName = classes[i]
    if (!clsName)
      continue

    if (el.classList) {
      el.classList.remove(clsName)
    }
    else {
      if (hasClass(el, clsName))
        curClass = curClass.replace(` ${clsName} `, ' ')
    }
  }
  if (!el.classList)
    el.className = trim(curClass)
}

/* istanbul ignore next */
export const getStyle
  = ieVersion < 9
    ? function(element, styleName) {
      if (isServer)
        return
      if (!element || !styleName)
        return null
      styleName = camelCase(styleName)
      if (styleName === 'float')
        styleName = 'styleFloat'

      try {
        switch (styleName) {
          case 'opacity':
            try {
              return element.filters.item('alpha').opacity / 100
            }
            catch (e) {
              return 1.0
            }
          default:
            return element.style[styleName] || element.currentStyle
              ? element.currentStyle[styleName]
              : null
        }
      }
      catch (e) {
        return element.style[styleName]
      }
    }
    : function(element, styleName) {
      if (isServer)
        return
      if (!element || !styleName)
        return null
      styleName = camelCase(styleName)
      if (styleName === 'float')
        styleName = 'cssFloat'

      try {
        const computed = document.defaultView.getComputedStyle(element, '')
        return element.style[styleName] || computed
          ? computed[styleName]
          : null
      }
      catch (e) {
        return element.style[styleName]
      }
    }

/* istanbul ignore next */
export function setStyle(element, styleName, value) {
  if (!element || !styleName)
    return

  if (typeof styleName === 'object') {
    for (const prop in styleName) {
      if (hasOwn(styleName, prop))
        setStyle(element, prop, styleName[prop])
    }
  }
  else {
    styleName = camelCase(styleName)
    if (styleName === 'opacity' && ieVersion < 9) {
      element.style.filter = isNaN(value)
        ? ''
        : `alpha(opacity=${value * 100})`
    }
    else {
      element.style[styleName] = value
    }
  }
}

/**
 * 
 * @param {} element
 * @param {} selector
 */
export function closest(element, selector) {
  if (!element || !selector)
    return

  let targetNode = null
  const traverse = function(element) {
    if (element === document)
      return

    const nodes = element.parentNode.querySelectorAll(selector)
    if (nodes && nodes.length) {
      for (let i = 0; i < nodes.length; i++) {
        if (nodes[i] === element) {
          targetNode = element
          return
        }
      }
    }
    traverse(element.parentNode)
  }
  traverse(element)

  return targetNode
}

/**
 * @desc 
 * @author chenguanbin
 * @param {Boolean} top Y
 */
export function getScroll(top) {
  let ret = window[`page${top ? 'Y' : 'X'}Offset`]
  const method = `scroll${top ? 'Top' : 'Left'}`
  if (typeof ret !== 'number') {
    const d = window.document
    // ie6,7,8 standard mode
    ret = d.documentElement[method]
    if (typeof ret !== 'number') {
      // quirks mode
      ret = d.body[method]
    }
  }
  return ret
}

/**
 * @desc  X Y 
 * @author chenguanbin
 * @param {Document} el 
 * @param {Document} container 
 */
export function getOffset(el, container) {
  container = container || document.body
  const elRect = el.getBoundingClientRect()
  const containerRect = container.getBoundingClientRect()
  const clientTop = el.clientTop || container.clientTop || 0
  const clientLeft = el.clientLeft || container.clientLeft || 0
  let top, left

  if (container === document.body) {
    top = getScroll(true)
    left = getScroll()
  }
  else {
    top = container.scrollTop - containerRect.top
    left = container.scrollLeft - containerRect.left
  }

  return {
    top: elRect.top + top - clientTop,
    left: elRect.left + left - clientLeft,
    right: elRect.right + left - clientLeft,
    bottom: elRect.bottom + top - clientTop,
  }
}
