export default {
  computed: {
    indexPath() {
      const path = [this.index]
      let parent = this.$parent
      while (parent.$options.componentName !== 'ElMenu') {
        if (parent.index)
          path.unshift(parent.index)

        parent = parent.$parent
      }
      return path
    },
    rootMenu() {
      let parent = this.$parent
      while (parent && parent.$options.componentName !== 'ElMenu')
        parent = parent.$parent

      return parent
    },
    parentMenu() {
      let parent = this.$parent
      while (
        parent
        && !['ElMenu', 'ElSubmenu'].includes(parent.$options.componentName)
      )
        parent = parent.$parent

      return parent
    },
    paddingStyle() {
      if (this.rootMenu.mode !== 'vertical')
        return {}

      let gutter = 12
      let parent = this.$parent

      if (this.rootMenu.collapse || !parent) {
        return {}
      }
      else {
        if (parent.$options.componentName === 'ElSubmenu') {
          gutter = 32 // 
          if (parent.$parent && parent.$parent.$options.componentName === 'ElMenu') {
            if (parent.icon || parent.$slots.icon)
              gutter -= 4
          }
          parent = parent.$parent
          while (parent && parent.$options.componentName !== 'ElMenu') {
            //  16px
            if (parent.$options.componentName === 'ElSubmenu')
              gutter += 16

            if (parent.$parent && parent.$parent.$options.componentName === 'ElMenu') {
              if (parent.icon || parent.$slots.icon)
                gutter -= 4
            }

            parent = parent.$parent
          }
        }
        else {
          gutter = this.icon || this.$slots.icon ? 12 : 24 // 
        }
        return `${gutter}px`
      }
    }
  }
}
