<script>
import { closest } from '@hui/utils'
import { getCollapseHeight, getOffset } from './util'

export default {
  name: 'ElNavCollapse',
  componentName: 'ElNavCollapse',
  props: {
    rootNav: { type: Object, default: null }
  },
  data() {
    return {
      open: false
    }
  },
  mounted() {},
  methods: {
    handleMouseenter(event) {
      this.$emit('mouse-enter', event)
    },
    handleMouseleave(event) {
      const toElement = event.toElement || event.relatedTarget
      // 
      if (closest(toElement, '.el-nav-collapse-wrap') === null)
        this.$emit('leave-collapse', event)
    },
    loadCollapseNav() {
      let isExistNode = false
      const nodes = Array.prototype.slice.call(document.body.childNodes)
      // body
      if (nodes && nodes.length) {
        nodes.forEach((node) => {
          if (node === this.$el)
            isExistNode = true
        })
      }
      // body
      if (!isExistNode)
        document.body.appendChild(this.$el)

      // 
      const parent = this.$parent.$el
      const parentOffset = getOffset(parent) // 
      const parentClientTop = parent.getBoundingClientRect().top // 

      const collapseHeight = getCollapseHeight(this.$el.childNodes[0]) // 
      const clientHeight = document.body.clientHeight // 

      const wrapScrollTop
        = document.getElementsByTagName('html')[0].scrollTop
        || document.body.scrollTop // html/body(HUIdemo)

      let gutter = 0 // top

      // 
      if (parentClientTop + collapseHeight >= clientHeight) {
        gutter
          = parentClientTop + collapseHeight - clientHeight + wrapScrollTop + 2
      }

      //  top/left 
      this.$el.style.top = `${parentOffset.top - gutter}px`
      this.$el.style.left = `${parentOffset.left + parent.offsetWidth}px`

      setTimeout(() => {
        this.rootNav.$emit('show-collapse', this.$el)
      }, 300)

      return this.$el
    }
  }
}
</script>

<template>
  <div
    class="el-nav-collapse-wrap"
    style="transition: all .2s;
"
    @mouseenter="handleMouseenter"
    @mouseleave="handleMouseleave"
  >
    <ul class="el-nav">
      <slot />
    </ul>
  </div>
</template>
