<script>
export default {
  name: 'ElCheckableTag',
  model: {
    prop: 'checked',
  },
  props: {
    text: {
      type: String,
      default: null,
    },
    closable: {
      type: Boolean,
      default: null,
    },
    type: {
      type: String,
      default: null,
    },
    hit: {
      type: Boolean,
      default: null,
    },
    disableTransitions: {
      type: Boolean,
      default: null,
    },
    color: {
      type: String,
      default: null,
    },
    size: {
      type: String,
      default: '',
    },
    maxWidth: {
      type: String,
      default: '200px',
    },
    title: {
      type: String,
      default: '',
    },
    round: {
      type: Boolean,
      default: false,
    },
    variant: {
      type: String,
      default: 'secondary', // primary/secondary/tertiary
    },
    flood: {
      type: String,
      default: 'gray',
    },
    checkable: {
      type: Boolean,
      default: true,
    },
    checked: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {
      type: [String, Number, Boolean],
      default: undefined,
    },
    label: {
      type: [String, Number, Boolean],
      default: undefined,
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    uncheckedProps: {
      type: Object,
      default: () => {
        return {
          type: 'facetness', // facetness outline outline-none
        }
      }
    },
    checkedProps: {
      type: Object,
      default: () => {
        return {
          type: 'facetness',
        }
      }
    }
  },
  data() {
    return {
      selfModel: false,
    }
  },
  computed: {
    isGroup() {
      let parent = this.$parent
      while (parent) {
        if (parent.$options.componentName !== 'ElCheckableTagGroup') {
          parent = parent.$parent
        }
        else {
          // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          this._checkableTagGroup = parent
          return true
        }
      }
      return false
    },

    store() {
      return this._checkableTagGroup ? this._checkableTagGroup.value : this.value
    },

    model: {
      get() {
        return this.isGroup
          ? this.store
          : this.checked !== undefined
            ? this.checked
            : this.selfModel
      },

      set(val) {
        if (this.isGroup) {
          this.dispatch('ElCheckableTagGroup', 'input', [val])
        }
        else {
          this.$emit('input', val)
          this.selfModel = val
        }
      },
    },

    isChecked() {
      if ({}.toString.call(this.model) === '[object Boolean]')
        return this.model
      else if (Array.isArray(this.model))
        return this.model.includes(this.value)
      else if (this.model !== null && this.model !== undefined)
        return this.model === this.trueLabel

      return false
    },
  },
  created() {
    this.checked && this.addToStore()
  },
  methods: {
    addToStore() {
      if (Array.isArray(this.model) && !this.model.includes(this.value))
        this.model.push(this.value)
      else
        this.model = this.trueLabel || true
    },
    handleClose(event) {
      this.$emit('close', event)
    },
    handleClick(event) {
      if (this.disabled)
        return

      const tempModel = this.model

      if (this.isGroup) {
        this._checkableTagGroup.toggleChecked({
          checked: !this.isChecked,
          value: this.value
        })
      }
      else {
        this.$emit('input', !tempModel)
        this.$emit('click', !tempModel)
      }
    },
  },
}
</script>
<template>
  <transition :name="disableTransitions ? '' : 'el-zoom-in-center'">
    <span
      :class="[
        type ? 'el-tag--' + type : '',
        size ? `el-tag--${size}` : '',
        maxWidth ? `el-tag--maxwidth` : '',
        { 'is-hit': hit },
        color ? 'el-tag--custom--color' : '',
        closable ? 'el-tag--closable' : '',
        round ? 'is-round' : '',
        checkable ? 'is-checkable' : '',
        {'is-checked' : isChecked},
        disabled ? 'is-disabled' : '',
        !isChecked && uncheckedProps.type ? `el-tag--${uncheckedProps.type}` : '',
        isChecked && checkedProps.type ? `is-checked--${checkedProps.type}` : ''
      ]"
      :style="[{ backgroundColor: color }]"
      :title="title"
      class="el-tag"
      :data-variant="variant"
      :data-flood="flood"
      @click="handleClick"
    >
      <span
        v-if="$slots.default || label"
        :class="[maxWidth ? `el-tag--maxwidth` : '']"
        :style="[
          maxWidth ? { 'max-width': maxWidth, display: 'inline-block' } : '',
        ]"
      >
        <slot />
        <template v-if="!$slots.default && showLabel">
          {{ label }}
        </template>
      </span>
      <i
        v-if="closable"
        class="el-tag__close h-icon-close"
        @click="handleClose"
      />
    </span>
  </transition>
</template>
