<script>
export default {
  name: 'ElLoadIcon',
  props: {
    // 16 or 84
    size: { type: Number, default: 16 },
  },
  computed: {
    stroke() {
      // return this.size === 16 ? 3 : 8
      return Math.round(this.size / 16) + 2
    },
    p() {
      return this.size / 2
    },
    r() {
      return this.p - this.stroke / 2
    },
    perimeter() {
      return 2 * Math.PI * this.r
    },
    dasharray() {
      return `${Math.round(this.perimeter * 0.75)} ${Math.round(this.perimeter)}`
    },
  },
}
</script>

<template>
  <svg :width="size" :height="size" class="el-icon-loading">
    <circle
      :cx="p"
      :cy="p"
      :r="r"
      fill="none"
      :stroke-width="stroke"
      stroke="currentColor"
      :stroke-dasharray="`${dasharray}`"
    >
      <animateTransform
        attributeName="transform"
        attributeType="XML"
        type="rotate"
        :from="`0 ${p} ${p}`"
        :to="`360 ${p} ${p}`"
        dur="1s"
        repeatCount="indefinite"
      />
    </circle>
  </svg>
</template>
