<script>
import * as Icons from '@hui/icons-vue'
import feedbackIconMap from './feedbackIconMap.js'
export default {
  name: 'HFeedbackIcon',

  props: {
    iconName: {
      type: String,
      default: undefined,
    },
    iconType: {
      type: String,
      default: 'svg', // svg / font
    },
    iconStyle: {
      type: Object,
      default: () => {}
    }
  },

  data() {
    return {
      Icons
    }
  },

  computed: {
    feedbackIcon() {
      return !this.iconName.includes('h-icon-')
        ? `h-icon-${this.iconName}`
        : this.iconName
    },
    iconsKeys() {
      return Object.keys(Icons)
    },
    isIconExist() {
      if (this.iconType === 'svg')
        return this.iconName && (this.iconsKeys.includes(this.iconName) || this.iconsKeys.includes(feedbackIconMap[this.feedbackIcon]))
      else
        return false
    },
    svgIconName() {
      if (this.isIconExist)
        return this.Icons[this.iconName] || this.Icons[feedbackIconMap[this.feedbackIcon]]
      else
        return null
    }
  }
}
</script>

<template>
  <h-icon v-if="iconType === 'svg'" class="h-icon-feedback">
    <component :is="svgIconName" v-if="isIconExist" :style="iconStyle" />
  </h-icon>

  <i v-else-if="iconType === 'font' && iconName" :class="feedbackIcon" :style="iconStyle">
    <span class="path1" />
    <span class="path2" />
  </i>
</template>
