<script>
import { Checkbox, CheckboxGroup } from '@hui/components/checkbox'
import { Input } from '@hui/components/input'
import { Scrollbar } from '@hui/components/scrollbar'
import { Delete } from '@hui/icons-vue'
import { t } from '@hui/locale'

export default {
  name: 'ElTransferPanel',

  components: {
    ElCheckboxGroup: CheckboxGroup,
    ElCheckbox: Checkbox,
    ElInput: Input,
    ElScrollbar: Scrollbar,
    Delete,
    OptionContent: {
      props: {
        option: Object
      },
      render(h) {
        const getParent = (vm) => {
          if (vm.$options.componentName === 'ElTransferPanel')
            return vm
          else if (vm.$parent)
            return getParent(vm.$parent)
          else
            return vm
        }
        const parent = getParent(this)
        return parent.renderContent
          ? (
              parent.renderContent(h, this.option)
            )
          : (
          <span class="panel-item-option-content">
            {this.option[parent.labelProp] || this.option[parent.keyProp]}
          </span>
            )
      }
    }
  },

  componentName: 'ElTransferPanel',

  props: {
    data: {
      type: Array,
      default() {
        return []
      }
    },
    renderContent: {
      type: Function,
      default: null
    },
    title: {
      type: String,
      default: null
    },
    filterable: {
      type: Boolean,
      default: null
    },
    filterPlaceholder: {
      type: String,
      default: ''
    },
    footerFormat: {
      type: [Object, Boolean],
      default: null
    },
    filterMethod: {
      type: Function,
      default: null
    },
    defaultChecked: {
      type: Array,
      default: null
    },
    props: {
      type: Object,
      default: null
    },
    panelAt: {
      type: String,
      default: null
    },
    searchPlaceholder: {
      type: String,
      default: ''
    },
    searchMethod: {
      type: Function,
      default: null
    },
    simple: {
      type: Boolean,
      default: false
    },
    retainSelectedItems: {
      type: Boolean,
      default: false
    },
    panelContentHeight: {
      type: Number,
      default: undefined,
    },
    showAllCheckedBox: {
      type: Boolean,
      default: true
    },
  },

  data() {
    return {
      checked: [],
      cloneDefaultChecked: [],
      allChecked: false,
      query: '',
      inputHover: false,
      searchQuery: '',
      t
    }
  },

  computed: {
    filteredData() {
      return this.data.filter((item) => {
        if (typeof this.filterMethod === 'function') {
          return this.filterMethod(this.query, item)
        }
        else {
          const label = item[this.labelProp] || item[this.keyProp].toString()
          return label.toLowerCase().includes(this.query.toLowerCase())
        }
      })
    },

    checkableData() {
      return this.filteredData.filter(item => !item[this.disabledProp])
    },

    checkedSummary() {
      if (this.footerFormat === false)
        return ''
      const checkedLength = this.checked.length
      const dataLength = this.data.length
      const { noChecked, hasChecked } = this.footerFormat
      if (noChecked && hasChecked) {
        return checkedLength > 0
          ? hasChecked
            .replace(/\${checked}/g, checkedLength)
            .replace(/\${total}/g, dataLength)
          : noChecked.replace(/\${total}/g, dataLength)
      }
      else {
        return checkedLength > 0
          ? t('el.transfer.hasCheckedFormat', {
            total: dataLength,
            checked: checkedLength
          })
          : t('el.transfer.noCheckedFormat', {
            total: dataLength
          })
      }
    },

    isIndeterminate() {
      const checkedLength = this.checked.length
      return checkedLength > 0 && checkedLength < this.checkableData.length
    },

    hasNoMatch() {
      return this.query.length > 0 && this.filteredData.length === 0
    },

    inputIcon() {
      return this.query.length > 0 && this.inputHover
        ? 'h-icon-close_f'
        : 'h-icon-search'
    },

    labelProp() {
      return this.props.label || 'label'
    },

    keyProp() {
      return this.props.key || 'key'
    },

    disabledProp() {
      return this.props.disabled || 'disabled'
    },

    isSearch() {
      return this.panelAt === 'left' && typeof this.searchMethod === 'function'
    },

    isShowFooter() {
      return this.$slots.default
      // return this.footerFormat !== false
    },

    placeholder() {
      const {
        filterPlaceholder,
        filterable,
        searchPlaceholder,
        isSearch
      } = this
      if (filterable && !isSearch)
        return filterPlaceholder || t('el.transfer.filterPlaceholder')
      else if (isSearch)
        return searchPlaceholder || t('el.transfer.filterPlaceholder')
      else
        return ''
    },

    isRightSimplePanel() {
      return this.panelAt === 'right' && this.simple
    },
  },

  watch: {
    checked(val) {
      this.updateAllChecked()
      this.$emit('checked-change', val)
    },

    data() {
      const checked = []
      const filteredDataKeys = this.filteredData.map(
        item => item[this.keyProp]
      )
      this.checked.forEach((item) => {
        if (filteredDataKeys.includes(item))
          checked.push(item)
      })
      this.checked = checked
      this.cloneDefaultChecked = checked
    },

    checkableData() {
      this.updateAllChecked()
    },
    cloneDefaultChecked: {
      immediate: true,
      deep: true,
      handler(val, oldVal) {
        if (
          oldVal
          && val.length === oldVal.length
          && val.every(item => oldVal.includes(item))
        )
          return

        const checked = []
        const checkableDataKeys = this.checkableData.map(
          item => item[this.keyProp]
        )
        val.forEach((item) => {
          if (checkableDataKeys.includes(item))
            checked.push(item)
        })
        this.checked = checked
      }
    },
    defaultChecked: {
      immediate: true,
      handler(val) {
        this.cloneDefaultChecked = val && Array.isArray(val) ? JSON.parse(JSON.stringify(val)) : []
      }
    },
  },

  methods: {
    updateAllChecked() {
      const checkableDataKeys = this.checkableData.map(
        item => item[this.keyProp]
      )
      this.allChecked
        = checkableDataKeys.length > 0
        && checkableDataKeys.every(item => this.checked.includes(item))
    },

    handleAllCheckedChange(value) {
      this.checked = value
        ? this.checkableData.map(item => item[this.keyProp])
        : []
      if (value && this.simple)
        this.handleItemClick(this.checked, this.panelAt)
    },

    clearQuery() {
      const { searchMethod, searchQuery, isSearch } = this
      if (this.inputIcon === 'h-icon-close_f') {
        if (isSearch)
          this.searchQuery = ''
        else
          this.query = ''
      }
      else if (isSearch) {
        searchMethod(searchQuery)
      }
    },

    onScrolling(s) {
      const { scrollTop, percentY } = s
      this.$parent.$emit(`on-${this.panelAt}-scrolling`, {
        scrollTop,
        percentY
      })
    },

    handleItemClick(checkedKeys) {
      if (this.simple)
        this.$emit('simple-item-click', checkedKeys, this.panelAt)
    },

    handleAllCheckedClick(event) {
      if (this.isRightSimplePanel || !this.showAllCheckedBox) {
        event.stopPropagation()
        event.preventDefault()
      }
    }
  }
}
</script>

<template>
  <div class="el-transfer-panel" :class="[isRightSimplePanel?'is-right-simple-panel':'']">
    <div class="el-transfer-panel__header">
      <el-checkbox
        v-model="allChecked"
        :indeterminate="isIndeterminate"
        :disabled="simple && !isRightSimplePanel && checked.length === filteredData.length"
        :class="[isRightSimplePanel || !showAllCheckedBox? 'checkbox-hidden' : '']"
        @click.native="handleAllCheckedClick"
        @change="handleAllCheckedChange"
      >
        <div class="panel-item-option-content">
          {{ title }}
          <template v-if="filteredData && filteredData.length">
            ({{ filteredData.length }})
          </template>
        </div>
      </el-checkbox>
      <div v-if="isRightSimplePanel && filteredData.length" class="panel-item-operation-area">
        <el-button type="link" size="small" @click.stop="handleAllCheckedChange(!allChecked)">
          {{ t('el.transfer.clear') }}
        </el-button>
      </div>
    </div>

    <div
      :class="[
        'el-transfer-panel__body',
        !isShowFooter ? 'no-footer' : 'is-with-footer',
        filterable ? 'is-filterable' : ''
      ]"
      :style="[panelContentHeight?{height:panelContentHeight+'px'}:{}]"
    >
      <div v-if="(filterable && !isSearch) || isSearch" class="el-transfer-panel__filter">
        <el-input
          v-if="filterable && !isSearch"
          v-model="query"
          :placeholder="placeholder"
          clearable
          suffix-icon="h-icon-search"
          @mouseenter.native="inputHover = true"
          @mouseleave.native="inputHover = false"
        />
        <el-input
          v-else-if="isSearch"
          v-model="searchQuery"
          :placeholder="placeholder"
          clearable
          suffix-icon="h-icon-search"
          :on-icon-click="()=>{searchMethod(searchQuery)}"
          @keyup.enter.native="searchMethod(searchQuery)"
          @mouseenter.native="inputHover = true"
          @mouseleave.native="inputHover = false"
        />
      </div>
      <el-scrollbar
        v-if="filteredData.length > 0"
        :wrap-class="
          filterable
            ? 'el-transfer-scrollbar-filterable__wrap'
            : 'el-transfer-scrollbar__wrap'
        "
        view-class="el-transfer-scrollbar__view"
        is-small
        @on-scrolling="onScrolling"
      >
        <template v-if="$slots['left-panel-content']">
          <slot name="left-panel-content" />
        </template>
        <template v-else-if="$slots['right-panel-content']">
          <slot name="right-panel-content" />
        </template>
        <template v-else>
          <el-checkbox-group
            v-show="!hasNoMatch && data.length > 0"
            v-model="checked"
            :class="{
              'is-filterable': filterable
            }"
            class="el-transfer-panel__list"
          >
            <el-checkbox
              v-for="item in filteredData"
              :key="item[keyProp]"
              :label="item[keyProp]"
              :disabled="item[disabledProp] || (retainSelectedItems && item.__checked)"
              class="el-transfer-panel__item"
              :class="[isRightSimplePanel?'el-transfer-panel__simple-item':'']"
              :checked="retainSelectedItems && item.__checked"
              @click.native="isRightSimplePanel?null:handleItemClick([item[keyProp]])"
            >
              <option-content :option="item" />
              <div v-if="isRightSimplePanel" class="panel-item-operation-area">
                <h-icon @click.stop.native="handleItemClick([item[keyProp]])">
                  <Delete />
                </h-icon>
              </div>
            </el-checkbox>
          </el-checkbox-group>
        </template>
      </el-scrollbar>
      <!--</div>-->
      <p v-show="hasNoMatch" class="el-transfer-panel__empty">
        {{ t('el.transfer.noMatch') }}
      </p>
      <p
        v-show="data.length === 0 && !hasNoMatch"
        class="el-transfer-panel__empty"
      >
        {{ t('el.transfer.noData') }}
      </p>
    </div>
    <p v-if="isShowFooter" class="el-transfer-panel__footer">
      <!-- <el-checkbox
        v-model="allChecked"
        :indeterminate="isIndeterminate"
        @change="handleAllCheckedChange"
      >
        {{ checkedSummary }}
      </el-checkbox> -->
      <slot />
    </p>
  </div>
</template>
