import { t } from '@hui/locale'

export default {
  computed: {
    showPrompt() {
      // issues831
      // issues:1432valuevaluevalue.lengthcustomValidation
      if (
        typeof this.customValidation === 'function'
        && ((this.minDate && this.maxDate) || (this.value && this.value.length > 0))
      )
        return this.customValidation(this.calcMinDate, this.calcMaxDate)

      return true
    },
    prompt() {
      if (this.showAbnormalInfo)
        return t('el.datepicker.abnormalInfo')

      return this.customPrompt
    },
    disabledConfirm() {
      if (this.showAbnormalInfo || !this.showPrompt)
        return true
      return this.isLinkedRangeChosen
        ? !(
            this.minDate
        && this.maxDate
        && !this.selecting
        && this.isValidValue([this.minDate, this.maxDate])
          )
        : !(
            this.leftDate
        && this.rightDate
        && !this.selecting
        && this.isValidValue([this.leftDate, this.rightDate])
          )
    },
    isUnlinkRange() {
      if (!this.unlink)
        return false

      return this.unlinkRange
    },
    isLinkedRangeChosen() {
      return this.linkedRangeChosen
    }
  },
  methods: {
    emitRange({ newDate, opts }) {
      if (this.isLinkedRangeChosen) {
        if (opts.type === 'min') {
          this.$emit(
            'pick',
            { minDate: newDate, maxDate: this.maxDate },
            opts,
          )
          this.$nextTick(() => {
            // emitRangedate-tabletableTypeChangeddate-rangedate-rangeminmaxmaxmin
            // TODO: month-range year-range 
            if (this.$parent.getTableTypeChanged && !this.$parent.getTableTypeChanged()) {
              this.$emit(
                'tableTypeChange',
                'max'
              )
              this.$parent.setTableTypeChanged(true)
            }
          })
        }
        else if (opts.type === 'max') {
          this.$emit(
            'pick',
            { minDate: this.minDate, maxDate: newDate },
            opts,
          )
          this.$nextTick(() => {
            if (this.$parent.getTableTypeChanged && !this.$parent.getTableTypeChanged()) {
              this.$emit(
                'tableTypeChange',
                'min'
              )
              this.$parent.setTableTypeChanged(true)
            }
          })
        }
      }
      else if (this.unlinkRange) {
        if (this.tableType === 'min') {
          if (
            newDate < this.maxDate
            || (this.minDate === null && this.maxDate === null)
            || this.unlinkRange
          ) {
            this.$emit(
              'pick',
              { minDate: newDate, maxDate: this.maxDate },
              opts,
            )
          }
          else {
            this.$emit('pick', { minDate: null, maxDate: this.maxDate }, opts)
          }
        }
        else {
          if (
            newDate > this.minDate
            || (this.minDate === null && this.maxDate === null)
            || this.unlinkRange
          ) {
            this.$emit(
              'pick',
              { minDate: this.minDate, maxDate: newDate },
              opts,
            )
          }
          else {
            this.$emit('pick', { minDate: this.minDate, maxDate: null }, opts)
          }
        }
      }
      else {
        if (!this.rangeState.selecting) {
          this.$emit('pick', { minDate: newDate, maxDate: null }, opts)
          this.rangeState.selecting = true
        }
        else {
          if (newDate >= this.minDate) {
            this.$emit(
              'pick',
              { minDate: this.minDate, maxDate: newDate },
              opts,
            )
          }
          else {
            this.$emit(
              'pick',
              { minDate: newDate, maxDate: this.minDate },
              opts,
            )
          }
          this.rangeState.selecting = false
        }
      }
    },
  },
  data() {
    return {
      customValidation: () => true,
      customPrompt: t('el.datepicker.abnormalInfo'),
    }
  },
}
