<script>
import { addClass, emitter, hasClass, removeClass } from '@hui/utils'

export default {
  name: 'ElNav',

  componentName: 'ElNav',

  components: {
    'el-nav-collapse-transition': {
      functional: true,
      render(createElement, context) {
        const data = {
          props: {
            mode: 'out-in'
          },
          on: {
            beforeEnter(el) {
              el.style.opacity = 0.2
            },

            enter(el) {
              addClass(el, 'el-opacity-transition')
              el.style.opacity = 1
            },

            afterEnter(el) {
              removeClass(el, 'el-opacity-transition')
              el.style.opacity = ''
            },

            beforeLeave(el) {
              if (!el.dataset)
                el.dataset = {}

              if (hasClass(el, 'el-nav--collapse')) {
                removeClass(el, 'el-nav--collapse')
                el.dataset.oldOverflow = el.style.overflow
                el.dataset.scrollWidth = el.scrollWidth
                addClass(el, 'el-nav--collapse')
              }

              el.style.width = `${el.scrollWidth}px`
              el.style.overflow = 'hidden'
            },

            leave(el) {
              if (!hasClass(el, 'el-nav--collapse')) {
                addClass(el, 'horizontal-collapse-transition')
                el.style.width = '48px'
              }
              else {
                addClass(el, 'horizontal-collapse-transition')
                el.style.width = `${el.dataset.scrollWidth}px`
              }
            },

            afterLeave(el) {
              removeClass(el, 'horizontal-collapse-transition')
              if (hasClass(el, 'el-nav--collapse'))
                el.style.width = `${el.dataset.scrollWidth}px`
              else
                el.style.width = '48px'

              el.style.overflow = el.dataset.oldOverflow
            }
          }
        }
        return createElement('transition', data, context.children)
      }
    }
  },

  mixins: [emitter],

  provide() {
    return {
      rootNav: this
    }
  },

  props: {
    mode: {
      type: String,
      default: 'vertical'
    },
    defaultActive: {
      type: String,
      default: ''
    },
    defaultOpeneds: {
      type: Array,
      default: null
    },
    uniqueOpened: {
      type: Boolean,
      default: null
    },
    router: {
      type: Boolean,
      default: null
    },
    navTrigger: {
      type: String,
      default: 'hover'
    },
    collapse: {
      type: Boolean,
      default: null
    },
    collapseBtn: {
      type: Boolean,
      default: null
    },
    collapseBtnClick: {
      type: Function,
      default: null
    },
    noIcon: {
      type: Boolean,
      default: false
    },
    wrapScroll: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeIndex: this.defaultActive,
      openedNavs: this.defaultOpeneds ? this.defaultOpeneds.slice(0) : [],
      openedCollapseNavs: [], // 
      items: {},
      subnavs: {}
    }
  },
  watch: {
    defaultActive(value) {
      const item = this.items[value]
      if (item) {
        this.activeIndex = item.index
        this.initOpenedNav()
      }
      else {
        this.activeIndex = ''
      }
    },
    defaultOpeneds(value) {
      this.openedNavs = value
    },
    collapse(value) {
      if (value) {
        this.openedNavs = []
        this.openedCollapseNavs = []
        this.$emit('fold')
      }
      else {
        this.$emit('unfold')
      }
    }
  },
  mounted() {
    this.initOpenedNav()
    this.$on('item-click', this.handleItemClick)
    this.$on('subnav-click', this.handleSubnavClick)
  },
  methods: {
    addItem(item) {
      this.$set(this.items, item.index, item)
    },
    removeItem(item) {
      delete this.items[item.index]
    },
    addSubnav(item) {
      this.$set(this.subnavs, item.index, item)
    },
    removeSubnav(item) {
      delete this.subnavs[item.index]
    },
    openNav(index, indexPath) {
      const openedNavs = this.openedNavs
      if (openedNavs.includes(index))
        return
      // 
      if (this.uniqueOpened) {
        this.openedNavs = openedNavs.filter((index) => {
          return indexPath.includes(index)
        })
      }
      this.openedNavs.push(index)
    },
    closeNav(index) {
      const i = this.openedNavs.indexOf(index)
      if (i !== -1)
        this.openedNavs.splice(i, 1)
    },
    // 
    closeCollapseNav() {
      this.openedCollapseNavs.forEach((index) => {
        const i = this.openedNavs.indexOf(index)
        if (i !== -1)
          this.openedNavs.splice(i, 1)
      })
      this.openedCollapseNavs.length = 0
    },
    handleSubnavClick(subnav) {
      const { index, indexPath } = subnav
      const isOpened = this.openedNavs.includes(index)

      if (isOpened) {
        this.closeNav(index)
        this.$emit('close', index, indexPath)
      }
      else {
        this.openNav(index, indexPath)
        this.$emit('open', index, indexPath)
      }
    },
    handleItemClick(item) {
      const { index, indexPath } = item
      this.activeIndex = item.index
      this.$emit('select', index, indexPath, item)

      if (this.mode === 'horizontal' || this.collapse)
        this.openedNavs = []

      if (this.router) {
        this.routeToItem(item, (error) => {
          if (error) {
            // vue-router 3.1.0+ push/replace cause NavigationDuplicated error
            if (error.name === 'NavigationDuplicated')
              return
            console.error(error)
          }
        })
      }
    },
    // 
    initOpenedNav() {
      const index = this.activeIndex
      const activeItem = this.items[index]
      if (!activeItem || this.mode === 'horizontal' || this.collapse)
        return

      const indexPath = activeItem.indexPath

      // 
      indexPath.forEach((index) => {
        const subnav = this.subnavs[index]
        subnav && this.openNav(index, subnav.indexPath)
      })
    },
    routeToItem(item, onError) {
      const route = item.route || item.index
      try {
        this.$router.push(route, () => {}, onError)
      }
      catch (e) {
        console.error(e)
      }
    },
    collapseNav() {
      //  prop 
      this.$emit('click-collapse')
      if (this.wrapScroll)
        this.closeCollapseNav()
    },
    open(index) {
      const { indexPath } = this.subnavs[index.toString()]
      indexPath.forEach(i => this.openNav(i, indexPath))
    },
    close(index) {
      this.closeNav(index.toString())
    }
  }
}
</script>
<template>
  <el-nav-collapse-transition>
    <ul
      :key="+collapse"
      :class="{
        'el-nav--horizontal': mode === 'horizontal',
        'el-nav--collapse': collapse
      }"
      class="el-nav"
    >
      <slot name="nav-top" />
      <li
        v-if="collapseBtn"
        class="el-nav--colloase-btn"
        @click="collapseNav"
      >
        <div class="el-subnav__title">
          <i class="el-nav-icon h-icon-menu_leftbar" />
        </div>
      </li>
      <slot />
    </ul>
  </el-nav-collapse-transition>
</template>
