<script>
import {
  emitter,
} from '@hui/utils'

export default {
  name: 'ElSubDropdown',

  componentName: 'ElSubDropdown',

  mixins: [emitter],

  props: {
    icon: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: null
    }
  },

  data() {
    return {
      opened: false
    }
  },

  computed: {

  },

  watch: {

  },

  created() {

  },
  mounted() {

  },
  beforeDestroy() {

  },
  methods: {
    handleMouseenter() {
      this.opened = true
    },
    handleMouseleave() {
      this.opened = false
    },
    handleClick() {}
  }
}
</script>
<template>
  <li
    :class="{
      'el-dropdown-menu__item': true,
      'el-dropdown-menu__sub-item': true,
      'el-dropdown': true,
      'is-active': active,
      'is-opened': opened,
      'is-disabled': disabled,
    }"
    :tabindex="disabled ? null : -1"
    @mouseenter="handleMouseenter"
    @mouseleave="handleMouseleave"
  >
    <div
      ref="dropdown-title"
      :title="title"
      class="el-dropdown__title"
      @click="handleClick"
    >
      <i v-if="$slots.icon" class="el-menu-icon is-slot">
        <slot name="icon" />
      </i>
      <i v-else-if="icon" :class="icon" class="el-menu-icon" />
      <span v-if="$slots.title" class="el-dropdown__title--text">
        <slot name="title" />
      </span>
      <span v-else class="el-dropdown__title--text">{{ title }}</span>
      <i
        :class="{
          'el-dropdown__icon-arrow': true,
          'h-icon-angle_right_sm': true
        }"
      />
    </div>
    <template>
      <transition>
        <ul v-show="opened" class="el-dropdown_open">
          <slot />
        </ul>
      </transition>
    </template>
  </li>
</template>
