<script>
import { Clickoutside } from '@hui/utils'
import SelectorDropdown from './components/selector-dropdown.vue'
export default {
  name: 'HColorSelector',

  directives: { Clickoutside },

  components: {
    SelectorDropdown
  },

  props: {
    type: { type: String, default: 'radio' },
    colorList: {
      type: Array,
      default: () => {
        return [
          '#f30303',
          '#e92656',
          '#960fa3',
          '#3f40a9',
          '#2c84ee',
          '#00b2cd',
          '#008b7e',
          '#36a751',
          '#79be4d',
          '#c2da46',
          '#fdeb4a',
          '#ff911b',
        ]
      },
    },
    placement: {
      type: String,
      default: 'bottom-start',
    },
  },

  data() {
    return {
      showPicker: false,
      selectedColor: undefined
    }
  },

  computed: {

  },
  watch: {

  },

  mounted() {

  },

  methods: {
    switchShow() {
      this.showPicker = !this.showPicker
    },
    hide() {
      this.showPicker = false
    },
    handleValueChange(value) {
      this.selectedColor = value
      this.$emit('valueChange', value)
    },
  },
}
</script>

<template>
  <div v-clickoutside="hide" class="h-color-selector">
    <div class="h-color-selector__input" @click="switchShow">
      <template v-if="type==='checkbox' && selectedColor">
        <div v-for="item in selectedColor" :key="item" class="color-block" :style="{backgroundColor:item}" />
      </template>
      <template v-else>
        <div v-if="selectedColor" class="color-block" :style="{backgroundColor:selectedColor}" />
      </template>
    </div>
    <selector-dropdown v-model="showPicker" :placement="placement" :type="type" class="h-color-selector__panel" @valueChange="handleValueChange" />
  </div>
</template>
