<script>
import { Button } from '@hui/components/button'
import { RepeatClick } from '@hui/utils'
import Input from './input.vue'
export default {
  name: 'ElInputNumber',
  directives: {
    repeatClick: RepeatClick,
  },
  components: {
    ElInput: Input,
    ElButton: Button,
  },
  inheritAttrs: false,
  props: {
    step: {
      type: Number,
      default: 1,
    },
    stepStrictly: {
      type: Boolean,
      default: false,
    },
    max: {
      type: Number,
      default: Infinity,
    },
    min: {
      type: Number,
      default: 0,
    },
    value: {
      type: null,
      default: 0,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: '',
    },
    controls: {
      type: Boolean,
      default: true,
    },
    controlsPosition: {
      type: String,
      default: '', // positionrightsides
    },
    name: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    precision: {
      type: Number,
      validator(val) {
        return val >= 0 && val === parseInt(val, 10)
      },
      default: 0,
    },
    resetNum: {
      type: [Number, String, null],
      default: null,
    },
    formatter: {
      type: Function,
    },
    parser: {
      type: Function,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    simple: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentValue: 0,
      userInput: null,
    }
  },
  computed: {
    // decrease
    minDisabled() {
      return this.value <= this.min
      // return this._decrease(this.value, this.step) < this.min;
    },
    // increase
    maxDisabled() {
      // return this._increase(this.value, this.step) > this.max;
      return this.value >= this.max
    },
    // 
    numPrecision() {
      const { value, step, getPrecision, precision } = this
      const stepPrecision = getPrecision(step)
      if (precision !== undefined) {
        //  valuevaulue
        if (stepPrecision > precision) {
          console.warn(
            '[HUI Warn][InputNumber]precision should not be less than the decimal places of step',
          )
        }
        return precision
      }
      else {
        // value
        return Math.max(getPrecision(value), stepPrecision)
      }
    },
    controlsAtRight() {
      return this.controls && this.controlsPosition === 'right'
    },
    inputNumberSize() {
      return this.size
    },
    inputNumberDisabled() {
      return this.disabled
    },
    // 
    displayValue() {
      if (this.userInput !== null && this.userInput !== '')
        return this.formatter?.(this.parser?.(this.userInput) ?? this.userInput) ?? this.userInput

      let currentValue = this.currentValue
      if (typeof currentValue === 'number') {
        if (this.stepStrictly) {
          const stepPrecision = this.getPrecision(this.step)
          const precisionFactor = Math.pow(10, stepPrecision)
          currentValue
            = (Math.round(currentValue / this.step)
              * precisionFactor
              * this.step)
            / precisionFactor
        }
        // 
        if (this.precision !== undefined)
          currentValue = currentValue.toFixed(this.precision)
      }
      return this.formatter?.(currentValue) ?? currentValue
    },
  },
  watch: {
    // 
    value: {
      immediate: true,
      handler(tempValue) {
        const value = this.parser?.(tempValue) ?? tempValue
        let newVal
          = value === undefined || value === '' ? value : Number(value)

        if (typeof newVal === 'number') {
          if (isNaN(newVal))
            return

          if (this.stepStrictly) {
            const stepPrecision = this.getPrecision(this.step)
            const precisionFactor = Math.pow(10, stepPrecision)
            newVal
              = (Math.round(newVal / this.step) * precisionFactor * this.step)
              / precisionFactor
          }
          if (this.precision !== undefined)
            newVal = this.toPrecision(newVal, this.precision)
        }
        if (typeof newVal === 'number') {
          if (newVal >= this.max)
            newVal = this.max
          if (newVal <= this.min)
            newVal = this.min
        }
        this.currentValue = newVal
        // this.userInput = null;
        this.$emit('input', newVal)
      },
    },
  },
  mounted() {
    const innerInput = this.$refs.input.$refs.input
    innerInput.setAttribute('role', 'spinbutton')
    innerInput.setAttribute('aria-valuemax', this.max)
    innerInput.setAttribute('aria-valuemin', this.min)
    innerInput.setAttribute('aria-valuenow', this.currentValue)
    innerInput.setAttribute('aria-disabled', this.inputNumberDisabled)
  },
  updated() {
    if (!this.$refs || !this.$refs.input)
      return
    const innerInput = this.$refs.input.$refs.input
    innerInput.setAttribute('aria-valuenow', this.currentValue)
  },
  methods: {
    // 
    toPrecision(num, precision) {
      if (precision === undefined)
        precision = this.numPrecision
      return parseFloat(
        Math.round(num * Math.pow(10, precision)) / Math.pow(10, precision),
      )
    },
    // 
    getPrecision(value) {
      if (value === undefined)
        return 0
      const valueString = value.toString()
      const dotPosition = valueString.indexOf('.')
      let precision = 0
      // 
      if (dotPosition !== -1)
        precision = valueString.length - dotPosition - 1

      return precision
    },
    _increase(val, step) {
      if (typeof val !== 'number' && val !== undefined)
        return this.currentValue
      const precisionFactor = Math.pow(10, this.numPrecision)
      // Solve the accuracy problem of JS decimal calculation by converting the value to integer.
      return this.toPrecision(
        (precisionFactor * val + precisionFactor * step) / precisionFactor,
      )
    },
    _decrease(val, step) {
      if (typeof val !== 'number' && val !== undefined)
        return this.currentValue
      const precisionFactor = Math.pow(10, this.numPrecision)
      return this.toPrecision(
        (precisionFactor * val - precisionFactor * step) / precisionFactor,
      )
    },
    increase() {
      if (this.inputNumberDisabled || this.maxDisabled)
        return
      this.userInput = null
      const value = this.value || 0
      const newVal = this._increase(value, this.step)
      this.setCurrentValue(newVal)
    },
    decrease() {
      if (this.inputNumberDisabled || this.minDisabled)
        return
      this.userInput = null
      const value = this.value || 0
      const newVal = this._decrease(value, this.step)
      this.setCurrentValue(newVal)
    },
    handleBlur(event) {
      const { value } = event.target
      if (value.trim() === '' || (this.currentValue === '' && isNaN(Number(this.parser?.(value) ?? value)))) {
        const tempValue = this.resetNum === null
          ? this.min
          : this.resetNum
        this.setCurrentValue(tempValue)
      }
      this.userInput = null
      this.$emit('blur', event)
    },
    handleFocus(event) {
      this.$emit('focus', event)
    },
    setCurrentValue(newVal) {
      const oldVal = this.currentValue

      if (typeof newVal === 'number' && this.precision !== undefined)
        newVal = this.toPrecision(newVal, this.precision)

      if (typeof newVal === 'number') {
        if (newVal >= this.max)
          newVal = this.max
        if (newVal <= this.min)
          newVal = this.min
      }
      if (oldVal === newVal)
        return
      // this.userInput = null;
      this.$emit('input', newVal)
      this.$emit('change', newVal, oldVal)
      this.currentValue = newVal
    },
    handleInput(value) {
      this.userInput = value
    },
    handleInputChange(value) {
      // resetNum xx
      // const newVal
      //   = value.trim() === ''
      //     ? this.resetNum === null
      //       ? this.min
      //       : this.resetNum
      //     : Number(this.parser?.(value) ?? value)
      // if (!isNaN(newVal) || value === '')
      //   this.setCurrentValue(newVal)

      const newVal
        = value.trim() === ''
          ? this.resetNum === null
            ? ''
            : `${this.resetNum}`
          : Number(this.parser?.(value) ?? value)
      if (!isNaN(newVal) || value === '')
        this.setCurrentValue(newVal)

      // this.userInput = null;
    },
    slotBorderHide(slot) {
      // ElSelectElSelect
      if (slot && slot.length === 1 && slot[0].tag && slot[0].tag.split('-').pop() === 'ElSelect')
        return true
      else
        return false
    }
  },
}
</script>
<template>
  <div
    :class="[
      'el-input',
      size ? 'el-input--' + size : '',
      'el-input-number',
      inputNumberSize ? 'el-input-number--' + inputNumberSize : '',
      { 'is-disabled': inputNumberDisabled },
      { 'is-without-controls': !controls },
      controls && controlsPosition ? `is-controls-${controlsPosition}` : '',
      {
        'el-input-group': $slots.prepend || $slots.append,
        'el-input-group--append': $slots.append,
        'el-input-group--prepend': $slots.prepend,
        'is-simple': simple,
      }
    ]" @dragstart.prevent
  >
    <!--  -->
    <div
      v-if="$slots.prepend" :class="['el-input-group__prepend',{
        'no-border':slotBorderHide($slots.prepend)
      }]"
    >
      <slot name="prepend" />
    </div>

    <el-input
      ref="input" v-bind="$attrs" :value="displayValue" :placeholder="placeholder"
      :disabled="inputNumberDisabled" :size="inputNumberSize" :name="name" :label="label"
      :readonly="readonly" @keydown.up.native.prevent="increase" @keydown.down.native.prevent="decrease"
      @blur="handleBlur" @focus="handleFocus" @input="handleInput"
      @change="handleInputChange"
    >
      <template v-if="$slots.prefix" #prefix>
        <slot name="prefix" />
      </template>
      <template v-if="$slots.suffix" #suffix>
        <slot name="suffix" />
      </template>
      <div slot="inputNumber" class="input-number-button">
        <el-button
          v-if="controls" v-repeat-click="decrease" :disabled="disabled" :class="{ 'is-disabled': minDisabled }" :type="simple && controlsPosition!=='sides'?undefined:'ghost'"
          :icon="`h-icon-${controlsPosition?'minus':'angle_down'}`" size="mini" class="el-decrease  el-input-number__decrease " @keydown.enter="decrease"
        />
        <el-divider v-if="controlsPosition==='right'" class="input-button-divider" direction="vertical" />
        <el-button
          v-if="controls" v-repeat-click="increase" :disabled="disabled" :class="{ 'is-disabled': maxDisabled }" :type="simple && controlsPosition!=='sides'?undefined:'ghost'"
          :icon="`h-icon-${controlsPosition?'add':'angle_up'}`" size="mini" class="el-increase el-input-number__increase" @keydown.enter="increase"
        />
      </div>
    </el-input>
    <!--  -->
    <div
      v-if="$slots.append" :class="['el-input-group__append',{
        'no-border':slotBorderHide($slots.append)
      }]"
    >
      <slot name="append" />
    </div>
  </div>
</template>
