<script>
export default {
  name: 'ElTimeline',
  provide() {
    return {
      timeline: this
    }
  },

  props: {
    reverse: {
      type: Boolean,
      default: false
    },
    mode: {
      type: String,
      default: 'left-top'
    },
    pending: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      timeLines: []
    }
  },

  render() {
    const reverse = this.reverse
    const pending = this.pending
    const classes = {
      'el-timeline': true,
      'is-reverse': reverse,
      'is-pending': pending,
      'is-custom': this.mode === 'crossed-custom'
    }
    let slots = this.$slots.default || []
    if (reverse)
      slots = slots.reverse()

    return <ul class={classes}>{slots}</ul>
  }
}
</script>
