<script>
import {
  addResizeListener,
  debounce,
  removeResizeListener,
  throttle
} from '@hui/utils'
import { t } from '@hui/locale'
import { Scrollbar as ElScrollbar } from '@hui/components/scrollbar'
import TableStore from './table-store'
import TableLayout from './table-layout'
import TableBody from './table-body'
import TableHeader from './table-header'
import TableFooter from './table-footer'
import { mousewheel, normalizeWheel } from './util'

const tableIdSeed = 1

export default {
  name: 'ElTable',

  components: {
    TableHeader,
    TableFooter,
    TableBody,
    // ElCheckbox,
    ElScrollbar
  },

  props: {
    data: {
      type: Array,
      default() {
        return []
      }
    },

    width: {
      type: [String, Number],
      default: null
    },

    height: {
      type: [String, Number],
      default: null
    },

    maxHeight: {
      type: [String, Number],
      default: null
    },

    fit: {
      type: Boolean,
      default: true
    },

    stripe: {
      type: Boolean,
      default: null
    },

    border: {
      type: Boolean,
      default: null
    },

    // 
    outBorder: {
      type: Boolean,
      default: false
    },

    rowKey: {
      type: [String, Function],
      default: null
    },

    context: {
      type: null,
      default: null
    },

    showHeader: {
      type: Boolean,
      default: true
    },

    showSummary: {
      type: Boolean,
      default: null
    },

    sumText: {
      type: String,
      default: null
    },

    summaryMethod: {
      type: Function,
      default: null
    },

    rowClassName: {
      type: [String, Function],
      default: null
    },

    rowStyle: {
      type: [Object, Function],
      default: null
    },

    cellClassName: {
      type: [String, Function],
      default: null
    },

    cellStyle: {
      type: [Object, Function],
      default: null
    },

    headerRowClassName: {
      type: [String, Function],
      default: null
    },

    headerRowStyle: {
      type: [Object, Function],
      default: null
    },

    headerCellClassName: {
      type: [String, Function],
      default: null
    },

    headerCellStyle: {
      type: [Object, Function],
      default: null
    },

    highlightCurrentRow: {
      type: Boolean,
      default: null
    },

    currentRowKey: {
      type: [String, Number],
      default: null
    },

    emptyText: {
      type: String,
      default: null
    },

    expandRowKeys: {
      type: Array,
      default: null
    },

    defaultExpandAll: {
      type: Boolean,
      default: null
    },

    defaultSort: {
      type: Object,
      default() {
        return {
          prop: '',
          order: ''
        }
      }
    },
    //  add by yangzhini 2019-04-18
    defaultSortMethod: {
      type: Function,
      default: null
    },
    // 
    defaultCheckedRowKeys: {
      type: Array,
      default: null
    },

    alwaysShowBar: {
      type: Boolean,
      default: null
    }, // 

    forceScroll: {
      // 
      type: [Boolean, String],
      default: false
    },

    showLoading: {
      // 
      type: Boolean,
      default: false
    },

    spanMethod: {
      type: Function,
      default: null
    },

    showHeaderOverflow: {
      // headertitle add by yangzhini 2018-08-02
      type: Boolean,
      default: true
    },
    showOverflowTooltip: {
      type: Boolean,
      default: null
    },
    showOverflowTitle: {
      // title modify by yangzhini 2019-03-21
      type: Boolean,
      default: true
    },

    //  add by yangzhini 2019-09-05
    enableVirtualScroll: {
      type: Boolean,
      default: false
    },
    // 
    showHeaderSelection: {
      type: Boolean,
      default: true
    },
    // 
    showSelectedRowBgcolor: {
      type: Boolean,
      default: false
    },
    // 
    showHeaderSortTip: {
      type: Boolean,
      default: true
    }
  },

  data() {
    const store = new TableStore(this, {
      rowKey: this.rowKey,
      defaultExpandAll: this.defaultExpandAll,
      defaultSortMethod: this.defaultSortMethod
    })
    const layout = new TableLayout({
      store,
      table: this,
      fit: this.fit,
      showHeader: this.showHeader
    })
    return {
      t,
      store,
      layout,
      renderExpanded: null,
      resizeProxyVisible: false,
      // isShowLoading: false,  2021/7/27
      /* force-scrollboolean
       * booleanstring
       * force-scrollundefined
       * 
       * */
      niceScroll: true,
      /**
       * el-table__fixedalwaysShowBar
       */
      isFixBarScrollShow: false,
      // fixedShadowHeight: '',
      resizeState: {
        width: null,
        height: null
      },
      // 
      isGroup: false,

      scrollTop: 0,
      bodyWrapperHeight: 1080
    }
  },

  computed: {
    bodyWrapper() {
      return this.$refs.bodyWrapper
    },

    shouldUpdateHeight() {
      return (
        this.height
        || this.maxHeight
        || this.fixedColumns.length > 0
        || this.rightFixedColumns.length > 0
      )
    },

    selection() {
      return this.store.states.selection
    },

    columns() {
      return this.store.states.columns
    },

    tableData() {
      return this.store.states.data
    },

    fixedColumns() {
      return this.store.states.fixedColumns || []
    },

    rightFixedColumns() {
      return this.store.states.rightFixedColumns
    },

    wrapStyle() {
      let style = []

      if (this.maxHeight) {
        let _maxHeight = this.maxHeight
        if (typeof _maxHeight === 'string')
          _maxHeight = +_maxHeight.replace('px', '')

        style = [
          {
            'max-height':
              `${this.showHeader
                ? _maxHeight
                - this.layout.headerHeight
                - this.layout.footerHeight
                : _maxHeight - this.layout.footerHeight}px`
          }
        ]
      }

      return style
    },

    bodyHeight() {
      let style = {}

      if (this.maxHeight) {
        let _maxHeight = this.maxHeight
        if (typeof _maxHeight === 'string')
          _maxHeight = +_maxHeight.replace('px', '')

        style = {
          'max-height':
            `${this.showHeader
              ? _maxHeight - this.layout.headerHeight - this.layout.footerHeight
              : _maxHeight - this.layout.footerHeight}px`
        }
        // this.setScrollStyle();
      }
      else if (this.niceScroll && this.niceScroll !== 'horizontal') {
        //  IE 
        style = {
          'flex-basis': '100%',
          'min-height': '0'
        }
      }
      else {
        style = {
          height: this.layout.bodyHeight ? `${this.layout.bodyHeight}px` : ''
        }
      }

      return style
    },

    bodyWidth() {
      const { bodyWidth } = this.layout
      return bodyWidth ? `${bodyWidth}px` : ''
    },

    fixedWidth() {
      const { fixedWidth } = this.layout
      return fixedWidth ? `${fixedWidth}px` : ''
    },

    rightFixedWidth() {
      const { rightFixedWidth } = this.layout
      return rightFixedWidth ? `${rightFixedWidth}px` : ''
    },

    fixedBodyHeight() {
      let style = {}

      if (this.height || this.niceScroll) {
        style = {
          height: this.layout.fixedBodyHeight
            ? `${this.layout.fixedBodyHeight}px`
            : ''
        }
      }
      else if (this.maxHeight) {
        let _maxHeight = this.maxHeight
        if (typeof _maxHeight === 'string')
          _maxHeight = +_maxHeight.replace('px', '')

        if (this.showHeader)
          _maxHeight -= this.layout.headerHeight

        _maxHeight -= this.layout.footerHeight

        style = {
          'max-height': `${_maxHeight}px`
        }
      }
      style.top = `${this.layout.headerHeight}px`

      return style
    },

    fixedHeight() {
      let style = {}

      if (this.maxHeight) {
        style = {
          bottom: 0
        }
      }
      else {
        style = {
          height: this.layout.tableHeight ? `${this.layout.tableHeight}px` : ''
        }
      }

      return style
    },

    fixedShadowHeight() {
      const { tableHeight } = this.layout
      return tableHeight ? `${tableHeight}px` : ''
    },

    isShowScrollbar() {
      const isShow = this.height || this.maxHeight || !!this.niceScroll
      return !!isShow
    },

    isVirtualScroll() {
      return (
        this.isShowScrollbar
        && this.enableVirtualScroll
        && this.niceScroll !== 'horizontal'
      )
    },

    isShowEmptyText() {
      const empty
        = !this.showLoading
        && (!this.data
          || this.data.length === 0
          || !this.tableData
          || this.tableData.length === 0)
      empty && this.fixEmptyScrollViewHeight()
      return empty
    }
  },

  watch: {
    height: {
      immediate: true,
      handler(value) {
        this.layout.setHeight(value)
        this.setScrollStyle()
      }
    },

    maxHeight: {
      immediate: true,
      handler(value) {
        this.layout.setMaxHeight(value)
        this.setScrollStyle()
      }
    },

    currentRowKey: {
      immediate: true,
      handler(value) {
        if (!this.rowKey)
          return
        this.$nextTick(() => {
          this.store.setCurrentRowKey(value)
        })
      }
    },

    data: {
      immediate: true,
      handler(val = []) {
        this.store.commit('setData', val)
        if (this.$ready) {
          this.doLayout()
          this.$refs.tableScrollbar && this.$refs.tableScrollbar.update()
        }
      }
    },

    expandRowKeys: {
      immediate: true,
      handler(newVal) {
        if (newVal)
          this.store.setExpandRowKeys(newVal)
      }
    },

    isShowEmptyText(v) {
      v && this.fixEmptyScrollViewHeight()
    },

    columns(c, p) {
      /**
       * modify by zhangxiaogang
       * 2018-06-07
       * info: 
       *    realWidth
       */
      if (p.length > 0 && p.length !== c.length) {
        this.columns.forEach((column) => {
          column.realWidth = column.width || column.minWidth
        })
      }
    },

    defaultCheckedRowKeys: {
      immediate: true,
      deep: true,
      handler(val) {
        val && this.data.forEach((item) => {
          this.toggleRowSelection(item, val.includes(item[this.rowKey || 'id']))
        })
      }
    }
  },

  created() {
    this.tableId = `el-table_${tableIdSeed}_`
    // this.debouncedLayout = debounce(50, () => this.doLayout());
    this.debouncedLayout = debounce(50, debouncedLayout =>
      this.doLayout(debouncedLayout)
    )

    this.niceScroll = this.forceScroll
    if (this.niceScroll === '')
      this.niceScroll = true
  },

  destroyed() {
    if (this.resizeListener)
      removeResizeListener(this.$el, this.resizeListener)
  },

  mounted() {
    this.$nextTick(() => {
      this.bindEvents()
      this.doLayout() //  nextTick  IE  bodyWidth    add by yangzhini  2020-05-11
    })
    this.store.updateColumns()

    this.resizeState = {
      width: this.$el.offsetWidth,
      height: this.$el.offsetHeight
    }

    // init filters
    this.columns.forEach((column) => {
      if (column.filteredValue && column.filteredValue.length) {
        this.store.commit('filterChange', {
          column,
          values: column.filteredValue,
          silent: true
        })
      }
    })

    this.$ready = true

    this.setScrollStyle()
  },

  methods: {
    setCurrentRow(row) {
      this.store.commit('setCurrentRow', row)
    },

    toggleRowSelection(row, selected, emitChange = true) {
      this.store.toggleRowSelection(row, selected, emitChange)
      this.store.updateAllSelected()
    },

    toggleRowExpanded(row, expanded) {
      this.store.toggleRowExpanded(row, expanded)
    },

    clearSelection() {
      this.store.clearSelection()
    },

    handleMouseLeave() {
      this.store.commit('setHoverRow', null)
      if (this.hoverState)
        this.hoverState = null
    },

    // updateScrollY() {
    //   this.layout.updateScrollY();
    // },

    /**
     * scrollbar
     * add by zhangxiaogang 2017-09-27
     * @param {Object} s {scrollLeft: wrap.scrollLeft, scrollTop: wrap.scrollTop} 
     */
    onScrollingX(s) {
      //  ()
      const { headerWrapper, footerWrapper } = this.$refs
      const { scrollLeft, percentX } = s
      if (headerWrapper)
        headerWrapper.scrollLeft = s.scrollLeft
      if (footerWrapper)
        footerWrapper.scrollLeft = s.scrollLeft
      this.$emit('on-scrolling-x', scrollLeft, percentX)
      this.$emit('on-scrolling', s)
    },
    /**
     * scrollbar
     * add by zhangxiaogang 2017-09-27
     * @param {Object} s {scrollLeft: wrap.scrollLeft, scrollTop: wrap.scrollTop} 
     */
    onScrollingY(s) {
      //  ()
      const { fixedBodyWrapper, rightFixedBodyWrapper } = this.$refs
      const { scrollTop, percentY } = s
      if (this.isVirtualScroll) {
        this.scrollTop = scrollTop
        process.env.NODE_ENV !== 'production'
          && this.store.getTableRowsHeight()
      }
      if (fixedBodyWrapper)
        fixedBodyWrapper.scrollTop = s.scrollTop
      if (rightFixedBodyWrapper)
        rightFixedBodyWrapper.scrollTop = s.scrollTop
      this.$emit('on-scrolling-y', scrollTop, percentY)
      this.$emit('on-scrolling', s)
    },

    bindEvents() {
      const { fixedBodyWrapper, rightFixedBodyWrapper } = this.$refs
      /**
       * modify by yangzhini
       * 2019-02-13
       * info: 
       */
      const scrollFixedBodyWrapper = (event) => {
        const data = normalizeWheel(event)
        const wrap = this.$refs.tableScrollbar.$refs.wrap

        if (Math.abs(data.spinY) > 0) {
          const currentScrollTop = wrap.scrollTop
          if (data.pixelY < 0 && currentScrollTop !== 0)
            event.preventDefault()

          if (
            data.pixelY > 0
            && wrap.scrollHeight - wrap.clientHeight > currentScrollTop
          )
            event.preventDefault()

          wrap.scrollTop += Math.ceil(data.pixelY / 5)
        }
        else {
          wrap.scrollLeft += Math.ceil(data.pixelX / 5)
        }
      }

      if (fixedBodyWrapper)
        mousewheel(fixedBodyWrapper, throttle(16, scrollFixedBodyWrapper))

      if (rightFixedBodyWrapper)
        mousewheel(rightFixedBodyWrapper, throttle(16, scrollFixedBodyWrapper))

      if (this.fit)
        addResizeListener(this.$el, this.resizeListener)
    },

    resizeListener() {
      if (!this.$ready)
        return
      let shouldUpdateLayout = false
      const el = this.$el
      const { width: oldWidth, height: oldHeight } = this.resizeState

      const width = el.offsetWidth
      if (oldWidth !== width)
        shouldUpdateLayout = true

      const height = el.offsetHeight
      if ((this.height || this.shouldUpdateHeight) && oldHeight !== height)
        shouldUpdateLayout = true

      if (shouldUpdateLayout) {
        this.resizeState.width = width
        this.resizeState.height = height
        this.doLayout()
      }
    },

    doLayout(dragResize) {
      this.layout.update(dragResize)
      if (this.shouldUpdateHeight)
        this.layout.updateHeight()

      if (this.isVirtualScroll || this.store.states.isComplex) {
        this.$nextTick(() => {
          // 
          this.bodyWrapperHeight = this.$refs.bodyWrapper
            ? this.$refs.bodyWrapper.clientHeight
            : 1080
          this.store.getTableRowsHeight()
        })
      }
    },
    /**
     * 
     * add by zhangxiaogang 2017-11-28
     * @param {Number} index 0
     */
    getData(index) {
      const d = this.data
      if (typeof index === 'undefined' || isNaN(index)) {
        return d
      }
      else if (index > d.length - 1) {
        // 
        const l = d.length - 1
        const i = index % l
        return d[l - i]
      }
      else {
        return d[index]
      }
    },
    /**
     * indexdata
     * add by zhangxiaogang 2017-11-28
     * @param {Array, Object} data 
     * @param {Number} index 0
     */
    setData(data, index = 0) {
      if (
        !Array.isArray(data)
        && {}.toString.call(data) !== '[object Object]'
      ) {
        throw new Error(
          'table func setDate first arguments type must be Array or Object'
        )
      }
      if ({}.toString.call(data) === '[object Object]')
        data = [data]

      const d = this.data
      const l = d.length - 1
      if (isNaN(index)) {
        index = 0
      }
      else if (index > l) {
        // 
        const i = index % l
        index = l - i
      }
      d.splice(index, data.length, ...data)
    },

    /**
     * 
     */
    setScrollStyle() {
      // max-heightmax-height
      this.$nextTick(() => {
        const scrollWrap = this.$el.querySelector('.el-table-scrollbar__wrap')
        if (!scrollWrap)
          return
        /**
         * 
         * zhangxiaogang  2017-11-7
         */
        if (!this.height && !this.maxHeight && this.niceScroll) {
          // 
          if (this.niceScroll === 'horizontal') {
            const $tableTixed = this.$el.querySelector('.el-table__fixed')
            const $tableTixedRight = this.$el.querySelector(
              '.el-table__fixed-right'
            )
            if ($tableTixed)
              $tableTixed.style.overflow = 'hidden'

            if ($tableTixedRight)
              $tableTixedRight.style.overflow = 'hidden'

            return
          }
          this.$el.style.height = '100%'
          // chrome
          scrollWrap.style.overflow = 'hidden'
          setTimeout(() => {
            scrollWrap.style.overflow = 'visible'
          }, 0)
        }
      })
    },

    fixEmptyScrollViewHeight() {
      this.$nextTick(() => {
        const scrollView = this.$el.querySelector('.el-table-scrollbar__view')
        if (scrollView)
          scrollView.style.height = '100%'
      })
    }
  }
}
</script>

<template>
  <div
    :class="{
      'el-table--fit': fit,
      'el-table--striped': stripe,
      'el-table--border': border,
      'el-table--group': isGroup,
      'el-table--fluid-height': maxHeight,
      'el-table--enable-row-hover': !store.states.isComplex,
      'el-table--enable-row-transition':
        (store.states.data || []).length !== 0 &&
        (store.states.data || []).length < 100,
      'el-table--no-out-border': !outBorder,
      'el-table--out-border': outBorder
    }" class="el-table" @mouseleave="handleMouseLeave($event)"
  >
    <div ref="hiddenColumns" class="hidden-columns">
      <slot />
    </div>
    <div v-if="showHeader" ref="headerWrapper" class="el-table__header-wrapper">
      <table-header :store="store" :default-sort="defaultSort" :style="{ width: bodyWidth }" />
    </div>
    <div ref="bodyWrapper" :style="[bodyHeight]" class="el-table__body-wrapper">
      <el-scrollbar
        v-if="isShowScrollbar" ref="tableScrollbar"
        :overflow="alwaysShowBar || isFixBarScrollShow ? 'scroll' : 'auto'"
        :overflow-x="niceScroll === 'vertical' ? 'hidden' : 'auto'" wrap-class="el-table-scrollbar__wrap"
        view-class="el-table-scrollbar__view" :wrap-style="wrapStyle" :view-style="[{ width: bodyWidth }]"
        @on-scrolling-x="onScrollingX" @on-scrolling-y="onScrollingY"
      >
        <table-body
          ref="tableBody" :context="context" :store="store" :stripe="stripe" :row-class-name="rowClassName"
          :row-style="rowStyle" :highlight="highlightCurrentRow" :show-selected-row-bgcolor="showSelectedRowBgcolor" :style="{ width: bodyWidth }"
        />
        <div v-if="isShowEmptyText" :style="{ width: bodyWidth }" class="el-table__empty-block">
          <span class="el-table__empty-text">
            <slot name="empty">
              {{ emptyText || t('el.table.emptyText') }}
            </slot>
          </span>
        </div>
      </el-scrollbar>
      <template v-else>
        <table-body
          ref="tableBody" :context="context" :store="store" :stripe="stripe" :row-class-name="rowClassName"
          :row-style="rowStyle" :highlight="highlightCurrentRow" :style="{ width: bodyWidth }"
        />
        <div v-if="isShowEmptyText" :style="{ width: bodyWidth }" class="el-table__empty-block">
          <span class="el-table__empty-text">
            <slot name="empty">
              {{ emptyText || t('el.table.emptyText') }}
            </slot>
          </span>
        </div>
      </template>
      <slot v-if="showLoading" name="loading">
        <div v-loading="showLoading" class="el-table__loading" />
      </slot>
    </div>
    <div v-if="showSummary" ref="footerWrapper" class="el-table__footer-wrapper">
      <table-footer
        :store="store" :border="border" :sum-text="sumText || t('el.table.sumText')"
        :summary-method="summaryMethod" :default-sort="defaultSort" :style="{ width: bodyWidth }"
      />
    </div>
    <div
      v-if="fixedColumns.length > 0" ref="fixedWrapper" :style="[{ width: fixedWidth }, fixedHeight]"
      class="el-table__fixed" @mouseenter.stop="isFixBarScrollShow = true"
      @mouseleave.stop="isFixBarScrollShow = false"
    >
      <div v-if="showHeader" ref="fixedHeaderWrapper" class="el-table__fixed-header-wrapper">
        <table-header :store="store" :style="{ width: fixedWidth }" fixed="left" />
      </div>
      <div ref="fixedBodyWrapper" :style="[fixedBodyHeight]" class="el-table__fixed-body-wrapper">
        <table-body
          ref="tableBody" :store="store" :stripe="stripe" :highlight="highlightCurrentRow" :row-class-name="rowClassName"
          :row-style="rowStyle" :style="{ width: fixedWidth }" fixed="left"
        />
      </div>
      <div v-if="showSummary" ref="fixedFooterWrapper" class="el-table__fixed-footer-wrapper">
        <table-footer
          :border="border" :sum-text="sumText || t('el.table.sumText')" :summary-method="summaryMethod"
          :store="store" :style="{ width: fixedWidth }" fixed="left"
        />
      </div>
    </div>
    <div
      v-if="fixedColumns.length > 0" :style="[
        {
          width: fixedWidth,
          height: fixedShadowHeight
        }
      ]" class="el-table__fixed-box-shadow el-table__fixed-box-shadow-left"
    />
    <div
      v-if="rightFixedColumns.length > 0" ref="rightFixedWrapper" :style="[
        { width: rightFixedWidth },
        {
          right: border ? layout.gutterWidth : (layout.gutterWidth || 0) + 'px'
        },
        fixedHeight
      ]" class="el-table__fixed-right" @mouseenter.stop="isFixBarScrollShow = true"
      @mouseleave.stop="isFixBarScrollShow = false"
    >
      <div v-if="showHeader" ref="rightFixedHeaderWrapper" class="el-table__fixed-header-wrapper">
        <table-header :store="store" :style="{ width: rightFixedWidth }" fixed="right" />
      </div>
      <div ref="rightFixedBodyWrapper" :style="[fixedBodyHeight]" class="el-table__fixed-body-wrapper">
        <table-body
          ref="tableBody" :store="store" :stripe="stripe" :row-class-name="rowClassName" :row-style="rowStyle"
          :highlight="highlightCurrentRow" :style="{ width: rightFixedWidth }" fixed="right"
        />
      </div>
      <div v-if="showSummary" ref="rightFixedFooterWrapper" class="el-table__fixed-footer-wrapper">
        <table-footer
          :border="border" :sum-text="sumText || t('el.table.sumText')" :summary-method="summaryMethod"
          :store="store" :style="{ width: rightFixedWidth }" fixed="right"
        />
      </div>
    </div>
    <div
      v-if="rightFixedColumns.length > 0" :style="[
        {
          width: rightFixedWidth,
          height: fixedShadowHeight
        }
      ]" class="el-table__fixed-box-shadow el-table__fixed-box-shadow-right"
    />
    <div
      v-if="rightFixedColumns.length > 0" :style="{ width: '0', height: layout.headerHeight + 'px' }"
      class="el-table__fixed-right-patch"
    />
    <div v-show="resizeProxyVisible" ref="resizeProxy" class="el-table__column-resize-proxy" />
  </div>
</template>
