<script>
import { debounce, emitter, merge } from '@hui/utils'
import { Popover as ElPopover } from '@hui/components/popover'
import calcTextareaHeight from './calcTextareaHeight'

export default {
  name: 'ElInput',

  components: {
    ElPopover,
  },

  mixins: [emitter],

  inheritAttrs: false, // input/textrear

  componentName: 'ElInput',

  props: {
    value: {
      type: [String, Number],
      default: null,
    },
    placeholder: {
      type: String,
      default: null,
    },
    size: {
      type: String,
      default: null,
    },
    kind: {
      type: String,
      default: null,
    },
    resize: {
      type: String,
      default: null,
    },
    readonly: {
      type: Boolean,
      default: null,
    },
    autofocus: {
      type: Boolean,
      default: null,
    },
    icon: {
      type: String,
      default: null,
    },
    unselectable: {
      type: String,
      default: null,
    },
    clearable: {
      type: Boolean,
      default: null,
    },
    select: {
      type: Boolean,
      default: null,
    },
    suffixIcon: {
      type: String,
      default: null,
    },
    prefixIcon: {
      type: String,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: 'text',
    },
    name: {
      type: String,
      default: null,
    },
    count: {
      type: Number,
      default: null,
    },
    autosize: {
      type: [Boolean, Object],
      default: false,
    },
    rows: {
      type: Number,
      default: 2,
    },
    autoComplete: {
      type: String,
      default: 'off',
    },
    form: {
      type: String,
      default: null,
    },
    step: {
      type: null,
      default: null,
    },
    validateEvent: {
      type: Boolean,
      default: true,
    },
    onIconClick: {
      type: Function,
      default: null,
    },
    onMousedownClick: {
      type: Function,
      default: null,
    },
    onMouseupClick: {
      type: Function,
      default: null,
    },
    clearIconClick: {
      type: Function,
      default: null,
    },
    // tipspopover
    tips: {
      type: String,
      default: null,
    },
    // popover
    tipsMaxWidth: {
      type: null,
      default: 480, // tip480
    },
    // popover
    tipsPlacement: {
      type: String,
      default: 'top-start',
    },
    // 
    tipsOffset: {
      type: Number,
      default: 0,
    },
    // popover
    tipsTrigger: {
      type: String,
      default: 'focus',
    },
    // popover
    tipsClass: {
      type: String,
      default: '',
    },
    dataPicker: {
      type: Boolean,
      default: false,
    },
    debounce: {
      type: Number,
      default: 0,
    },
  },

  data() {
    return {
      textareaRect: {},
      textareaCalcStyle: {},
      prefixOffset: null,
      suffixOffset: null,
      tipsDisabled: true,
      ie: {}, // ie
      ie9: false, // ie9
      isFormItem: false, // 
      isFocus: false,
      isHover: false,
      prefixWidth: null,
      suffixWidth: null,
      clearIconOffset: 0, // 
    }
  },

  computed: {
    inputStyle() {
      return {
        paddingLeft: this.prefixWidth ? `${this.prefixWidth + 8}px` : null,
        paddingRight: this.suffixWidth
          ? `${this.suffixWidth + (this.clearable ? 28 : 8)}px`
          : this.clearable
            ? '28px'
            : null,
      }
    },
    validating() {
      return this.$parent.validateState === 'validating'
    },
    textareaStyle() {
      const tempStyle = {
        // resize: this.resize === '' || this.autosize ? 'both' : this.resize,
        resize: (this.resize === '' || this.autosize) && !this.disabled ? 'vertical' : this.resize,
      }
      if (this.clearable && tempStyle)
        tempStyle.paddingRight = '20px'

      return merge({}, this.textareaCalcStyle, tempStyle)
    },
    isGroup() {
      return this.$slots.prepend || this.$slots.append
    },
    valueLength() {
      if (this.value)
        return this.value.length

      return 0
    },
    nativeInputValue() {
      return this.value === null || this.value === undefined ? '' : this.value
    },
    total() {
      const num = this.count - this.valueLength
      if (num < 0)
        return 0

      return this.count - this.valueLength || 0
    },
  },

  watch: {
    value(val) {
      this.$nextTick(this.resizeTextarea)
      if (this.validateEvent)
        this.dispatch('ElFormItem', 'el.form.change', [val])
    },
    nativeInputValue: {
      handler(val) {
        if (this.type === 'textarea') {
          this.$nextTick(() => {
            const refTextarea = this.$refs.textarea
            if (refTextarea && refTextarea.scrollHeight > refTextarea.clientHeight)
              this.clearIconOffset = 12
            else
              this.clearIconOffset = 0
          })
        }
      },
      immediate: true
    }
  },

  created() {
    this.$on('inputSelect', this.inputSelect)
    this.debouncedOnInputChange
      = this.debounce > 0
        ? debounce(this.debounce, (e) => {
          this.handleInput(e)
        })
        : this.handleInput
  },

  mounted() {
    if (this.type === 'textarea') {
      this.textareaRect = this.$refs.wrapper
        ? this.$refs.wrapper.getBoundingClientRect()
        : {}
      this.resizeTextarea()
    }
    if (this.$refs.prefix)
      this.prefixWidth = this.$refs.prefix.getBoundingClientRect().width

    if (this.$refs.suffix)
      this.suffixWidth = this.$refs.suffix.getBoundingClientRect().width

    if (this.isGroup) {
      this.prefixOffset = this.calcIconOffset('pre')
      this.suffixOffset = this.calcIconOffset('suf')
    }
    // ie9
    if (
      navigator.appName === 'Microsoft Internet Explorer'
      && navigator.appVersion.split(';')[1].replace(/[ ]/g, '') === 'MSIE9.0'
      && !this.dataPicker
    )
      this.ie9 = true

    // switch beforeChange  placeholder 
    // if (this.placeholder === '') {
    //   console.error('inputplaceholder');
    // }
    this.isFormItem = !!this.findComponentUpward(this, 'ElFormItem')
  },

  methods: {
    /**
     * @author xuzilong
     * @desc 
     */
    findComponentUpward(context, componentName) {
      let parent = context.$parent
      let name = parent.$options.name

      while (parent && (!name || ![componentName].includes(name))) {
        parent = parent.$parent
        if (parent)
          name = parent.$options.name
      }
      return parent
    },
    handleBlur(event) {
      this.isFocus = false
      this.$emit('blur', event)
      if (this.validateEvent)
        this.dispatch('ElFormItem', 'el.form.blur', [this.value])

      if (this.tipsTrigger === 'focus')
        this.tipsDisabled = true

      if (this.ie9)
        clearTimeout(this.ie) // ie9
        // document.removeEventListener('selectionchange', this.handleInput, false);
    },
    inputSelect() {
      // TODO 
      this.$refs.input.select()
    },
    resizeTextarea() {
      if (this.$isServer)
        return
      const { autosize, type } = this
      if (!autosize || type !== 'textarea')
        return
      const minRows = autosize.minRows
      const maxRows = autosize.maxRows
      const maxWidth = autosize.maxWidth

      this.textareaCalcStyle = calcTextareaHeight(
        this.$refs.textarea,
        this.textareaRect,
        minRows,
        maxRows,
        maxWidth,
      )
    },
    handleMouseenter() {
      this.isHover = true
    },
    handleMouseout() {
      this.isHover = false
    },
    handleFocus(event) {
      if (this.readonly)
        event.preventDefault()

      this.isFocus = true
      this.$emit('focus', event)
      if (this.tips && this.tipsTrigger === 'focus')
        this.tipsDisabled = false

      if (this.ie9)
        this.ie = setInterval(this.handleInput, 200) // ie9
        // document.addEventListener('selectionchange', this.handleInput, false);
    },
    handleInput(event) {
      const value = event.target.value
      if (value === this.nativeInputValue)
        return
      this.$emit('input', value)
      this.$emit('change', value)
    },
    handleIconClick(event) {
      if (this.onIconClick)
        this.onIconClick(event)

      this.$emit('click', event)
    },
    handleMousedownClick(event) {
      if (this.onMousedownClick)
        this.onMousedownClick(event)
    },
    handleMouseupClick(event) {
      if (this.onMouseupClick)
        this.onMouseupClick(event)
    },
    handleClearClick(event) {
      if (!this.disabled) {
        if (this.clearIconClick)
          this.clearIconClick(event)

        this.deleteSelected(event)
        this.$emit('delete')
        this.$nextTick(() => {
          this.$refs[this.type === 'textarea' ? 'textarea' : 'input'].focus()
        })
      }
    },
    deleteSelected(event) {
      event.stopPropagation()
      this.$emit('input', '')
      this.$emit('clear')
    },
    calcIconOffset(place) {
      const pendantMap = {
        suf: 'append',
        pre: 'prepend',
      }

      const pendant = pendantMap[place]
      if (this.$slots[pendant]) {
        return {
          transform: `translateX(${place === 'suf' ? '-' : ''}${this.$el.querySelector(`.el-input-group__${pendant}`).offsetWidth
            }px)`,
        }
      }
    },
    slotBorderHide(slot) {
      // ElSelectElSelect
      // TODO: select  button 
      if (slot && slot.length === 1 && slot[0].tag && (slot[0].tag.split('-').pop() === 'ElSelect' || slot[0].tag.split('-').pop() === 'ElButton'))
        return true
      else
        return false
    }
  },
}
</script>
<template>
  <div
    ref="wrapper" :class="[
      type === 'textarea' ? 'el-textarea' : 'el-input',
      kind ? 'el-input--' + kind : '',
      size ? 'el-input--' + size : '',
      {
        'is-disabled': disabled,
        'is-count': count,
        'el-input-group': $slots.prepend || $slots.append,
        'el-input-group--append': $slots.append,
        'el-input-group--prepend': $slots.prepend,
        'el-input--prefix': $slots.prefix || prefixIcon,
        'el-input--suffix': $slots.suffix || suffixIcon,
        'is-hover': isHover,
        'is-focus': isFocus,
        'is-clearable': clearable && value && (String(value).length || select)
      }
    ]"
  >
    <template v-if="type !== 'textarea'">
      <!--  -->
      <div
        v-if="$slots.prepend" :class="['el-input-group__prepend',{
          'no-border':slotBorderHide($slots.prepend)
        }]"
      >
        <slot name="prepend" />
      </div>
      <!-- input  -->
      <slot name="icon">
        <i
          v-if="icon" :class="[
            icon,
            onIconClick || onMousedownClick || onMouseupClick
              ? 'is-clickable'
              : ''
          ]" :style="suffixOffset" class="el-input__icon" @click="handleIconClick" @mousedown="handleMousedownClick"
          @mouseup="handleMouseupClick"
        />
      </slot>
      <!-- right: $slots.suffix || suffixIcon ? 28 + 'px' : 4 + 'px', -->
      <i
        v-if="clearable && value && (String(value).length || select)" :style="{
          right: '4px',
          transform: suffixOffset ? suffixOffset.transform : 0,
          zIndex: 10
        }" class="el-input__icon h-icon-close_f is-clickable" @click="handleClearClick"
      />
      <template v-if="tips">
        <!-- input -->
        <el-popover
          ref="popoverInput" :content="tips" :max-width="tipsMaxWidth" :placement="tipsPlacement"
          :trigger="tipsTrigger" :disabled="tipsDisabled" :show-html="true" :offset-placement="tipsOffset"
          :popper-class="
            `el-popover__for-input ${tipsClass} el-popover__for-input-form`
          " effect="light"
        >
          <input
            ref="input" slot="reference" v-bind="[$props, $attrs]" :autocomplete="autoComplete"
            :value="nativeInputValue" :style="inputStyle" class="el-input__inner" @input="debouncedOnInputChange"
            @focus="handleFocus" @blur="handleBlur"
          >
        </el-popover>
      </template>
      <template v-else>
        <input
          ref="input" v-bind="[$props, $attrs]" :autocomplete="autoComplete" :value="nativeInputValue"
          :style="inputStyle" class="el-input__inner" @input="debouncedOnInputChange" @focus="handleFocus"
          @blur="handleBlur"
        >
      </template>
      <!--  -->
      <span v-if="$slots.prefix || prefixIcon" ref="prefix" :style="prefixOffset" class="el-input__prefix">
        <span class="el-input__prefix-inner">
          <template>
            <slot name="prefix" />
            <i v-if="prefixIcon" :class="prefixIcon" class="el-input__icon" @click="handleIconClick" />
          </template>
        </span>
      </span>
      <!--  -->
      <span v-if="$slots.suffix || suffixIcon" ref="suffix" :style="suffixOffset" class="el-input__suffix">
        <span class="el-input__suffix-inner">
          <template>
            <slot name="suffix" />
            <i v-if="suffixIcon" :class="suffixIcon" class="el-input__icon" @click="handleIconClick" />
          </template>
        </span>
      </span>
      <i v-if="validating" class="el-input__icon el-validate__icon el-icon-restart" />
      <!--  -->
      <div
        v-if="$slots.append" :class="['el-input-group__append',{
          'no-border':slotBorderHide($slots.append)
        }]"
      >
        <slot name="append" />
      </div>
      <!-- input-number -->
      <template v-if="$slots.inputNumber">
        <slot name="inputNumber" />
      </template>
    </template>
    <template v-else>
      <template v-if="tips">
        <!-- input -->
        <el-popover
          ref="popoverTextarea" :content="tips" :max-width="tipsMaxWidth" :placement="tipsPlacement"
          :trigger="tipsTrigger" :disabled="tipsDisabled" :show-html="true" :offset-placement="tipsOffset"
          :popper-class="
            `el-popover__for-input ${tipsClass} el-popover__for-input-form`
          " effect="light"
        >
          <textarea
            ref="textarea" slot="reference" v-bind="[$props, $attrs]" :value="nativeInputValue"
            :style="textareaStyle" class="el-textarea__inner" @input="debouncedOnInputChange"
            @mouseenter="handleMouseenter" @mouseout="handleMouseout" @focus="handleFocus" @blur="handleBlur"
          />
        </el-popover>
        <span v-if="count" class="el-textarea__count" v-text="total" />
        <i
          v-if="clearable && value && (String(value).length || select)" :style="{
            right: $slots.suffix || suffixIcon ? (28+clearIconOffset) + 'px' : (4+clearIconOffset) + 'px',
            transform: suffixOffset ? suffixOffset.transform : 0,
            zIndex: 1
          }" class="el-input__icon h-icon-close_f is-clickable" @click="handleClearClick"
        />
      </template>
      <template v-else>
        <textarea
          ref="textarea" v-bind="[$props, $attrs]" :value="nativeInputValue" :style="textareaStyle"
          class="el-textarea__inner" @mouseenter="handleMouseenter" @mouseout="handleMouseout"
          @input="debouncedOnInputChange" @focus="handleFocus" @blur="handleBlur"
        />
        <span v-if="count" class="el-textarea__count" v-text="total" />
        <i
          v-if="clearable && value && (String(value).length || select)" :style="{
            right: (4+clearIconOffset)+'px',
            zIndex: 1
          }" class="el-input__icon h-icon-close_f is-clickable" @click="handleClearClick"
        />
      </template>
    </template>
  </div>
</template>
