<script>
import { getOffset, off, on } from '@hui/utils'
import { Affix as HAffix } from '@hui/components/affix'
import { findComponentsDownward, scrollTop, sharpMatcherRegx } from './util'

export default {
  name: 'HAnchor',
  components: {
    HAffix,
  },
  provide() {
    return {
      anchor: this,
    }
  },
  props: {
    affix: {
      /*  */
      type: Boolean,
      default: true,
    },
    height: {
      type: [Number, String],
      default: '',
    },
    offsetTop: {
      /*  */
      type: Number,
      default: 0,
    },
    offsetBottom: {
      type: Number,
      default: -1,
    } /*  */,
    bounds: {
      /* px */
      type: Number,
      default: 5,
    },
    scrollOffset: {
      /*  */
      type: Number,
      default: 0,
    },
    container: {
      type: [String, Object],
      default: '',
    } /*  */,
    showInk: {
      /*  */
      type: Boolean,
      default: false,
    },
    folded: {
      /*  */
      type: Boolean,
      default: false,
    },
    foldedScrollTime: {
      /*  */
      type: Number,
      default: 1000,
    },
    size: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      inkActived: false, // 
      inkTop: 0, // top
      animating: false, // 
      currentLink: '#', // current show link =>  #href -> currentLink = #href
      currentId: '', // current show title id =>  #href -> currentId = href
      scrollContainer: null, //  window 
      scrollElement: null, //  document.documentElement || document.body
      titlesOffsetArr: [], // 
      wrapperTop: 0,
      scrollTop: 0,
      isFolded: false,
      isMouseEnter: false,
      timer: null,
      childWidth: null,
      minWidth: 120,
      scrollTimer: null,
      // upperFirstTitle: true
    }
  },
  computed: {
    //  Affix 
    wrapperComponent() {
      return this.affix ? 'h-affix' : 'div'
    },
    // height
    scrollComponent() {
      return this.height && !(this.folded && this.isFolded)
        ? 'el-scrollbar'
        : 'div'
    },
    // 
    wrapperStyle() {
      const isFolded = this.folded && this.isFolded
      const isFoldedStyle = {
        'padding': '0',
        'box-shadow': 'none',
        'background-color': 'transparent',
      }
      return Object.assign(
        {
          'height': this.height ? `${this.height}px` : 'inherit',
          'maxHeight': this.offsetTop
            ? `calc(100vh - ${this.offsetTop}px)`
            : '100vh',
          'min-width': `${this.minWidth}px`,
        },
        isFolded ? isFoldedStyle : {},
      )
    },
    //  window
    containerIsWindow() {
      return this.scrollContainer === window
    },
  },
  watch: {
    $route() {
      this.handleHashChange()
      this.$nextTick(() => {
        this.handleScrollTo()
      })
    },
    container() {
      this.init()
    },
    currentLink(newHref, oldHref) {
      this.$emit('on-change', newHref, oldHref)
    },
    isFolded: {
      handler(val) {
        if (!val) {
          this.$nextTick(() => {
            const offsetWidth = this.$refs.anchor.offsetWidth
            this.minWidth = offsetWidth > 120 ? offsetWidth : 120
          })
        }
      },
      immediate: true,
    },
  },
  mounted() {
    this.init()
  },
  beforeDestroy() {
    this.removeListener()
  },
  methods: {
    handleMouseenter(e) {
      this.isFolded = false
      this.isMouseEnter = true
      this.scrollTop
        = document.documentElement.scrollTop
        || document.body.scrollTop
        || e.target.scrollTop
      clearTimeout(this.timer)
    },
    handleMouseleave() {
      this.isMouseEnter = false
      this.isFolded = true
    },
    /**
     * @desc 
     * @author chenguanbin
     * @param {Event} e
     */
    handleScroll(e) {
      if (this.animating)
        return

      this.updateTitleOffset()
      const scrollTop
        = document.documentElement.scrollTop
        || document.body.scrollTop
        || e.target.scrollTop
      this.getCurrentScrollAtTitleId(scrollTop)

      if (this.scrollTop !== scrollTop) {
        clearTimeout(this.timer)
        this.isFolded = false
        this.timer = setTimeout(() => {
          !this.isMouseEnter && (this.isFolded = true)
        }, this.foldedScrollTime)
      }

      this.scrollTop = scrollTop
    },
    /**
     * @desc 
     * @author chenguanbin
     */
    handleHashChange() {
      const url = window.location.href
      const sharpLinkMatch = sharpMatcherRegx.exec(url)
      if (!sharpLinkMatch)
        return
      this.currentLink = sharpLinkMatch[0]
      this.currentId = sharpLinkMatch[1]
    },
    /**
     * @desc 
     * @author chenguanbin
     */
    handleScrollTo() {
      const anchor = document.getElementById(this.currentId)
      const currentLinkElementA = document.querySelector(
        `a[data-href="${this.currentLink}"]`,
      )
      let offset = this.scrollOffset
      if (currentLinkElementA) {
        offset = parseFloat(
          currentLinkElementA.getAttribute('data-scroll-offset'),
        )
      }
      if (!anchor)
        return
      const offsetTop
        = getOffset(
          anchor,
          this.scrollContainer === window ? null : this.scrollContainer,
        ).top
        - this.wrapperTop
        - offset
      this.animating = true
      scrollTop(
        this.scrollContainer,
        this.scrollElement.scrollTop,
        offsetTop,
        600,
        () => {
          this.animating = false
        },
      )
      this.handleSetInkTop()
    },
    /**
     * @desc  top 
     * @author chenguanbin
     */
    handleSetInkTop() {
      let currentLinkElementA = document.querySelector(
        `a[data-href="${this.currentLink}"]`,
      )
      if (!currentLinkElementA) {
        const currentChild = this.titlesOffsetArr.find(item => item.active)
        if (!currentChild) {
          this.inkActived = false
          return
        }
        else {
          currentLinkElementA = document.querySelector(
            `a[data-href="${currentChild.link}"]`,
          )
        }
      }
      this.inkActived = true

      this.$nextTick(() => {
        const elementATop = getOffset(currentLinkElementA, this.$refs.anchor)
          .top
        // const top = elementATop < 0 ? this.offsetTop : elementATop;
        // this.inkTop = top;
        this.inkTop = Math.abs(elementATop)
      })
    },
    /**
     * @desc 
     * @author chenguanbin
     */
    updateTitleOffset() {
      const links = findComponentsDownward(this, [
        'HAnchorLink',
        'HSubanchor',
      ]).map((link) => {
        return {
          href: link.href,
          active: link.active,
        }
      })
      const offsetArr = []
      for (const link of links) {
        const id = link.href.split('#')[1]
        const titleEle = document.getElementById(id)
        if (titleEle) {
          offsetArr.push({
            link: `#${id}`,
            offset:
              getOffset(
                titleEle,
                this.scrollContainer === window ? null : this.scrollContainer,
              ).top - this.scrollElement.offsetTop,
            active: link.active,
          })
        }
      }
      this.titlesOffsetArr = offsetArr
      this.handleSetScroll()
    },
    /**
     * @author xuzilong
     * @desc 
     */
    handleSetScroll() {
      if (!this.height)
        return

      const currentLinkElementA = document.querySelector(
        `a[data-href="${this.currentLink}"]`,
      )
      if (!currentLinkElementA)
        return
      const offset = getOffset(currentLinkElementA, this.$refs.anchor)

      // 
      clearTimeout(this.scrollTimer)
      this.scrollTimer = setTimeout(() => {
        this.$refs.scrollbar.setScroll(offset.top)
      }, 100)
    },
    /**
     * @desc 
     * @author chenguanbin
     * @param {Number} scrollTop 
     */
    getCurrentScrollAtTitleId(scrollTop) {
      let i = -1
      const len = this.titlesOffsetArr.length
      let titleItem = {
        link: '#',
        offset: 0,
      }
      scrollTop += this.bounds
      while (++i < len) {
        const currentEle = this.titlesOffsetArr[i]
        const nextEle = this.titlesOffsetArr[i + 1]
        if (
          scrollTop >= currentEle.offset
          && scrollTop < ((nextEle && nextEle.offset) || Infinity)
        ) {
          titleItem = this.titlesOffsetArr[i]
          break
        }
      }
      this.currentLink = titleItem.link
      this.handleSetInkTop()
    },
    /**
     * @desc 
     *        windowdocument.documentElement || document.body container container
     * @author chenguanbin
     */
    getContainer() {
      this.scrollContainer = this.container
        ? typeof this.container === 'string'
          ? document.querySelector(this.container)
          : this.container
        : window
      this.scrollElement = this.container
        ? this.scrollContainer
        : document.documentElement || document.body
    },
    /**
     * @desc 
     * @author chenguanbin
     */
    removeListener() {
      off(this.scrollContainer, 'scroll', this.handleScroll)
      off(window, 'hashchange', this.handleHashChange)
    },
    /**
     * @desc 
     * @author chenguanbin
     */
    init() {
      this.handleHashChange()
      this.$nextTick(() => {
        this.removeListener()
        this.getContainer()
        this.wrapperTop = this.containerIsWindow
          ? 0
          : this.scrollElement.offsetTop
        this.updateTitleOffset()
        this.handleScrollTo()
        this.handleSetInkTop()
        on(this.scrollContainer, 'scroll', this.handleScroll)
        on(window, 'hashchange', this.handleHashChange)
      })
    },
  },
}
</script>
<template>
  <component
    :is="wrapperComponent" ref="affix" :container="container" :offset-top="offsetTop"
    :offset-bottom="offsetBottom" style="text-align: right;"
  >
    <div
      :class="{ folded }" :style="wrapperStyle" class="h-anchor-wrapper" @mouseenter="handleMouseenter"
      @mouseleave="handleMouseleave"
    >
      <component
        :is="scrollComponent" ref="scrollbar" wrap-class="el-anchor-scrollbar__wrap"
        view-class="el-anchor-scrollbar__view"
      >
        <div ref="anchor" :class="['h-anchor',size ? 'h-anchor--' + size : '', folded && isFolded ? 'is-folded' : '']">
          <div v-show="!folded || !isFolded">
            <slot />
            <!-- <span
              v-show="showInk && inkActived" :style="{ top: `${inkTop + 8}px`, left: '15px' }"
              class="h-anchor__ink-ball"
            /> -->
          </div>
          <i v-show="folded && isFolded" class="h-anchor-icon h-icon-ctrl_anchor" />
        </div>
      </component>
    </div>
  </component>
</template>
