<script>
export default {
  name: 'ElDropdownItemGroup',

  componentName: 'ElDropdownItemGroup',

  inject: ['dropdown'],
  props: {
    title: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      paddingLeft: 10
    }
  },
  computed: {
    levelPadding() {
      let padding = 10
      let subPadding = 26 // 
      let parent = this.$parent

      // 
      if (!this.icon) {
        padding = 22
        subPadding = 0
      }

      if (this.dropdown.collapse)
        return 16
      while (parent && parent.$options.componentName !== 'ElMenu') {
        if (parent.$options.componentName === 'ElSubmenu')
          padding += 10

        parent = parent.$parent
        // 
        if (parent && parent.$options.componentName === 'ElMenu')
          padding += subPadding
      }
      padding === 10 && (padding = 10)
      return padding
    }
  }
}
</script>
<template>
  <ul class="el-dropdown-item-group__wrap">
    <li class="el-dropdown-item-group__title">
      {{ title }}
    </li>
    <li>
      <ul class="el-dropdown-item-group">
        <slot />
      </ul>
    </li>
  </ul>
</template>
