<script>
export default {
  name: 'ElTimelineItem',

  inject: ['timeline'],

  props: {
    timestamp: {
      type: String,
      default: '',
    },

    hideTimestamp: {
      type: Boolean,
      default: false,
    },

    placement: {
      type: String,
      default: '',
    },

    type: {
      type: String,
      default: '',
    },

    color: {
      type: String,
      default: '',
    },

    size: {
      type: String,
      default: 'normal',
    },

    icon: {
      type: String,
      default: '',
    },
    position: {
      type: String,
      default: '',
    },
    cursor: {
      type: Boolean,
      default: false,
    },
    dotClass: {
      type: String,
      default: '',
    },
  },
  computed: {
    showElAndModifier() {
      const mode = this.timeline.mode
      // showWrapperLeft true:false:
      // leftWrapperModifier  center right
      // timeShowLeft // crossedcrossed-custom
      // tailModifier // ,right: center:
      // dotModifier // right: center:
      // showWrapperRight //  true:false:
      // rightWrapperModifier//  center customcrossed-custom
      // timePlacement// 
      // crossed // crossed-
      // isCustom crossed-custom
      switch (mode) {
        case 'left-top':

          return {
            showWrapperLeft: false,
            showWrapperRight: true,
            dotModifier: '',
            timePlacement: this.placement || 'top',
          }
        case 'left-bottom':
          return {
            showWrapperLeft: false,
            showWrapperRight: true,
            dotModifier: '',
            timePlacement: this.placement || 'bottom',
          }
        case 'right-top':
          return {
            showWrapperLeft: true,
            leftWrapperModifier: 'right',
            tailModifier: 'right',
            dotModifier: 'Right',
            showWrapperRight: false,
            timePlacement: this.placement || 'top',
          }
        case 'right-bottom':
          return {
            showWrapperLeft: true,
            leftWrapperModifier: 'right',
            tailModifier: 'right',
            dotModifier: 'Right',
            showWrapperRight: false,
            timePlacement: this.placement || 'bottom',
          }
        case 'crossed-time-left':
          return {
            crossed: true,
            showWrapperLeft: true,
            leftWrapperModifier: 'center',
            tailModifier: 'center',
            dotModifier: 'Center',
            showWrapperRight: true,
            rightWrapperModifier: 'center',
            timePlacement: 'top',
            timeShowLeft: true,
          }
        case 'crossed-time-right':
          return {
            crossed: true,
            showWrapperLeft: true,
            leftWrapperModifier: 'center',
            tailModifier: 'center',
            dotModifier: 'Center',
            showWrapperRight: true,
            rightWrapperModifier: 'center',
            timePlacement: 'top',
            timeShowLeft: false,
          }
        case 'crossed-time-alternate': {
          const index = this.timeline.timeLines.indexOf(this)
          if (index % 2 === 0) {
            return {
              crossed: true,
              showWrapperLeft: true,
              leftWrapperModifier: 'center',
              tailModifier: 'center',
              dotModifier: 'Center',
              showWrapperRight: true,
              rightWrapperModifier: 'center',
              timePlacement: 'top',
              timeShowLeft: true,
            }
          }
          else {
            return {
              crossed: true,
              showWrapperLeft: true,
              leftWrapperModifier: 'center',
              tailModifier: 'center',
              dotModifier: 'Center',
              showWrapperRight: true,
              rightWrapperModifier: 'center',
              timePlacement: 'top',
              timeShowLeft: false,
            }
          }
        }
        case 'crossed-custom':
          if (this.position === 'right') {
            return {
              crossed: true,
              tailModifier: 'center',
              dotModifier: 'Center',
              showWrapperRight: true,
              rightWrapperModifier: 'custom',
              timePlacement: this.placement || 'top',
              isCustom: true,
            }
          }
          else {
            return {
              crossed: true,
              showWrapperLeft: true,
              leftWrapperModifier: 'center',
              tailModifier: 'center',
              dotModifier: 'Center',
              timePlacement: this.placement || 'top',
              isCustom: true,
            }
          }
        default:
          return {
            showWrapperLeft: false,
            showWrapperRight: true,
            dotModifier: '',
            timePlacement: this.placement || 'top',
          }
      }
    },
    showLeftWrapperTopTime() {
      const { timePlacement, crossed, timeShowLeft, isCustom }
        = this.showElAndModifier
      if (this.timeline.mode === 'crossed-time-left')
        return true

      if (!this.hideTimestamp && timePlacement === 'top' && this.timestamp) {
        if (!crossed || (crossed && (timeShowLeft || isCustom)))
          return true

        return false
      }
      return false
    },
    showLeftWrapperBottomTime() {
      const { timePlacement, crossed, isCustom } = this.showElAndModifier
      if (!this.hideTimestamp && timePlacement === 'bottom' && this.timestamp) {
        if (!crossed || (crossed && isCustom))
          return true

        return false
      }
      return false
    },
    showRightWrapperTopTime() {
      const { timePlacement, crossed, timeShowLeft } = this.showElAndModifier
      if (!this.hideTimestamp && timePlacement === 'top' && this.timestamp) {
        if (!crossed || (crossed && !timeShowLeft))
          return true

        return false
      }
      return false
    },
  },
  created() {
    this.timeline.timeLines.push(this)
  },
  methods: {
    dotClick() {
      this.$emit('dotClick')
    },
  },
}
</script>

<template>
  <li class="el-timeline-item">
    <div
      v-if="showElAndModifier.showWrapperLeft"
      class="el-timeline-item__leftWrapper"
      :class="[
        `el-timeline-item__leftWrapper--${
          showElAndModifier.leftWrapperModifier || ''
        }`,
      ]"
    >
      <div
        v-if="showLeftWrapperTopTime"
        class="el-timeline-item__timestamp is-top"
        :class="[!(!showElAndModifier.crossed ||
          (showElAndModifier.crossed && !showElAndModifier.timeShowLeft)) ? 'is-only-timestamp': '']"
      >
        {{ !hideTimestamp ? timestamp : "" }}
      </div>

      <div
        v-if="
          !showElAndModifier.crossed ||
            (showElAndModifier.crossed && !showElAndModifier.timeShowLeft)
        "
        class="el-timeline-item__content"
      >
        <slot />
      </div>

      <div
        v-if="showLeftWrapperBottomTime"
        class="el-timeline-item__timestamp is-bottom"
      >
        {{ timestamp }}
      </div>
    </div>

    <div
      class="el-timeline-item__tail"
      :class="[
        `el-timeline-item__tail--${showElAndModifier.tailModifier || ''}`,
      ]"
    />

    <div
      v-if="!$slots.dot"
      class="el-timeline-item__node"
      :class="[
        `el-timeline-item__node--${size || ''}${showElAndModifier.dotModifier}`,
        `el-timeline-item__node--${type || ''}`,
      ]"
      :style="{
        backgroundColor: color,
        cursor: cursor ? 'pointer' : '',
      }"
      @click="dotClick"
    >
      <i v-if="icon" class="el-timeline-item__icon" :class="icon" />
    </div>
    <div
      v-if="$slots.dot"
      class="el-timeline-item__dot"
      :class="[
        `el-timeline-item__dot--${showElAndModifier.dotModifier}`,
        dotClass,
      ]"
      @click="dotClick"
    >
      <slot name="dot" />
    </div>

    <div
      v-if="showElAndModifier.showWrapperRight"
      class="el-timeline-item__wrapper"
      :class="[
        `el-timeline-item__wrapper--${
          showElAndModifier.rightWrapperModifier || ''
        }`,
      ]"
    >
      <div
        v-if="showRightWrapperTopTime"
        class="el-timeline-item__timestamp is-top"
        :class="[!(!showElAndModifier.crossed ||
          (showElAndModifier.crossed &&
            (showElAndModifier.timeShowLeft || showElAndModifier.isCustom)))?'is-only-timestamp':'']"
      >
        {{ timestamp }}
      </div>

      <div
        v-if="
          !showElAndModifier.crossed ||
            (showElAndModifier.crossed &&
              (showElAndModifier.timeShowLeft || showElAndModifier.isCustom))
        "
        class="el-timeline-item__content"
      >
        <slot />
      </div>

      <div
        v-if="
          !hideTimestamp &&
            showElAndModifier.timePlacement === 'bottom' &&
            timestamp
        "
        class="el-timeline-item__timestamp is-bottom"
      >
        {{ timestamp }}
      </div>
    </div>
  </li>
</template>
