<script>
import { Tooltip as ElTooltip } from '@hui/components/tooltip'
import {
  addResizeListener,
  emitter,
  removeResizeListener
} from '@hui/utils'
import Nav from './nav-mixin'

export default {
  name: 'ElNavItem',

  componentName: 'ElNavItem',

  components: {
    ElTooltip
  },

  mixins: [Nav, emitter],
  props: {
    index: {
      type: String,
      required: true
    },
    route: {
      type: Object,
      required: false,
      default: null
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    },
    icon: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      isIe: false,
      textEllipsis: false
    }
  },
  computed: {
    active() {
      return this.index === this.rootNav.activeIndex
    }
  },
  created() {
    this.parentNav.addItem(this)
    this.rootNav.addItem(this)
  },
  mounted() {
    addResizeListener(this.$el, this.setTextEllipsis)
    if (!!window.ActiveXObject || 'ActiveXObject' in window)
      this.isIe = true
  },
  beforeDestroy() {
    this.parentNav.removeItem(this)
    this.rootNav.removeItem(this)
    removeResizeListener(this.$el, this.setTextEllipsis)
  },
  methods: {
    handleClick() {
      if (!this.disabled) {
        this.dispatch('ElNav', 'item-click', this)
        this.$emit('click', this)
      }
    },
    setTextEllipsis() {
      const $text = this.$el.querySelector('.el-nav-item--text')
      if (!$text || !$text.parentNode)
        return
      const gutter = 60 // 
      this.textEllipsis
        = $text.offsetWidth > $text.parentNode.offsetWidth - gutter
    }
  }
}
</script>
<template>
  <li
    :style="paddingStyle"
    :class="{
      'is-active': active,
      'is-disabled': disabled
    }"
    :title="
      rootNav.mode === 'vertical' &&
        !rootNav.collapse &&
        !disabled &&
        (isIe || textEllipsis) &&
        $slots.default &&
        $slots.default[0].text
    "
    class="el-nav-item"
    @click="handleClick"
  >
    <el-tooltip
      v-if="parentNav === rootNav && rootNav.collapse"
      effect="dark"
      placement="right"
      popper-class="tooltip-for-nav-item"
    >
      <div slot="content">
        {{ $slots.default[0].text }}
      </div>
      <div class="el-nav-item__collapse">
        <i v-if="$slots.icon" class="el-nav-icon is-slot">
          <slot name="icon" />
        </i>
        <i v-else-if="icon" :class="icon" class="el-nav-icon" />
        <span class="el-nav-item--text">
          <slot />
        </span>
      </div>
    </el-tooltip>
    <template v-else>
      <i v-if="$slots.icon" class="el-nav-icon is-slot">
        <slot name="icon" />
      </i>
      <i v-else-if="icon" :class="icon" class="el-nav-icon" />
      <span class="el-nav-item--text">
        <slot />
      </span>
    </template>
  </li>
</template>
