import { on, once } from '../dom'

export default {
  bind(el, binding, vnode) {
    let interval = null
    let startTime
    // handlersetIntervalhandlerhandler
    let executed = false
    const handler = () => {
      vnode.context[binding.expression].apply()
      executed = true
    }
    const clear = () => {
      clearInterval(interval)
      interval = null
      if (new Date() - startTime < 100 || !executed)
        handler()
    }

    on(el, 'mousedown', (e) => {
      if (e.button !== 0)
        return
      executed = false
      startTime = new Date()
      once(document, 'mouseup', clear)
      clearInterval(interval)
      interval = setInterval(handler, 100)
    })
  },
}
