<script>
export default {
  name: 'ElDivider',
  props: {
    direction: {
      type: String,
      default: 'horizontal',
      validator(val) {
        return ['horizontal', 'vertical'].includes(val)
      }
    },
    position: {
      type: String,
      default: 'center',
      validator(val) {
        return ['left', 'center', 'right'].includes(val)
      }
    },
    borderStyle: {
      type: String,
      default: 'solid'
    }
  }
}
</script>

<template>
  <div
    :class="['el-divider', `el-divider--${direction}`]"
    :style="`border-style: ${borderStyle}`"
  >
    <div
      v-if="$slots.default && direction !== 'vertical'"
      :class="['el-divider__text', `is-${position}`]"
    >
      <slot />
    </div>
  </div>
</template>
