<script>
import { CollapseTransition as ElCollapseTransition } from '@hui/components/collapse'
import {
  addResizeListener,
  closest,
  emitter,
  removeResizeListener
} from '@hui/utils'
import menuMixin from './menu-mixin'

const HIDE_TIME = 20

export default {
  name: 'ElSubmenu',

  componentName: 'ElSubmenu',

  components: { ElCollapseTransition },

  mixins: [menuMixin, emitter],

  props: {
    index: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: null
    },
    isBgFill: {
      type: Boolean,
      default: false
    },
    description: {
      type: String,
      default: null
    },
    divided: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    return {
      isIe: false,
      timeout: null,
      items: {},
      submenus: {},
      collapseEl: null, // 
      textEllipsis: false,
      level: 1,
      hideTimer: null
    }
  },

  computed: {
    menuTransitionName() {
      return this.rootMenu.collapse ? 'el-zoom-in-left' : 'el-zoom-in-top'
    },
    opened() {
      return this.rootMenu.openedMenus.includes(this.index)
    },
    active: {
      cache: false,
      get() {
        let isActive = false
        const submenus = this.submenus
        const items = this.items

        Object.keys(items).forEach((index) => {
          if (items[index].active)
            isActive = true
        })

        Object.keys(submenus).forEach((index) => {
          if (submenus[index].active)
            isActive = true
        })

        return isActive
      }
    }
  },

  watch: {
    opened(val) {
      const { rootMenu } = this
      if (rootMenu.menuTrigger === 'hover' && rootMenu.mode === 'horizontal') {
        const { index, indexPath } = this
        val
          ? rootMenu.$emit('open', index, indexPath)
          : rootMenu.$emit('close', index, indexPath)
      }
    }
  },

  created() {
    // 
    const $subMenu = this.rootMenu.wrapScroll
      ? this.$parent.$parent
      : this.$parent
    // level1
    this.level = $subMenu.level ? $subMenu.level + 1 : 1
    this.parentMenu.addSubmenu(this)
    this.rootMenu.addSubmenu(this)
  },
  mounted() {
    addResizeListener(this.$el, this.setTextEllipsis)
    if (!!window.ActiveXObject || 'ActiveXObject' in window)
      this.isIe = true
  },
  beforeDestroy() {
    this.parentMenu.removeSubmenu(this)
    this.rootMenu.removeSubmenu(this)
    removeResizeListener(this.$el, this.setTextEllipsis)
  },
  methods: {
    addItem(item) {
      this.$set(this.items, item.index, item)
    },
    removeItem(item) {
      delete this.items[item.index]
    },
    addSubmenu(item) {
      this.$set(this.submenus, item.index, item)
    },
    removeSubmenu(item) {
      delete this.submenus[item.index]
    },
    handleClick() {
      const { rootMenu } = this
      if (
        (rootMenu.menuTrigger === 'hover' && rootMenu.mode === 'horizontal')
        || (rootMenu.collapse && rootMenu.mode === 'vertical')
        || this.disabled
      )
        return

      this.dispatch('ElMenu', 'submenu-click', this)
    },
    handleMouseenter(event) {
      const { rootMenu } = this
      if (
        (rootMenu.menuTrigger === 'click' && rootMenu.mode === 'horizontal')
        || (!rootMenu.collapse && rootMenu.mode === 'vertical')
        || this.disabled
      )
        return

      const fromElement = event.fromElement || event.originalTarget
      // 
      if (
        closest(fromElement, '.el-scrollbar__bar')
        || closest(fromElement, '.el-scrollbar__thumb')
      )
        this.rootMenu.closeCollapseMenu()

      // 
      clearTimeout(this.hideTimer)
      this.rootMenu.openMenu(this.index, this.indexPath)

      //  wrapScroll  true collapseEl
      //  collapseEl
      if (
        (rootMenu.wrapScroll && this.$refs.collapseMenu && !this.collapseEl)
        || (this.collapseEl
          && !this.rootMenu.openedCollapseMenus.includes(this.index))
      ) {
        // 
        this.collapseEl = this.$refs.collapseMenu.loadCollapseMenu()
        if (!this.rootMenu.openedCollapseMenus.includes(this.index)) {
          // index
          this.rootMenu.openedCollapseMenus.push(this.index)
        }
      }
    },
    handleMouseleave(event) {
      const { rootMenu } = this
      const activeIndex = this.index
      if (
        (rootMenu.menuTrigger === 'click' && rootMenu.mode === 'horizontal')
        || (!rootMenu.collapse && rootMenu.mode === 'vertical')
      )
        return

      // 
      this.hideTimer = setTimeout(() => {
        this.rootMenu.closeMenu(activeIndex)
      }, HIDE_TIME)

      if (rootMenu.wrapScroll) {
        // firefoxchrome  toElementfirefox  relatedTarget
        const toElement = event.toElement || event.relatedTarget

        // 
        if (
          this.$refs.collapseMenu
          && closest(toElement, '.el-menu-collapse-wrap')
            === this.$refs.collapseMenu.$el
        )
          return

        // 
        if (
          closest(toElement, '.el-scrollbar__bar')
          || closest(toElement, '.el-scrollbar__thumb')
        )
          return
      }

      // 
      if (rootMenu.wrapScroll)
        this.emitHideCollapse()
    },

    // 
    leaveCollapse(event) {
      const toElement = event.toElement || event.relatedTarget
      const activeIndex = this.index
      // 
      if (
        closest(toElement, '.el-menu') == null
        && closest(toElement, '.el-menu-collapse-wrap') === null
      )
        this.rootMenu.closeCollapseMenu()

      // 
      if (closest(toElement, '.el-submenu') !== this.$el) {
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          this.rootMenu.closeMenu(activeIndex)
          this.emitHideCollapse()
        }, 100)
      }
    },

    // 
    emitHideCollapse() {
      this.rootMenu.$emit('hide-collapse', this.collapseEl)
      this.collapseEl = null
    },

    /**
     * @desc 
     * @author chenguanbin
     */
    setTextEllipsis() {
      const $text = this.$el.querySelector('.el-submenu__title--text')
      if (!$text || !$text.parentNode)
        return
      const gutter = 84 // 
      this.textEllipsis
        = $text.offsetWidth > $text.parentNode.offsetWidth - gutter
    }
  }
}
</script>
<template>
  <li
    :class="{
      'el-submenu': true,
      'is-active': active,
      'is-opened': opened,
      'is-disabled': disabled,
    }"
    @mouseenter="handleMouseenter"
    @mouseleave="handleMouseleave"
  >
    <div
      ref="submenu-title"
      :style="{paddingLeft:paddingStyle}"
      :title="
        !rootMenu.collapse && !disabled && (isIe || textEllipsis) && title
      "
      class="el-submenu__title"
      @click="handleClick"
    >
      <div class="content">
        <i v-if="$slots.icon" class="el-menu-icon is-slot">
          <slot name="icon" />
        </i>
        <i v-else-if="icon" :class="icon" class="el-menu-icon" />
        <i v-else class="el-menu-no-icon" />
        <span v-if="$slots.title" class="el-submenu__title--text">
          <slot name="title" />
        </span>
        <span v-else class="el-submenu__title--text">{{ title }}</span>
        <i
          :class="{
            'el-submenu__icon-arrow': true,
            'h-icon-angle_down_sm':
              rootMenu.mode === 'horizontal' ||
              (rootMenu.mode === 'vertical' && !rootMenu.collapse),
            'h-icon-angle_right_sm':
              rootMenu.mode === 'vertical' && rootMenu.collapse
          }"
        />
      </div>
      <div v-if="$slots.description || description" class="el-menu-item--description">
        <slot v-if="$slots.description" name="description" />
        <div v-else class="el-menu-item--description-content">
          {{ description }}
        </div>
      </div>
    </div>
    <template v-if="rootMenu.mode === 'horizontal'">
      <transition :name="menuTransitionName">
        <ul v-show="opened" class="el-submenu_open">
          <slot />
        </ul>
      </transition>
    </template>
    <template v-else-if="rootMenu.mode === 'vertical' && rootMenu.collapse">
      <transition :name="menuTransitionName">
        <ul v-show="opened" v-if="!rootMenu.wrapScroll" class="el-submenu_open">
          <li v-if="title && level === 1" class="el-submenu__collpase-title">
            {{ title }}
          </li>
          <slot />
        </ul>
        <el-menu-collapse
          v-show="opened"
          v-else
          ref="collapseMenu"
          :root-menu="rootMenu"
          @mouse-enter="handleMouseenter"
          @leave-collapse="leaveCollapse"
        >
          <li v-if="title && level === 1" class="el-submenu__collpase-title">
            {{ title }}
          </li>
          <slot />
        </el-menu-collapse>
      </transition>
    </template>
    <el-collapse-transition v-else>
      <ul v-show="opened" :class="[{'is-bg-fill':isBgFill}]">
        <slot />
      </ul>
    </el-collapse-transition>
    <div v-if="divided" class="el-menu--divided" />
  </li>
</template>
