const hasOwnProperty = Object.prototype.hasOwnProperty

export function noop() {}

export function hasOwn(obj, key) {
  return hasOwnProperty.call(obj, key)
}

function extend(to, _from) {
  for (const key in _from)
    to[key] = _from[key]

  return to
}

export function toObject(arr) {
  const res = {}
  for (let i = 0; i < arr.length; i++) {
    if (arr[i])
      extend(res, arr[i])
  }
  return res
}

export const getValueByPath = function(object, prop) {
  prop = prop || ''
  const paths = prop.split('.')
  let current = object
  let result = null
  for (let i = 0, j = paths.length; i < j; i++) {
    const path = paths[i]
    if (!current)
      break

    if (i === j - 1) {
      result = current[path]
      break
    }
    current = current[path]
  }
  return result
}

export function getPropByPath(obj, path, strict, keyIncludePoint = false) {
  let tempObj = obj
  path = path.replace(/\[(\w+)\]/g, '.$1')
  path = path.replace(/^\./, '')

  let keyArr = path.split('.')
  let i = 0
  if (keyIncludePoint)
    keyArr = [path]

  for (let len = keyArr.length; i < len - 1; ++i) {
    const key = keyArr[i]
    if (key in tempObj) {
      tempObj = tempObj[key]
    }
    else {
      if (strict)
        throw new Error('please transfer a valid prop path to form item!')

      break
    }
  }
  return {
    o: tempObj,
    k: keyArr[i],
    v: tempObj[keyArr[i]],
  }
}

/**
 * ajax
 * add by zhangxiaogang 2017-09-27
 * @param {string} opt.method httpPOSTGET
 * @param {string} opt.url url
 * @param {boolean} opt.async truefalse
 * @param {object} opt.data 
 * @param {object} opt.dataType jsontextdataType:'text'

 * @param {function} opt.success ajax
 * @param {function} opt.error ajax
 */
function ajax(opt) {
  opt = opt || {}
  opt.type = (opt.method && opt.method.toUpperCase()) || 'GET'
  opt.url = opt.url || ''
  opt.async = !!opt.async
  opt.data = opt.data || opt.params || {}
  let xmlHttp = null
  if (XMLHttpRequest)
    xmlHttp = new XMLHttpRequest()
  else
    xmlHttp = new window.ActiveXObject('Microsoft.XMLHTTP')

  const params = []
  for (const key in opt.data)
    params.push(`${key}=${opt.data[key]}`)

  xmlHttp.onreadystatechange = function() {
    if (xmlHttp.readyState === 4) {
      if (xmlHttp.status === 200) {
        let resp = xmlHttp.responseText
        if (opt.dataType !== 'text')
          resp = JSON.parse(resp)

        opt.success && opt.success(resp)
      }
      else {
        opt.error && opt.error(new Error(xmlHttp.statusText))
      }
    }
  }
  const postData = params.join('&')
  if (opt.type.toUpperCase() === 'POST') {
    xmlHttp.open(opt.type, opt.url, opt.async)
    xmlHttp.setRequestHeader(
      'Content-Type',
      'application/x-www-form-urlencoded;charset=utf-8',
    )
    if (opt.headers) {
      for (const c in opt.headers)
        xmlHttp.setRequestHeader(c, opt.headers[c])
    }
    xmlHttp.send(postData)
  }
  else if (opt.type.toUpperCase() === 'GET') {
    const connector = ~opt.url.indexOf('?') ? '&' : '?'
    xmlHttp.open(
      opt.type,
      opt.url + (postData && connector) + postData,
      opt.async,
    )
    xmlHttp.send(null)
  }
}

/**
 * ajaxpromise
 * add by zhangxiaogang 2017-09-27
 * @param {object} opt ajax
 */
export function defaultPromise(opt) {
  if (Object.prototype.toString.call(opt) !== '[object Object]')
    return null
  return new window.Promise((resolve, reject) => {
    opt.success = resolve
    opt.error = reject
    ajax(opt)
  })
}

/**
 * 
 * add by zhangxiaogang 2018-05-04
 */
export function objectMerge(to, from) {
  for (const key in from) {
    to[key]
      = to[key] && to[key].toString() === '[object Object]'
        ? objectMerge(to[key], from[key])
        : (to[key] = from[key])
  }
  return to
}

// TODO: use native Array.find, Array.findIndex when IE support is dropped
export const arrayFindIndex = function(arr, pred) {
  for (let i = 0; i !== arr.length; ++i) {
    if (pred(arr[i]))
      return i
  }
  return -1
}

export const arrayFind = function(arr, pred) {
  const idx = arrayFindIndex(arr, pred)
  return idx !== -1 ? arr[idx] : undefined
}

// coerce truthy value to array
export const coerceTruthyValueToArray = function(val) {
  if (Array.isArray(val))
    return val
  else if (val)
    return [val]
  else
    return []
}

export const generateId = function() {
  return Math.floor(Math.random() * 10000)
}

/**
 * @desc 
 * @author chenguanbin
 * @param {Object} context 
 * @param {String} componentName 
 */
export const findComponentsDownward = (context, componentName) => {
  return context.$children.reduce((components, child) => {
    if (child.$options.name === componentName)
      components.push(child)
    const foundChilds = findComponentsDownward(child, componentName)
    return components.concat(foundChilds)
  }, [])
}

/**
 * Check if the given variable is a function
 * @function
 * @author fanzhuohua
 * @argument {*} functionToCheck - variable to check
 * @returns {Boolean} answer to: is a function?
 */
export function isFunction(functionToCheck) {
  const getType = {}
  return (
    functionToCheck
    && getType.toString.call(functionToCheck) === '[object Function]'
  )
}
