<script>
import { emitter } from '@hui/utils'

export default {
  name: 'ElCheckableTagGroup',

  componentName: 'ElCheckableTagGroup',

  mixins: [emitter],

  inject: {
    elFormItem: {
      default: '',
    },
  },

  props: {
    value: {
      type: Array,
      default() {
        return []
      },
    },
    vertical: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    value(value) {
      console.log(value)
      this.dispatch('ElFormItem', 'el.form.change', [value])
    },
  },
  methods: {
    toggleChecked({ checked, value }) {
      const valueSet = new Set(this.value)
      if (checked)
        valueSet.add(value)
      else
        valueSet.delete(value)

      const tempValue = Array.from(valueSet)
      this.$emit('input', tempValue)
    }
  }
}
</script>
<template>
  <div class="el-checkable-tag-group" :class="{ 'el-checkable-tag-group--vertical': vertical }">
    <slot />
  </div>
</template>
