<script>
import { VuePopper as Popper } from '@hui/utils'
import { Radio as ElRadio } from '@hui/components/Radio'
import { Checkbox as ElCheckbox, CheckboxGroup as ElCheckboxGroup } from '@hui/components/Checkbox'
import { Scrollbar as ElScrollbar } from '@hui/components/Scrollbar'
export default {
  components: { ElRadio, ElCheckbox, ElCheckboxGroup, ElScrollbar },
  mixins: [Popper],
  props: {
    type: { type: String, default: 'radio' },
    colorList: {
      type: Array,
      default: () => {
        return [
          '#f30303',
          '#e92656',
          '#960fa3',
          '#3f40a9',
          '#2c84ee',
          '#00b2cd',
          '#008b7e',
          '#36a751',
          '#79be4d',
          '#c2da46',
          '#fdeb4a',
          '#ff911b',
        ]
      },
    },
  },
  data() {
    return {
      showPopper: false,
      radioValue: undefined,
      checkboxValues: undefined,
      checkAll: false,
      isIndeterminate: false,
    }
  },
  watch: {
    showPopper: {
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            // TODO: 0
            // this.$refs.scrollbarRef.setScroll(1)
          })
        }
      },
      immediate: true
    },
    radioValue: {
      handler(val) {
        this.$emit('valueChange', val)
      },
    },
    checkboxValues: {
      handler(val) {
        if (val && val.length) {
          if (val.length === this.colorList.length) {
            this.checkAll = true
            this.isIndeterminate = false
          }
          else {
            this.checkAll = false
            this.isIndeterminate = true
          }
        }
        else {
          this.checkAll = false
          this.isIndeterminate = false
        }
        this.$emit('valueChange', val)
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    this.$parent.popperElm = this.popperElm = this.$el
    this.referenceElm = this.$parent.$el
  },
  methods: {
    handleCheckAllChange(value) {
      if (value)
        this.checkboxValues = this.colorList
      else
        this.checkboxValues = []
    },
  }
}
</script>

<template>
  <transition name="el-zoom-in-top" @after-leave="doDestroy">
    <div v-show="showPopper">
      <el-scrollbar v-if="showPopper" ref="scrollbarRef" wrap-class="color-selector-scrollbar-wrap">
        <div v-if="type==='radio'" class="radio-list">
          <div v-for="item in colorList" :key="item" class="selector-item">
            <el-radio v-model="radioValue" :label="item">
              <div class="color-block" :style="{backgroundColor:item}" />
            </el-radio>
          </div>
        </div>
        <div v-if="type==='checkbox'" class="checkbox-list">
          <div class="selector-item">
            <el-checkbox v-model="checkAll" :indeterminate="isIndeterminate" @change="handleCheckAllChange">
              
            </el-checkbox>
          </div>
          <el-checkbox-group v-model="checkboxValues">
            <div v-for="item in colorList" :key="item" class="selector-item">
              <el-checkbox :label="item">
                <div class="color-block" :style="{backgroundColor:item}" />
              </el-checkbox>
            </div>
          </el-checkbox-group>
        </div>
      </el-scrollbar>
    </div>
  </transition>
</template>
