<script>
import { Tooltip as ElTooltip } from '@hui/components/tooltip'
export default {
  name: 'ElBadge',

  components: {
    ElTooltip,
  },

  props: {
    // showTips  span 
    showTip: { type: Boolean, default: false },
    value: { type: null, default: null },
    max: { type: Number, default: null },
    isDot: {
      type: Boolean,
      default: null,
    },
    hidden: {
      type: Boolean,
      default: null,
    },
    type: {
      type: String,
      default: 'danger',
    },
  },

  computed: {
    content() {
      if (this.isDot)
        return

      const value = this.value
      const max = this.max

      if (typeof value === 'number' && typeof max === 'number')
        return max < value ? `${max}+` : value

      return value
    },
  },
}
</script>

<template>
  <div class="el-badge">
    <slot />
    <transition name="el-zoom-in-center">
      <ElTooltip v-if="max && showTip" :content="'' + value" placement="right">
        <sup
          v-show="!hidden && (content || isDot)" :class="[type ? 'el-badge__content--' + type : '', {'is-fixed': $slots.default, 'is-dot': isDot}]"
          class="el-badge__content" v-text="content"
        />
      </ElTooltip>
      <sup
        v-else v-show="!hidden && (content || isDot)" :class="[type ? 'el-badge__content--' + type : '', {'is-fixed': $slots.default, 'is-dot': isDot}]"
        class="el-badge__content" v-text="content"
      />
    </transition>
  </div>
</template>
