export default {
  computed: {
    indexPath() {
      const path = [this.index]
      let parent = this.$parent
      while (parent.$options.componentName !== 'ElNav') {
        if (parent.index)
          path.unshift(parent.index)

        parent = parent.$parent
      }
      return path
    },
    rootNav() {
      let parent = this.$parent
      while (parent && parent.$options.componentName !== 'ElNav')
        parent = parent.$parent

      return parent
    },
    parentNav() {
      let parent = this.$parent
      while (
        parent
        && !['ElNav', 'ElSubnav'].includes(parent.$options.componentName)
      )
        parent = parent.$parent

      return parent
    },
    paddingStyle() {
      if (this.rootNav.mode !== 'vertical')
        return {}

      let gutter = 12
      let parent = this.$parent

      if (this.rootNav.collapse || !parent) {
        return {}
      }
      else {
        if (parent.$options.componentName === 'ElSubnav') {
          gutter = 48 // 
          parent = parent.$parent
          while (parent && parent.$options.componentName !== 'ElNav') {
            //  16px
            if (parent.$options.componentName === 'ElSubnav')
              gutter += 16

            parent = parent.$parent
          }
        }
        else {
          gutter = this.icon || this.$slots.icon ? 12 : 24 // 
        }
        return { paddingLeft: `${gutter}px` }
      }
    }
  }
}
