<script>
import { CollapseTransition as ElCollapseTransition } from '@hui/components/collapse'
import {
  addResizeListener,
  closest,
  emitter,
  removeResizeListener
} from '@hui/utils'
import navMixin from './nav-mixin'

const HIDE_TIME = 20

export default {
  name: 'ElSubnav',

  componentName: 'ElSubnav',

  components: { ElCollapseTransition },

  mixins: [navMixin, emitter],

  props: {
    index: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: null
    }
  },

  data() {
    return {
      isIe: false,
      timeout: null,
      items: {},
      subnavs: {},
      collapseEl: null, // 
      textEllipsis: false,
      level: 1,
      hideTimer: null
    }
  },

  computed: {
    navTransitionName() {
      return this.rootNav.collapse ? 'el-zoom-in-left' : 'el-zoom-in-top'
    },
    opened() {
      return this.rootNav.openedNavs.includes(this.index)
    },
    active: {
      cache: false,
      get() {
        let isActive = false
        const subnavs = this.subnavs
        const items = this.items

        Object.keys(items).forEach((index) => {
          if (items[index].active)
            isActive = true
        })

        Object.keys(subnavs).forEach((index) => {
          if (subnavs[index].active)
            isActive = true
        })

        return isActive
      }
    }
  },

  watch: {
    opened(val) {
      const { rootNav } = this
      if (rootNav.navTrigger === 'hover' && rootNav.mode === 'horizontal') {
        const { index, indexPath } = this
        val
          ? rootNav.$emit('open', index, indexPath)
          : rootNav.$emit('close', index, indexPath)
      }
    }
  },

  created() {
    // 
    const $subNav = this.rootNav.wrapScroll
      ? this.$parent.$parent
      : this.$parent
    // level1
    this.level = $subNav.level ? $subNav.level + 1 : 1
    this.parentNav.addSubnav(this)
    this.rootNav.addSubnav(this)
  },
  mounted() {
    addResizeListener(this.$el, this.setTextEllipsis)
    if (!!window.ActiveXObject || 'ActiveXObject' in window)
      this.isIe = true
  },
  beforeDestroy() {
    this.parentNav.removeSubnav(this)
    this.rootNav.removeSubnav(this)
    removeResizeListener(this.$el, this.setTextEllipsis)
  },
  methods: {
    addItem(item) {
      this.$set(this.items, item.index, item)
    },
    removeItem(item) {
      delete this.items[item.index]
    },
    addSubnav(item) {
      this.$set(this.subnavs, item.index, item)
    },
    removeSubnav(item) {
      delete this.subnavs[item.index]
    },
    handleClick() {
      const { rootNav } = this
      if (
        (rootNav.navTrigger === 'hover' && rootNav.mode === 'horizontal')
        || (rootNav.collapse && rootNav.mode === 'vertical')
        || this.disabled
      )
        return

      this.dispatch('ElNav', 'subnav-click', this)
    },
    handleMouseenter(event) {
      const { rootNav } = this
      if (
        (rootNav.navTrigger === 'click' && rootNav.mode === 'horizontal')
        || (!rootNav.collapse && rootNav.mode === 'vertical')
        || this.disabled
      )
        return

      const fromElement = event.fromElement || event.originalTarget
      // 
      if (
        closest(fromElement, '.el-scrollbar__bar')
        || closest(fromElement, '.el-scrollbar__thumb')
      )
        this.rootNav.closeCollapseNav()

      // 
      clearTimeout(this.hideTimer)
      this.rootNav.openNav(this.index, this.indexPath)

      //  wrapScroll  true collapseEl
      //  collapseEl
      if (
        (rootNav.wrapScroll && this.$refs.collapseNav && !this.collapseEl)
        || (this.collapseEl
          && !this.rootNav.openedCollapseNavs.includes(this.index))
      ) {
        // 
        this.collapseEl = this.$refs.collapseNav.loadCollapseNav()
        if (!this.rootNav.openedCollapseNavs.includes(this.index)) {
          // index
          this.rootNav.openedCollapseNavs.push(this.index)
        }
      }
    },
    handleMouseleave(event) {
      const { rootNav } = this
      const activeIndex = this.index
      if (
        (rootNav.navTrigger === 'click' && rootNav.mode === 'horizontal')
        || (!rootNav.collapse && rootNav.mode === 'vertical')
      )
        return

      // 
      this.hideTimer = setTimeout(() => {
        this.rootNav.closeNav(activeIndex)
      }, HIDE_TIME)

      if (rootNav.wrapScroll) {
        // firefoxchrome  toElementfirefox  relatedTarget
        const toElement = event.toElement || event.relatedTarget

        // 
        if (
          this.$refs.collapseNav
          && closest(toElement, '.el-nav-collapse-wrap')
            === this.$refs.collapseNav.$el
        )
          return

        // 
        if (
          closest(toElement, '.el-scrollbar__bar')
          || closest(toElement, '.el-scrollbar__thumb')
        )
          return
      }

      // 
      if (rootNav.wrapScroll)
        this.emitHideCollapse()
    },

    // 
    leaveCollapse(event) {
      const toElement = event.toElement || event.relatedTarget
      const activeIndex = this.index
      // 
      if (
        closest(toElement, '.el-nav') == null
        && closest(toElement, '.el-nav-collapse-wrap') === null
      )
        this.rootNav.closeCollapseNav()

      // 
      if (closest(toElement, '.el-subnav') !== this.$el) {
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          this.rootNav.closeNav(activeIndex)
          this.emitHideCollapse()
        }, 100)
      }
    },

    // 
    emitHideCollapse() {
      this.rootNav.$emit('hide-collapse', this.collapseEl)
      this.collapseEl = null
    },

    /**
     * @desc 
     * @author chenguanbin
     */
    setTextEllipsis() {
      const $text = this.$el.querySelector('.el-subnav__title--text')
      if (!$text || !$text.parentNode)
        return
      const gutter = 84 // 
      this.textEllipsis
        = $text.offsetWidth > $text.parentNode.offsetWidth - gutter
    }
  }
}
</script>
<template>
  <li
    :class="{
      'el-subnav': true,
      'is-active': active,
      'is-opened': opened,
      'is-disabled': disabled
    }"
    @mouseenter="handleMouseenter"
    @mouseleave="handleMouseleave"
  >
    <div
      ref="subnav-title"
      :style="paddingStyle"
      :title="
        !rootNav.collapse && !disabled && (isIe || textEllipsis) && title
      "
      class="el-subnav__title"
      @click="handleClick"
    >
      <i v-if="$slots.icon" class="el-nav-icon is-slot">
        <slot name="icon" />
      </i>
      <i v-else-if="icon" :class="icon" class="el-nav-icon" />
      <span v-if="$slots.title" class="el-subnav__title--text">
        <slot name="title" />
      </span>
      <span v-else class="el-subnav__title--text">{{ title }}</span>
      <i
        :class="{
          'el-subnav__icon-arrow': true,
          'h-icon-angle_down_sm':
            rootNav.mode === 'horizontal' ||
            (rootNav.mode === 'vertical' && !rootNav.collapse),
          'h-icon-angle_right_sm':
            rootNav.mode === 'vertical' && rootNav.collapse
        }"
      />
    </div>
    <template v-if="rootNav.mode === 'horizontal'">
      <transition :name="navTransitionName">
        <ul v-show="opened" class="el-subnav_open">
          <slot />
        </ul>
      </transition>
    </template>
    <template v-else-if="rootNav.mode === 'vertical' && rootNav.collapse">
      <transition :name="navTransitionName">
        <ul v-show="opened" v-if="!rootNav.wrapScroll" class="el-subnav_open">
          <li v-if="title && level === 1" class="el-subnav__collpase-title">
            {{ title }}
          </li>
          <slot />
        </ul>
        <el-nav-collapse
          v-show="opened"
          v-else
          ref="collapseNav"
          :root-nav="rootNav"
          @mouse-enter="handleMouseenter"
          @leave-collapse="leaveCollapse"
        >
          <li v-if="title && level === 1" class="el-subnav__collpase-title">
            {{ title }}
          </li>
          <slot />
        </el-nav-collapse>
      </transition>
    </template>
    <el-collapse-transition v-else>
      <ul v-show="opened">
        <slot />
      </ul>
    </el-collapse-transition>
  </li>
</template>
