<script>
import { IconV2 as HIcon } from '@hui/components/icon'
import Color from 'color'
import { generate } from '@arco-design/color'
export default {
  name: 'ElButton',
  components: { HIcon, },
  props: {
    minWidth: {
      type: Number,
      default: null,
    },
    maxWidth: {
      type: Number,
      default: null,
    },
    type: {
      type: String,
      default: 'default',
    },
    size: {
      type: String,
      default: null,
    },
    icon: { type: [String, Object], default: null },
    iconSuffix: { type: [String, Object], default: null },
    nativeType: {
      type: String,
      default: 'button',
    },
    loading: {
      type: Boolean,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: null,
    },
    iconBorder: {
      type: String,
      default: null,
    },
    plain: {
      type: Boolean,
      default: null,
    },
    autofocus: {
      type: Boolean,
      default: null,
    },
    radius: {
      type: Boolean,
      default: null,
    },
    variant: {
      type: String,
      default: 'solid', // : solid/outline/ghost/dashed
    },
    flood: {
      type: String,
      default: '', // : accent/gray/red
    }
  },

  data() {
    return {
      labelTitle: '',
    }
  },

  computed: {
    buttonDisabled() {
      return this.disabled || (this.elForm || {}).disabled
    },
    floodColors() {
      if (this.flood && Color(this.flood)) {
        return {
          base: Color(this.flood).hex(),
          hover: generate(Color(this.flood).hex(), { index: 5 }),
          active: generate(Color(this.flood).hex(), { index: 7 }),
          disabled: generate(Color(this.flood).hex(), { index: 2 }),
        }
      }
      return {}
    },
  },

  // watch: {
  //   flood(val) {
  //     if (val && Color(val)) {
  //       this.floodColors = {
  //         base: Color(this.flood).hex(),
  //         hover: generate(Color(this.flood).hex(), { index: 5 }),
  //         active: generate(Color(this.flood).hex(), { index: 7 }),
  //         disabled: generate(Color(this.flood).hex(), { index: 2 }),
  //       }
  //     }
  //     else {
  //       this.floodColors = null
  //     }
  //   }
  // },

  methods: {
    handleInnerClick(e) {
      if (this.disabled)
        e.stopPropagation()
    },

    handleClick(evt) {
      this.$emit('click', evt)
    },

    handleMouseover(e) {
      const t = e.target
      // title
      if (this.$attrs.title)
        return
      const slotContent = t.querySelector('.is-slot-content') || t
      this.labelTitle = slotContent.scrollWidth > slotContent.offsetWidth ? t.innerText : ''
    },
  },
}
</script>
<template>
  <button
    :disabled="buttonDisabled || loading" :autofocus="autofocus" :type="nativeType" :class="[
      type ? 'el-button--' + type : '',
      size ? 'el-button--' + size : '',
      {
        'is-disabled': buttonDisabled,
        'is-loading': loading,
        'is-plain': plain,
        'is-radius': radius,
        'is-icon': (icon || iconBorder) && !$slots.default,
        'is-icon-border': iconBorder,
        'is-icon-text': $slots.default && icon,
        'is-icon-suffix-text': $slots.default && iconSuffix,
        'is-flood': !!flood
      }
    ]" :style="{
      minWidth: minWidth && `${minWidth}px`,
      maxWidth: maxWidth && `${maxWidth}px`,
      '--h-button-flood-color': floodColors.base,
      '--h-button-flood-hover-color': floodColors.hover,
      '--h-button-flood-active-color': floodColors.active,
      '--h-button-flood-disabled-color': floodColors.disabled,
    }" :title="labelTitle" class="el-button" :data-variant="variant" :data-flood="flood"
    @click="handleClick"
    @mouseover.self="handleMouseover"
  >
    <!-- <i v-if="loading" class="el-icon-loading" @click="handleInnerClick" /> -->
    <el-load-icon v-if="loading" class="h-icon-loading" @click="handleInnerClick" />
    <i v-if="icon && !loading && typeof icon === 'string'" :class="icon" />
    <i v-if="iconBorder && !loading" :class="iconBorder" @click="handleInnerClick" />
    <!--  h-icon  -->
    <h-icon v-if="(icon && typeof icon === 'object') || $slots.icon" :class="[icon?'icon-svg':'']">
      <component :is="icon" v-if="icon" />
      <slot v-else name="icon" />
    </h-icon>
    <!-- <i :class="outicon" v-if="outicon" @click="handleInnerClick"></i> -->
    <span v-if="$slots.default" class="is-slot-content" @click="handleInnerClick">
      <slot />
    </span>
    <!-- icon-suffix  -->
    <i v-if="iconSuffix && typeof iconSuffix === 'string'" :class="iconSuffix" />
    <!--  h-icon  -->
    <h-icon v-if="(iconSuffix && typeof iconSuffix === 'object') || $slots.iconSuffix" :class="[iconSuffix?'icon-svg':'']">
      <component :is="iconSuffix" v-if="iconSuffix" />
      <slot v-else name="iconSuffix" />
    </h-icon>
  </button>
</template>
